<template>
  <div class="ds-control ds-form-item ds-system mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      {{ getValueString | proxy(formatter) }}
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy> 
    </template>
    <template v-else>
      <van-field v-model="getValueString" readonly :label="(showLabel&&label?label:'')" :label-width="getLabelWidth(true)" :required="required" input-align="right"></van-field>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl"
export default dsf.component({
  name: "DsfMobileUpdateTime",
  mixins: [formControl],
  ctrlCaption: "修改时间",
  props: {
    caption: {
      type: String,
      default: dsf.config.kw.updatetime,
    },
    label: {
      type: String,
      default: "修改时间",
    },
    formatter: {
      type: String,
      default: "dateTime^yyyy-mm-dd hh:ii",
    },
    simple: {
      type: Boolean,
      default: false,
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    visible: {
      type: Boolean,
      default: true,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("system-meta-data", {
          code: dsf.config.kw.updatetime,
          valueAttributes: [
            {
              name: "修改时间",
              code: dsf.config.kw.updatetime,
              type: dsf.metadata.getDataType("date"),
              length: "",
              defaultValue: null,
              unit: null,
            },
          ],
        });
      },
    },
  },
  computed: {
    getValueString: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      },
    },
  },
});
</script>
