<template>
  <div class="ds-login-page">
    <div class="ds-login-page-header">
      <img class="logo" src="https://gitee.com/static/images/logo.svg?t=158106664" />
      <!--波浪线-->
      <svg viewBox="0 50 500 35" preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg">
        <path fill="#fff" d="M 0 70 Q 87.5 47, 175 70 T 350 75 T 500 70 V 100 H 0 V 0"></path>
      </svg>
      <!--背景-->
      <div class="ds-login-page-header-bg">
        <div></div><div></div><div></div><div></div>
      </div>
    </div>
    <div class="ds-login-page-body">
      <div class="ds-login-page-tab-header">
        <i ref="tabBar" v-if="tabPositions.length" :style="tabPositions[tabActive]"></i>
        <template v-for="(item, index) in tabs">
          <div ref="tabs" :key="index" class="ds-login-page-tab-header-item" :class="{active: tabActive == index}" @click="tabClick(index)">{{ item }}</div>
        </template>
      </div>
      <div class="ds-login-page-tab-body">
        <div v-if="tabActive == 0" class="ds-login-page-tab-body-item">
          <form class="ds-login-page-form" @submit.prevent>
            <div class="ds-login-page-form-item">
              <input v-model.trim="username" type="text" name="username" autocomplete="off" placeholder="请输入用户名" />
            </div>
            <div class="ds-login-page-form-item">
              <i class="iconfont icon-xiayibu" @click.stop="submitByUsername"></i>
              <input v-model.trim="password" type="password" name="password" autocomplete="off" placeholder="请输入密码" />
            </div>
            <input type="submit" style="display: none" @click="submitByUsername"/>
          </form>
        </div>
        <div v-if="tabActive == 1" class="ds-login-page-tab-body-item">
          <form class="ds-login-page-form" @submit.prevent>
            <div class="ds-login-page-form-item">
              <div class="getCodeBt" :class="[codeBtType]" @click.stop="getCode">
                {{ codeBtType != '' ? `${countdownTime}s后重新获取` : '获取验证码' }}
              </div>
              <input v-model.trim="phoneNumber" type="tel" name="phoneNumber" maxlength="20" autocomplete="off" placeholder="请输入手机号" />
            </div>
            <div class="ds-login-page-form-item">
              <i class="iconfont icon-xiayibu" @click.stop="submitByPhone"></i>
              <input v-model.trim="phoneCode" type="email" name="phoneCode" maxlength="12" autocomplete="off" placeholder="请输入验证码" />
            </div>
            <input type="submit" style="display: none" @click="submitByPhone"/>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      tabs: ['账号登录', '验证码登录'],
      tabPositions: [],
      tabActive: 0,
      // 获取验证码按钮的状态。countdown：倒计时中
      codeBtType: '',
      // 验证码倒计时总的时间
      countdownTimes: 120,
      // 验证码倒计时的时间
      countdownTime: 0,
      username: '',
      password: '',
      phoneNumber: '',
      phoneCode: ''
    }
  },
  mounted() {
    this.resize();
    window.addEventListener('resize', this.resize);
  },
  destroyed() {
    clearInterval(this.timeout);
    window.removeEventListener('resize', this.resize);
  },
  methods: {
    resize() {
      let tabBarWidth = 100;
      this.tabPositions = this.$refs.tabs.map(el => {
        let scaleX = (el.offsetWidth - 6) / tabBarWidth;
        let left = el.offsetLeft + el.offsetWidth / 2 + 'px';
        return {
          display: 'block',
          transform: `translateX(${left}) scale3d(${scaleX}, 1, 1)`
        };
      });
    },
    tabClick(index) {
      this.$refs.tabBar.style.transition = 'transform .2s ease-in-out';
      setTimeout(() => {
        this.tabActive = index;
        setTimeout(() => {
          this.$refs.tabBar.style.transition = 'none';
        }, 210);
      },0);
    },
    // 获取验证码
    getCode() {
      if (this.codeBtType != '') return;
      if (this.phoneNumber === '') {
        dsf.layer.message('手机号不能为空！', false);
        return;
      }
      if (!dsf.validate('isMobileOrTelPhone', this.phoneNumber)) {
        dsf.layer.message('手机号输入有误', false);
        return;
      }
      let loading = dsf.layer.loading();
      // todo 获取验证码，这次setTimeout模拟了异步请求
      setTimeout(() => {
        // 异步请求成功
        this.codeBtType = 'countdown';
        dsf.layer.closeLoading(loading);
        this.doCountdown();
      }, 1000);
    },
    // 执行倒计时
    doCountdown() {
      this.countdownTime = this.countdownTimes - 1;
      this.timeout = setInterval(() => {
        this.countdownTime--;
        if (this.countdownTime <= -1) {
          this.codeBtType = '';
          clearInterval(this.timeout);
        }
      }, 1000);
    },
    // 账号登录
    submitByUsername() {
      if (this.username === '') {
        dsf.layer.message('账号不能为空！', false);
        return;
      }
      if (this.password === '') {
        dsf.layer.message('密码不能为空！', false);
        return;
      }
      this.loginByUsername();
    },
    loginByUsername() {
      let loading = dsf.layer.loading();
      this.$webAPI.login({
        userName: this.username,
        password: dsf.md5(dsf.md5(this.password)),
        code: 'good'
      })
        .then(({ success, message }) => {
          if (success) {
            this.setStateIsLogin();
          } else {
            dsf.layer.message(message, false);
          }
        })
        .catch((err) => {
          if (err) {
            dsf.layer.message(err.message || "未知错误", false);
          }
        })
        .finally(() => {
          dsf.layer.closeLoading(loading);
        });
    },
    // 手机号登录
    submitByPhone() {
      if (this.phoneNumber === '') {
        dsf.layer.message('手机号不能为空！', false);
        return;
      }
      if (!dsf.validate('isMobileOrTelPhone', this.phoneNumber)) {
        dsf.layer.message('手机号输入有误', false);
        return;
      }
      if (this.phoneCode === '') {
        dsf.layer.message('验证码不能为空！', false);
        return;
      }
      this.loginByPhone();
    },
    loginByPhone() {
      // todo 执行验证码登录
      dsf.layer.message('登录失败！', false);
    },
    // 储存cookie登录状态
    setStateIsLogin() {
      dsf.cookies.set('isLogin', 1);
      let from = this.$route.query.from;
      if (from) {
        from = decodeURIComponent(from);
      } else {
        from = dsf.config.setting_public_mobile_login_success_to;
      }
      location.replace(from);
    },
  }
}
</script>