<template>
  <div class="ds-control ds-form-item ds-time-picker mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else-if="readOnly && !isDesign">
      <van-field
        :label-width="getLabelWidth(true)"
        :label="showLabel && label || ''"
        :disabled="disabled"
        :readonly="readOnly"
        input-align="left"
      >
        <template #input>
          <DsfTextProxy v-model="value" :show-label="false" v-bind="_self.$props"></DsfTextProxy>
        </template>
        <template v-if="hasIcon" #right-icon>
          <i class="iconfont icon-tab_attendance_d"></i>
        </template>
      </van-field>
    </template>
    <template v-else>
      <van-field v-bind="attr" v-on="events">
        <template #input>
          <DsfTextProxy v-model="value" :show-label="false" v-bind="_self.$props" :formatter="'dateTime^' + $timeFormat"></DsfTextProxy>
        </template>
        <template v-if="hasIcon" #right-icon>
          <i class="iconfont icon-shijian" style="font-size: 1.1em; font-weight: bold"></i>
        </template>
      </van-field>
      <dsf-calendar-time-box
        v-if="!isDesign"
        ref="calendarTime"
        :type="type"
        :min-time="minTime"
        :max-time="maxTime"
        :step="hourStep"
      ></dsf-calendar-time-box>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl";

const baseDate = '2000/01/01';
const baseTime = 946656000000;

export default dsf.component({
  name: "DsfMobileTimePicker",
  mixins: [formControl],
  ctrlCaption: "时间选择",
  props: {
    value: {
      type: String,
      default: ""
    },
    label: {
      type: String,
      default: "时间选择"
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    // 时间格式
    timeFormat: {
      type: String,
      default: 'HH:mm:ss'
    },
    // 是否显示清除按钮
    clearable: {
      type: Boolean,
      default: true
    },
    // 显示小时段
    timeInter: {
      type: Array,
      default () {
        return [
          '00:00:00',
          '23:59:59'
        ]
      }
    },
    // 显示分钟步长
    hourStep: {
      type: Number,
      default: 1
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("time-picker-meta-data");
      }
    },
    // 是否有右侧图标
    hasIcon: {
      type: Boolean,
      default: true
    },
  },
  data() {
    return {
    }
  },
  computed: {
    type() {
      return this.timeFormat == 'HH:mm:ss' ? 'datetime_s' : 'datetime';
    },
    attr() {
      let res = {
        labelWidth: this.getLabelWidth(true),
        label: this.showLabel && this.label || '',
        placeholder: this.placeholder,
        disabled: this.disabled,
        readonly: true
      };
      if (!this.readOnly) {
        res.inputAlign = this.inputAlign;
        res.required = this.required;
      } else {
        res.inputAlign = 'left';
      }
      return res;
    },
    events() {
      let res = {};
      if (!this.readOnly && !this.disabled) {
        res.click = this.fieldClick
      }
      return res;
    },
    minTime() {
      return new Date(baseDate + ' ' + this.timeInter[0]).getTime();
    },
    maxTime() {
      return new Date(baseDate + ' ' + this.timeInter[1]).getTime();
    },
    $timeFormat() {
      if (this.type === 'datetime_s') {
        return 'hh:ii:ss';
      }
      return 'hh:ii';
    },
  },
  methods: {
    async fieldClick() {
      let date;
      if (this.value != '') {
        date = new Date(baseDate + ' ' + this.value);
      } else {
        date = new Date(baseDate + ' ' + dsf.date.format(new Date(), 'hh:ii:ss'));
      }
      let value = await this.$refs.calendarTime?.open(date);
      this.emitValueChange(value);
      this.$dispatch("change", value);
      this.$nextTick(() => {
        this.emitFormValidate(value);
      });
    }
  }
});
</script>
