<template>
  <van-popup v-model="show" get-container="#app" position="bottom" round>
    <van-picker
      ref="picker"
      :columns="columns"
      title="选择时间"
      show-toolbar
      @change="onChange"
      @cancel="onCancel"
      @confirm="onConfirm"
    />
  </van-popup>
</template>

<script>
import { oneDay, toStr, toStr2, getItems } from './calendarUtils';

const bTime = 28800000;

export default {
  name: "DsfCalendarTimeBox",
  inject: {
    $calendar: {
      default: null
    }
  },
  props: {
    unit: {
      type: Array,
      default() {
        return ['点', '分', '秒']
      }
    },
    // 选中方式
    type: {
      type: String,
      default: 'datetime'
    },
    minTime: {
      type: Number,
      default: -Infinity
    },
    maxTime: {
      type: Number,
      default: Infinity
    },
    // 步进（分钟）
    step: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      show: false,
      columns: []
    }
  },
  computed: {
    hours() {
      return getItems(24, this.unit[0]);
    },
    minutes() {
      return getItems(60, this.unit[1]);
    },
    seconds() {
      return getItems(60, this.unit[2]);
    },
  },
  created() {
    if (this.$calendar) {
      this.$calendar.timeView = this.$calendar.$timeView = this;
    }
  },
  methods: {
    updateColumns(type, minTime, maxTime, step = 1, val) {
      let opt = [type, minTime, maxTime, step].join(',');
      if (this.lastOpt == opt) {
        return;
      }
      this.lastOpt = opt;
      let columns;
      if (!minTime && !maxTime && step == 1) {
        this.cache = null;
        columns = [
          { values: this.hours, defaultIndex: 0 }, // 时
          { values: this.minutes, defaultIndex: 0 } // 分
        ]
      } else {
        columns = this.getColumns(minTime, maxTime, step, val);
      }
      if (type == 'datetime_s') {
        columns.push({ values: this.seconds, defaultIndex: 0 })
      }
      this.columns = columns;
    },
    getColumns(minTime = 0, maxTime = oneDay, step, val) {
      step = step * 60000;
      let days = Math.floor(val / oneDay);
      let minDays = Math.floor(minTime / oneDay);
      let maxDays = Math.floor(maxTime / oneDay);
      let n, n2, cache = { h: [], H: {}};
      n = minTime % oneDay + bTime;
      n2 = maxTime % oneDay + bTime;
      if (minDays == maxDays) {
        // 什么都不用做
      } else if (days <= minDays) {
        n2 = oneDay - 1;
      } else if (days >= maxDays) {
        n = step - ((maxDays - minDays) * oneDay - n) % step;
        n2 = maxTime % oneDay;
      } else {
        n = step - ((days - minDays) * oneDay - n) % step;
        n2 = oneDay - 1;
      }
      do {
        let h = toStr2(Math.floor(n / 3600000), this.unit[0]);
        let m = toStr2(Math.floor((n % 3600000) / 60000), this.unit[1]);
        if (!cache.H[h]) {
          cache.H[h] = [];
          cache.h.push({ text: h, disabled: false });
        }
        cache.H[h].push({ text: m, disabled: false });
        n += step;
      } while(n <= n2);
      this.cache = cache;
      return [
        { values: cache.h, defaultIndex: 0 }, // 时
        { values: cache.H[cache.h[0].text], defaultIndex: 0 } // 分
      ];
    },
    setIndexes(date) {
      let h = toStr2(date.getHours(), this.unit[0]);
      h = this.columns[0].values.findIndex(({text}) => text == h);
      h = h < 0 ? 0 : h;
      this.onChange(null, [this.columns[0].values[h]]);
      let m = toStr2(date.getMinutes(), this.unit[1]);
      m = this.columns[1].values.findIndex(({text}) => text == m);
      m = m < 0 ? 0 : m;
      let indexes = [h, m];
      if (this.type == 'datetime_s') {
        let s = toStr2(date.getSeconds(), this.unit[2]);
        s = this.columns[2].values.findIndex(({text}) => text == s);
        s = s < 0 ? 0 : s;
        indexes.push(s);
      }
      if (this.$refs.picker) {
        this.$nextTick(() => {
          this.$refs.picker.setIndexes(indexes);
        });
      } else {
        this.columns[0].defaultIndex = h;
        this.columns[1].defaultIndex = m;
        if (this.type == 'datetime_s') {
          let s = toStr2(date.getSeconds(), this.unit[2]);
          s = this.columns[2].values.findIndex(({text}) => text == s);
          s = s < 0 ? 0 : s;
          this.columns[2].defaultIndex = s;
        }
      }
    },
    open(date) {
      this.lastH = null;
      let minTime = this.minTime == -Infinity ? undefined : this.minTime;
      let maxTime = this.maxTime == Infinity ? undefined : this.maxTime;
      this.updateColumns(this.type, minTime, maxTime, this.step < 2 ? 1 : this.step, date.getTime());
      this.$nextTick(() => {
        this.setIndexes(date);
        this.show = true;
      });
      return new Promise((resolve, reject) => {
        this.resolve = resolve;
        this.reject = reject;
      });
    },
    onCancel() {
      this.show = false;
      this.reject();
      this.resolve = undefined;
    },
    onConfirm(values) {
      this.show = false;
      this.resolve(values.map(v => toStr(parseInt(v.text))).join(':'));
      this.reject = undefined;
    },
    onChange(vm, [h]) {
      if (!this.cache || (this.lastH && this.lastH.text == h.text)) {
        return;
      }
      this.lastH = h;
      this.$set(this.columns[1], 'values', this.cache.H[h.text]);
      vm && this.$nextTick(() => {
        this.$refs.picker.setColumnIndex(1, 0);
      });
    }
  }
}
</script>