<template>
  <div v-if="type == 'popup'">
    <van-popup
      ref="dialog"
      v-model="dialogShow"
      :style="{ width: width, height: height }"
      class="ds-dialog mobile"
      v-bind.sync="$attrs"
      get-container="body"
      @closed="closeHander"
      v-on="$listeners"
    >
      <div v-if="hasHeader" class="ds-dialog-header">
        <div v-if="showClose">
          <slot name="cancel"> </slot>
        </div>
        <div :class="[{ 'm-left': showClose }, 'ds-dialog-header-title']">
          <slot name="title"></slot>
        </div>
        <div class="ds-dialog-header-buttons">
          <slot name="buttons"></slot>
        </div>
      </div>
      <div class="ds-dialog-body">
        <slot></slot>
      </div>
      <div class="ds-dialog-footer">
        <slot name="buttonBar"></slot>
      </div>
    </van-popup>
  </div>
  <div v-else-if="(type = 'dialog')">
    <van-dialog
      v-model="dialogShow"
      :style="{ width: width }"
      class="ds-dialog mobile"
      v-bind.sync="$attrs"
      get-container="body"
      :show-confirm-button="ShowConfirmButton"
      :show-cancel-button="ShowCancelButton"
      :cancel-button-text="cancelButtonText"
      :confirm-button-text="confirmButtonText"
      @confirm="confirm"
      @cancel="confirmCancel"
      @closed="closeHander"
      v-on="$listeners"
    >
      <template v-slot:title>
        <slot name="title"></slot>
      </template>
      <template>
        <slot></slot>
      </template>
      <div class="van-hairline--top van-dialog__footer">
        <slot name="dialog-buttons"></slot>
      </div>
    </van-dialog>
  </div>
</template>
<script>
export default {
  name: "DsfMobileDialog",
  inheritAttrs: false,
  provide() {
    return {
      $dialog: this,
    };
  },
  props: {
    type: {
      type: String,
      default() {
        return "popup";
      },
    },
    dialogArgs: {
      type: Object,
      default() {
        return null;
      },
    },
    ShowConfirmButton: {
      type: Boolean,
      default: false,
    },
    ShowCancelButton: {
      type: Boolean,
      default: false,
    },
    confirmButtonText: {
      type: String,
      default: "确定",
    },
    cancelButtonText: {
      type: String,
      default: "取消",
    },
    width: {
      type: String,
      default: "auto",
    },
    height: {
      type: String,
      default: "auto",
    },
    openerVm: {
      type: Object,
      default() {
        return null;
      },
    },
    hasHeader: {
      type: Boolean,
      default: false,
    },
    visible: {
      type: Boolean,
      default: false,
    },
    dialogSure: {
      type: Function,
      default: dsf.noop,
    },
    showClose: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      dialogShow: false,
    };
  },
  computed: {},
  watch: {
    visible(v) {
      this.$nextTick(() => {
        this.dialogShow = v;
      });
    },
  },
  beforeCreate() {},
  created() {
    // this.$attrs.position = this.$attrs.position || "bottom";
    // this.height=this.height||"100"
  },
  beforeMount() {},
  mounted() {
    this.$nextTick(() => {
      this.dialogShow = this.visible;
    });
  },
  beforeDestroy() {},
  destroyed() {
    this?.$el?.parentNode?.removeChild(this.$el);
  },
  methods: {
    closeHander() {},
    close() {
      this.$emit("update:visible", false);
    },
    confirm() {
      this.$emit("confirm-sure");
      // this.$emit("update:visible", false)
    },
    confirmCancel() {
      this.$emit("confirm-cancel");
      this.$nextTick(() => {
        // this.close();
      });
    },
  },
};
</script>
