<template>
  <div class="ds-control ds-form-item mobile">
    <template v-if="simple && readOnly && !isDesign">
      <div class="ds-form-item ds-date-picker mobile" :class="getCss">
        <DsfTextProxy v-model="value.sdate" v-bind="_self.$props"></DsfTextProxy>
      </div>
      <div class="ds-form-item ds-date-picker" :class="getCss">
        <DsfTextProxy v-model="value.edate" v-bind="_self.$props"></DsfTextProxy>
      </div>
    </template>
    <template v-else>
      <div class="ds-form-item ds-date-picker mobile" :class="getCss">
        <van-field v-bind="attr" :label="showLabel && labelS || ''" v-on="events">
          <template #input>
            <DsfTextProxy v-model="value.sdate" :show-label="false" v-bind="_self.$props"></DsfTextProxy>
          </template>
          <template v-if="hasIcon" #right-icon>
            <i class="iconfont icon-tab_attendance_d"></i>
          </template>
        </van-field>
      </div>
      <div class="ds-form-item ds-date-picker" :class="getCss">
        <van-field v-bind="attr" :label="showLabel && labelE || ''" v-on="events">
          <template #input>
            <DsfTextProxy v-model="value.edate" :show-label="false" v-bind="_self.$props"></DsfTextProxy>
          </template>
          <template v-if="hasIcon" #right-icon>
            <i class="iconfont icon-tab_attendance_d"></i>
          </template>
        </van-field>
      </div>
      <van-popup v-if="!isDesign && !readOnly && !disabled" v-model="showPicker" get-container="#app" position="bottom" round>
        <dsf-calendar
          :show="showPicker"
          :default-value="currentDate"
          :type="chooseType"
          :min-date="minDate"
          :max-date="maxDate"
          :clearable="clearable"
          :default-view="pickerStepType"
          :default-view-date="pickerInitDate && new Date(pickerInitDate) || undefined"
          range
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl";
import dateMixin from "./dateMixin";

export default dsf.component({
  name: "DsfMobileDateIntervalPicker",
  mixins: [formControl, dateMixin],
  ctrlCaption: "日期区间",
  props: {
    value: {
      type: Object,
      default () {
        return {
          sdate: '',
          edate: ''
        }
      }
    },
    labelS: {
      type: String,
      default: "开始日期"
    },
    labelE: {
      type: String,
      default: "结束日期"
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    // 是否显示清除按钮
    clearable: {
      type: Boolean,
      default: false
    },
    // 弹框初始时间
    pickerInitDate: {
      type: Number,
      default: null
    },
    // 日期选择器类型
    chooseType: {
      type: String,
      default: "date"
    },
    // 时间选择步骤
    // 默认直接选择日
    pickerStepType: {
      type: String,
      default: "day"
    },
    // 显示小时段
    timeInter: {
      type: Array,
      default() {
        return [
          '00:00:00',
          '23:59:59'
        ]
      }
    },
    // 时间显示格式
    timeFormat: {
      type: String,
      default: "yyyy-MM-dd"
    },
    //只读状态下格式化
    formatter: {
      type: String,
      default: "dateTime^yyyy-mm-dd"
    },
    // 文字对齐方式
    inputAlign: {
      type: String,
      default: 'right'
    },
    // 是否有右侧图标
    hasIcon: {
      type: Boolean,
      default: true
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("date-interval-meta-data");
      }
    }
  },
  data() {
    return {
      currentDate: null,
      showPicker: false
    }
  },
  computed: {
    attr() {
      let res = {
        labelWidth: this.getLabelWidth(true),
        placeholder: this.placeholder,
        disabled: this.disabled,
        readonly: true
      };
      if (!this.readOnly) {
        res.inputAlign = this.inputAlign;
        res.required = this.required;
        if (this.hasIcon) {
          res.rightIcon = 'notes-o';
        }
      } else {
        res.inputAlign = 'left';
      }
      return res;
    },
    events() {
      let res = {};
      if (!this.readOnly && !this.disabled) {
        res.click = this.fieldClick
      }
      return res;
    },
    $timeFormat() {
      let timeFormat = this.timeFormat.replace(/M/g, 'm');
      if (this.chooseType === 'datetime') {
        return timeFormat + ' hh:ii';
      }
      if (this.chooseType === 'datetime_s') {
        return timeFormat + ' hh:ii:ss';
      }
      return timeFormat;
    },
    valueFormat() {
      if (this.chooseType === 'datetime') {
        return 'yyyy-mm-dd hh:ii';
      }
      if (this.chooseType === 'datetime_s') {
        return 'yyyy-mm-dd hh:ii:ss';
      }
      return 'yyyy-mm-dd'
    }
  },
  watch: {
    'value' (to) {
      this.initValue(to);
    }
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    initValue(v) {
      if (this.simple || this.readOnly || this.isDesign || dsf.isUnDef(v) || _.isEqual(v, this.valueCopy)) {
        return;
      }
      if (v && v.sdate && v.edate && v.sdate !== '' && v.edate !== '') {
        this.currentDate = [
          dsf.date.parse(v.sdate, this.valueFormat),
          dsf.date.parse(v.edate, this.valueFormat)
        ];
        return;
      }
      this.currentDate = [];
    },
    fieldClick() {
      this.showPicker = true;
    },
    onConfirm(arr) {
      let [dateS, dateE] = arr ? arr : ['', ''];
      console.log(dateS, dateE)
      this.showPicker = false;
      let value = {
        sdate: dsf.date.format(dateS, this.valueFormat),
        edate: dsf.date.format(dateE, this.valueFormat)
      };
      this.emitValueChange(value);
      this.$dispatch("change", value);
      this.$nextTick(() => {
        this.emitFormValidate(value);
      });
    }
  }
});
</script>