<template>
  <div class="ds-control ds-form-item ds-system mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="inputValue" v-bind="_self.$props"></DsfTextProxy> 
    </template>
    <template v-else>
      <van-field
        v-if="!readOnly"
        v-model="inputValue"
        :class="{ 'ds-error-border': errors.length > 0 }"
        :placeholder="placeholder"
        type="text"
        input-align="right"
        :label-width="getLabelWidth(true)"
        :disabled="disabled"
        :required="required" 
        :label="(showLabel&&label?label:'')"
        @blur="inputBlurHandler"
        @input="inputChangeHandler"
      ></van-field>
      <van-field v-if="readOnly" v-model="inputValue" :label="(showLabel&&label?label:'')" :disabled="disabled" input-align="right"></van-field>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl"
export default dsf.component({
  name: "DsfMobileOrder",
  mixins: [formControl],
  ctrlCaption: "排序码",
  props: {
    caption: {
      type: String,
      default: dsf.config.kw.order,
    },
    simple: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: "排序码",
    },
    placeholder: {
      type: String,
      default: "请输入排序编号",
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    required: {
      type: Boolean,
      default: false,
    },
    readOnly: {
      type: Boolean,
      default: false,
    },
    error_tipsPosition: {
      type: String,
      default: "left",
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("system-meta-data", {
          code: dsf.config.kw.order,
          valueAttributes: [
            {
              name: "排序码",
              code: dsf.config.kw.order,
              type: dsf.metadata.getDataType("number"),
              length: 10,
              defaultValue: 0,
              unit: null,
            },
          ],
        });
      },
    },
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      },
    },
  },
  methods: {
    inputChangeHandler() {
      this.$dispatch("change");
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      })
    },
    inputBlurHandler() {
      this.$dispatch("blur");
    },
  },
});
</script>
