<template>
  <div>
    <div class="box-wrap staff-vavate-manage-wrap top">
      <!-- 流转信息 -->
      <wf-process ref="wfProcess" />
      <div class="btn-area">
        <div class="disagree-btn" @click="getSendBackLink">退回</div>
        <div class="agree-btn" @click="sendAction">发送</div>
      </div>
      <van-dialog v-model="showDialog" title="审批意见" show-cancel-button confirm-button-text="发送" class="staffAdviseDialog" @confirm="onConfirm">
        <van-field v-model="shyj_value" :placeholder="dialogHolder" :border="true" rows="4" autosize type="textarea" />
      </van-dialog>
    </div>

    <!-- 流程弹框 -->
    <wf-last-node ref="wfLastNode" />
    <wf-choose-links ref="wfChooseLinks" />
    <wf-choose-users ref="wfChooseUsers" />
    <wf-Opinion ref="wfOpinion" />
  </div>
</template>

<script>
import WFButtons from "./Buttons.mixin.js";
export default {
  name: "FlowDemo",
  mixins: [WFButtons],
  data() {
    return {
      itemData: {},
      showDialog: false,
      dialogHolder: "请输入审批意见",
      shyj_value: "", //审核意见
      btnDir: {
        1: "同意",
        0: "不同意",
      },
      pageTitle: "详情",
    };
  },
  created() {
    this.getDetail();
  },
  methods: {
    getDetail() {
      // 暂时写死
      this.sWFID = "38e9795031b948fbb67e7a4ece909f9f";
      this.sObjectID = dsf.url.queryString("id");
      this.sPID = dsf.url.queryString("sPID");
      this.sCurLinkID = dsf.url.queryString("sCurLinkID");
      this.getProcessInfo();
    },
    popDialog(cate) {
      this.btn_type = cate;
      this.dialogHolder = this.btnDir[cate];
      this.showDialog = true;
    },
    onConfirm() {
      if (!this.shyj_value) {
        this.shyj_value = this.dialogHolder;
      }
      let _this = this;
      let param = {
        qjdId: this.itemData.teas_xyxt_qjd_id,
        shyj: this.shyj_value,
        type: this.btn_type,
      };
      let url = "teas/mobile/qjsq/sp";
      this.$http
        .get(url, param)
        .done(function (args) {
          console.log(args);
          if (args.success) {
            _this.dsf.layer.message(`操作${args.message}，请稍后再试`);
            _this.$router.go(-1);
          } else {
            _this.dsf.layer.message(`${args.message}，请稍后再试`);
          }
        })
        .error(function (error) {
          _this.dsf.layer.message("审批请求失败，请联系管理员");
          console.log(`${url} 接口请求失败 错误码:${error}`);
        });
    },
    previewFile(zyfj) {
      let file = JSON.parse(zyfj)[0];
      let url = file.relativePath;
      if (!file.id) {
        return;
      }
      this.$router.push({
        name: "bufferPage",
        params: {
          url: url,
          text: file.originalFileName,
          id: file.id,
        },
      });
    },
  },
};
</script>
