<template>
  <div class="work_flow-process">
    <div v-for="line in flowProcess" :key="line.linkid" class="item" :class="{ 'no-user': !line.rusername }">
      <van-image round width="40px" height="40px" fit="cover" :src="'/oua/getRedisUserPhoto?userId=' + line.ruserid">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>
          <img class="header" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
        </template>
      </van-image>

      <van-image class="icon" round fit="cover" :src="getStateImg(line)"></van-image>
      <div class="des">
        <p class="first-child">
          <span class="title">{{ line.nodename }}</span>
          <span class="time">{{ format(line.reachtime) }}</span>
        </p>
        <p v-if="line.rusername" class="name">{{ line.rusername }}</p>
        <p v-if="getIdea(line)" class="yj">{{ getIdea(line) }}</p>
        <p class="files">
          <van-grid :column-num="3" :border="false">
            <van-grid-item v-for="(file, i) in getFiles(line)" :key="i" :icon="getType(file)" :text="file.originalFileName" @click="preview(file)" />
          </van-grid>
        </p>
      </div>
      <van-popup v-model="previewing" :style="{ height: '80%', width: '100%' }">
        <div>
          <dsf-iframe v-if="previewing" :src="filePath"></dsf-iframe>
        </div>
      </van-popup>
      <van-image-preview v-model="showImg" :images="images" />
    </div>
  </div>
</template>

<script>
export default {
  name: "FlowProcess",
  data() {
    return {
      flowProcess: [],
      previewing: false,
      filePath: "",
      images: [],
      showImg: false,
    };
  },
  methods: {
    setData(data) {
      this.flowProcess = data;
    },
    getStateImg(data) {
      let imgI = data.STATUS;
      if (imgI == "-31") {
        imgI = "-3";
      } else if (imgI == "-1") {
        imgI = "-2";
      }
      if (!imgI && imgI !== 0) {
        return null;
      }
      return require(`_platform/assets/styles/themes/workFlow/status${imgI}.png`);
    },
    getIdea(data) {
      if (data.opinionatts) {
        return JSON.parse(data.opinionatts).Content;
      }
      return null;
    },
    getFiles(data) {
      if (data.opinionatts) {
        return JSON.parse(data.opinionatts).Attachments;
      }
      return null;
    },
    format(str) {
      if (str) {
        return dsf.date.format(str, "mm-dd hh:ii");
      } else {
        return "";
      }
    },
    getType(file) {
      if (file.contentType.slice(0, 5) === "image") {
        return "photo-o";
      } else {
        return "orders-o";
      }
    },
    preview(files) {
      if (files && files.contentType && files.contentType.slice(0, 5) !== "image") {
        this.filePath = dsf.config.webRoot + "preview/file?fileId=" + files.id;
        this.previewing = true;
      } else {
        this.images = [dsf.config.webRoot + files.relativePath];
        this.showImg = true;
      }
    },
  },
};
</script>
