import { DsfMobileLoading } from "../common";

const insertDom = (parent, el) => {
  // parent.appendChild(el.mask);
  parent.appendChild(el);
};

export default {
  install(Vue) {
    Vue.directive("mobileLoading", {
      bind: function (el, binding) {
        const mask = new Mask({
          el: document.createElement("div"),
          data: {
            text: el.getAttribute("load-text") || "加载中"
          }
        });
        el.instance = mask;
        el.mask = mask.$el;
        toggleLoading(el, el.mask, binding);
      },
      update: function (el, binding) {
        if (binding.oldValue !== binding.value) {
          toggleLoading(el, el.mask, binding);
        }
      },
      unbind: function (el, binding) {
        // document.body.removeChild(el.mask);
        el.mask && el.mask.parentNode && el.mask.parentNode.removeChild(el.mask);
        el.instance && el.instance.$destroy();
      }
    });
    const Mask = Vue.extend(DsfMobileLoading);
    const toggleLoading = (parentNode, mask, binding) => {
      insertDom(parentNode, mask);
      if (binding.value) {
        Vue.nextTick(() => {
          parentNode.instance.loading = true;
          let position = dsf.getCss(parentNode, "position");
          position !== "absolute" && position !== "relative" && position !== "fixed" && (parentNode.style.position = "relative");
        });
      } else {
        parentNode.instance.loading = false;
      }
    };
  }
};
