<template>
  <div class="ds-control ds-form-item ds-textbox mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <van-field
        v-model="inputValue"
        :placeholder="!readOnly?placeholder:''"
        :disabled="disabled"
        :label-width="getLabelWidth(true)"
        :label="(showLabel&&label?label:'')"
        :required="!readOnly?required: false"
        :input-align="!readOnly? inputAlign: 'right'"
        type="textarea"
        :autosize="inputAutoSize"
        :rows="rows"
        :error-message="errorsMsg"
        :error-message-align="!readOnly? inputAlign: 'right'"
        @blur="blurHandler"
        @change="changeHandler"></van-field>
      <slot name="error">
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </slot>
    </template>
  </div>
</template>
<script>
import formControl from "_platform/mixins/formControl";
export default dsf.component({
  name: "DsfMobileTextArea",
  mixins: [formControl],
  ctrlCaption: "多行文本",
  props: {
    placeholder: {
      type: String,
      default: "请输入"
    },
    metadata: {
      type: Object,
      default() {
        let mdata = dsf.metadata.get("text-meta-data");
        dsf.mix(mdata.valueAttributes[0], { length: 1000 });
        return mdata;
      }
    },
    inputAlign: {
      type: String,
      default: "right"
    },
    rows: {
      type: Number,
      default: 2
    },
    isAuto: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: Number,
      default: 0
    },
    minHeight: {
      type: Number,
      default: 0
    }
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      }
    },
    inputAutoSize() {
      if (!this.isAuto) return false;
      if (!this.maxHeight && !this.minHeight) return true;
      return {
        minHeight: this.minHeight,
        maxHeight: this.maxHeight
      };
    }
  },
  mounted() {
    // console.log(this.props)
  },
  methods: {
    changeHandler() {
      this.$dispatch("change", this.value);
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      });
    },
    blurHandler() {
      this.$dispatch("blur");
    }
  }
});
</script>