<template>
  <div class="dsf-calendar-main" :style="{height: boxHeight + 'px'}">
    <div ref="box" class="dsf-calendar-main__calendar" @scroll="onScroll">
      <div v-for="index in (showMonthE - showMonthS + 1)" :key="(year = showMonthS + index - 1) > 0 ? year : (year = year - 1)" class="dsf-calendar-main__calendar-item">
        <div class="dsf-calendar-main__calendar-item-date">{{ year }}年</div>
        <div class="dsf-calendar-main__calendar-item-body month">
          <div
            v-for="month in 12"
            :key="month"
            class="dsf-calendar-main__calendar-item-month"
            :class="cellClass(year, month)"
            @click="cellClick(showMonthS + index - 1, month)">
            <span>{{ month }}</span>月
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getDateAddMonth } from './calendarUtils';

export default {
  name: 'CalendarMonthBox',
  inject: ['$calendar'],
  props: {
    boxHeight: {
      type: Number,
      required: true
    },
    // 选中方式
    type: {
      type: String,
      required: true
    },
    minTime: {
      type: Object,
      required: true
    },
    maxTime: {
      type: Object,
      required: true
    },
    value: {
      type: Array,
      required: true
    },
    // 是否多选
    multiple: {
      type: Boolean,
      required: true
    },
    // 范围选择
    range: {
      type: Boolean,
      required: false
    }
  },
  data() {
    return {
      // 显示出的月份
      showMonthS: 0,
      showMonthE: 0,
      activeYearS: 0,
      activeYearE: 0
    }
  },
  computed: {
    activeText() {
      if (this.activeYearS == this.activeYearE) {
        return this.activeYearS + '年';
      }
      return this.activeYearS + '年 - ' + this.activeYearE + '年';
    }
  },
  watch: {
    '$calendar.currentView'(to, from) {
      if (from != 'year' && to == 'month') {
        let activeMonth = this.$calendar.$dayView?.activeMonth;
        let year = activeMonth && activeMonth.year || (this.$calendar.now.getFullYear());
        this.reset(year);
      }
    }
  },
  created() {
    this.$calendar.monthView = this.$calendar.$monthView = this;
  },
  mounted() {
    this.$box = this.$refs.box;
    if (this.$calendar.currentView == 'month') {
      this.reset(this.$calendar.defaultViewDate?.getFullYear() || this.$calendar.now.getFullYear());
    }
  },
  methods: {
    // 单元格样式
    cellClass(year, month) {
      let y = this.$calendar.now.getFullYear();
      let m = this.$calendar.now.getMonth() + 1;
      let res = {};
      if (this.minTime.Y > year ||
        this.maxTime.Y < year ||
        (this.minTime.Y == year && this.minTime.M > month) ||
        (this.maxTime.Y == year && this.maxTime.M < month)) {
        res.disabled = true;
      } else if (y == year && month == m) {
        res.toDay = true;
      }
      if (this.range && this.multiple) {
        // todo 范围多选
      } else if (this.range && !this.multiple) {
        // 范围单选
        if (this.$calendar.tempValue.length > 0) {
          let vsY = this.$calendar.tempValue[0].getFullYear();
          let vsM = this.$calendar.tempValue[0].getMonth() + 1;
          if (vsY == year && vsM == month) {
            res.activeS = true;
          }
          if (this.$calendar.tempValue.length == 2) {
            let veY = this.$calendar.tempValue[1].getFullYear();
            let veM = this.$calendar.tempValue[1].getMonth() + 1;
            if (veY == year && veM == month) {
              res.activeE = true;
            }
            if ((vsY < year || (vsY == year && vsM < month)) &&
                (veY > year || (veY == year && veM > month))) {
              res.activeC = true;
            }
          }
        }
      } else if (this.multiple) {
        // todo 多选
      } else if (this.value.length) {
        // 单选
        this.value.find(v => {
          return year == v.getFullYear() && month == v.getMonth() + 1;
        }) && (res.active = true);
      }
      return res;
    },
    cellClick(year, month) {
      year = year < 1 ? year - 1 : year;
      if (this.type != 'month') {
        this.$emit('change', year, month)
      } else if (this.range && this.multiple) {
        // 范围多选
        this.formatValueByCheckboxRange(year, month);
      } else if (this.range && !this.multiple) {
        // 范围单选
        this.formatValueByRadioRange(year, month);
      } else if (this.multiple) {
        // 多选
        this.formatValueByCheckbox(year, month);
      } else {
        // 单选
        this.formatValueByRadio(year, month);
      }
    },
    // todo 多选
    formatValueByCheckbox(year, month) {
      let index = this.value.findIndex(v => {
        return v.getFullYear() == year && v.getMonth() + 1 == month;
      });
      if (index == -1) {
        this.$calendar.value.push(new Date(`${year}/${month}/1`));
      } else {
        this.$calendar.value.splice(index, 1);
      }
    },
    // 单选
    formatValueByRadio(year, month) {
      this.$calendar.value = [new Date(`${year}/${month}/1`)];
    },
    // todo 范围多选
    formatValueByCheckboxRange(year, month) {

    },
    // todo 范围单选
    formatValueByRadioRange(year, month) {
      if (!this.$calendar.tempValue.length || this.$calendar.tempValue.length == 2) {
        this.$calendar.tempValue = [new Date(`${year}/${month}/1`)];
        this.$calendar.value = [this.$calendar.tempValue[0]];
      } else {
        let oldTime = this.$calendar.tempValue[0];
        let oldYear = oldTime.getFullYear(), oldMonth = oldTime.getMonth() + 1;
        if (oldYear > year || (oldYear == year && oldMonth > month)) {
          this.$calendar.tempValue = [new Date(`${year}/${month}/1`)];
        } else {
          oldYear = year;
          oldMonth = month;
        }
        let {year: newYear, month: newMonth} = getDateAddMonth(oldYear, oldMonth, 1);
        oldTime = new Date(`${newYear}/${newMonth}/1`);
        this.$calendar.tempValue.push(new Date(oldTime.getTime() - 1));
        this.$calendar.value = [[...this.$calendar.tempValue]];
      }
    },
    // 定位到date这个时间
    doLocation(date) {
      date = date || this.$calendar.now;
      let year = date.getFullYear();
      this.reset(year);
    },
    reset(year) {
      let itemHeight = this.$box.children[0].offsetHeight;
      let h = itemHeight ? Math.floor(this.boxHeight * 3.5 / itemHeight) : 4;
      let showMonthS = year - h;
      let showMonthE = year + h;
      this.showMonthS = showMonthS = showMonthS < this.minTime.Y ? this.minTime.Y : showMonthS;
      this.showMonthE = showMonthE = showMonthE > this.maxTime.Y ? this.maxTime.Y : showMonthE;
      let num = this.showMonthE - this.showMonthS + 1;
      if (num < 2) {
        this.activeYearS = this.showMonthS;
        this.activeYearE = this.showMonthE;
      } else if (num == 2) {
        this.$nextTick(() => {
          this.updateActive(this.$box.offsetHeight, 0, this.boxHeight);
        });
      } else {
        this.$nextTick(() => {
          this.$box.scrollTo(0, itemHeight * (0.5 + year - showMonthS) - this.boxHeight / 2 + 20);
          this.$nextTick(() => {
            let offsetHeight = this.$box.offsetHeight;
            let scrollHeight = this.$box.scrollHeight;
            let scrollTop = this.$box.scrollTop;
            this.updateActive(scrollHeight, scrollTop, offsetHeight);
          });
        });
      }
    },
    onScroll() {
      let offsetHeight = this.$box.offsetHeight;
      let scrollHeight = this.$box.scrollHeight;
      let scrollTop = this.$box.scrollTop;
      // 滚动条高度小于3屏高触发
      if (this.showMonthS >= this.minTime.Y && scrollTop < offsetHeight * 3) {
        let showMonthS = this.showMonthS - 8;
        this.showMonthS = showMonthS < this.minTime.Y ? this.minTime.Y : showMonthS;
      }
      // 滚动条离底部距离小于3屏高触发
      else if (this.showMonthE <= this.maxTime.Y && scrollHeight - offsetHeight - scrollTop < offsetHeight * 3) {
        let showMonthE = this.showMonthE + 8;
        this.showMonthE = showMonthE > this.maxTime.Y ? this.maxTime.Y : showMonthE;
      }
      this.updateActive(scrollHeight, scrollTop, offsetHeight);
    },
    updateActive(scrollHeight, scrollTop, offsetHeight) {
      let itemHeight = this.$box.children[0].offsetHeight;
      let activeYearS = Math.floor((scrollTop + 20) / itemHeight) + this.showMonthS;
      let activeYearE = Math.floor((scrollTop + offsetHeight - 10) / itemHeight - 0.1) + this.showMonthS;
      this.activeYearS = activeYearS < this.showMonthS ? this.showMonthS : activeYearS;
      this.activeYearE = activeYearE > this.showMonthE ? this.showMonthE : activeYearE;
    }
  }
}
</script>