<template>
  <van-row class="ds-control ds-row mobile  ds-no-drop" :gutter="gutter">
    <slot></slot>
  </van-row>
</template>
<script>
import layout from '_platform/mixins/layout';
import DsfMobileCol from './mobileCol';
export default dsf.component({
  name: 'DsfMobileRow',
  mixins: [layout],
  ctrlCaption: "栅格布局",
  props: {
    gutter: {
      type: Number,
      default: 0
    },
    minHeight: {
      type: String,
      default: "0",
    },
    height: {
      type: String,
      default: "auto",
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: "default", controls: [] }
        ]
      }
    }
  },
  provide() {
    return {
      parentRow: this
    }
  },
  data() {
    return {}
  },
  computed: {
    rowHeight() {
      if (this.isDesign) {
        return "100%";
      } else {
        let height = this.height;
        if (dsf.isNumber(height)) {
          height += "px";
        }
        return height;
      }
    }
  },
  created() {
  },
  mounted() {
    createDesignerColumns.call(this);
  },
  watch: {
    height: {
      handler(v) {
        if (this.isDesign) {
          let height = v;
          if (!v) {
            height = "auto";
          }
          if (dsf.isNumber(height)) {
            height += "px";
          }
          this.$dispatch('design-height-change', height);
        }
      },
      immediate: true
    },
  }
})
function createDesignerColumns() {
  if (this.isDesign) {
    if (this.slots[0].controls.length == 0) {
      let size = 2;
      for (let i = 0; i < size; i++) {
        let span = 24 / size;
        let obj = {
          height: "auto",
          span: span,
        };
        this.slots[0].controls.push(dsf.designer.createMobileComponent(DsfMobileCol, obj));
      }
    }
  }
}
</script>