<template>
  <div class="van-picker multiple">
    <div class="van-picker__toolbar">
      <button type="button" class="van-picker__cancel" @click="cancel">取消</button>
      <span v-show="checkValue.length">已勾选{{ checkValue.length }}个</span>
      <button type="button" class="van-picker__confirm" @click="confirm">确认</button>
    </div>
    <div class="van-picker__columns" :style="{height: visibleItemCount * itemHeight + 'px'}">
      <div class="van-picker-column" @transitionend="onTransitionEnd">
        <ul ref="wrapper" :style="wrapperStyle" class="van-picker-column__wrapper">
          <li v-for="item in $columns" :key="item[valueKey]" :style="{height: itemHeight + 'px'}" class="van-picker-column__item">
            <div
              class="van-picker-column__item-box van-hairline--bottom"
              :class="{active: checkValue.indexOf(item.value) > -1}"
              @click="itemClick(item)"
            >
              <span>{{ item.text }}</span>
              <van-icon name="success" />
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
const _touch = require("vant/lib/mixins/touch");
const _number = require("vant/lib/utils/format/number");
var _event = require("vant/lib/utils/dom/event");
var _utils = require("vant/lib/utils");

var DEFAULT_DURATION = 200; // 惯性滑动思路:
// 在手指离开屏幕时，如果和上一次 move 时的间隔小于 `MOMENTUM_LIMIT_TIME` 且 move
// 距离大于 `MOMENTUM_LIMIT_DISTANCE` 时，执行惯性滑动

var MOMENTUM_LIMIT_TIME = 300;
var MOMENTUM_LIMIT_DISTANCE = 15;

function getElementTranslateY(element) {
  var style = window.getComputedStyle(element);
  var transform = style.transform || style.webkitTransform;
  var translateY = transform.slice(7, transform.length - 1).split(', ')[5];
  return Number(translateY);
}
/**
 * 多选
 */
export default {
  name: "VanMultiplePicker",
  mixins: [_touch.TouchMixin],
  props: {
    show: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    showToolbar: {
      type: Boolean,
      default: false
    },
    columns: {
      type: Array,
      default () {
        return [];
      }
    },
    valueKey: {
      type: String,
      default: 'text'
    },
    visibleItemCount: {
      type: Number,
      default: 6
    },
    itemHeight: {
      type: Number,
      default: 44
    },
    swipeDuration: {
      type: Number,
      default: 1000
    }
  },
  data() {
    return {
      moving: false,
      offset: 0,
      duration: 0,
      checkValue: [],
      checkValueCopy: '[]'
    }
  },
  computed: {
    $columns() {
      return this.columns.map(col => {
        if (dsf.type(col) == 'string') {
          return {
            [this.valueKey]: col
          }
        }
        return col;
      });
    },
    count () {
      return this.$columns.length;
    },
    wrapperStyle() {
      return {
        transform: "translate3d(0, " + this.offset + "px, 0)",
        transitionDuration: this.duration + "ms",
        transitionProperty: this.duration ? 'all' : 'none'
      }
    }
  },
  watch: {
    'show'(to) {
      if (to) {
        this.checkValueCopy = JSON.stringify(this.checkValue);
      } else {
        this.checkValue = JSON.parse(this.checkValueCopy);
      }
    }
  },
  mounted () {
    this.bindTouchEvent(this.$refs.wrapper);
  },
  destroyed() {
    this.removeTouchEvent(this.$refs.wrapper);
  },
  methods: {
    itemClick({value}) {
      let index = this.checkValue.indexOf(value);
      if (index == -1) {
        this.checkValue.push(value);
      } else {
        this.checkValue.splice(index, 1);
      }
    },
    getCheckValue() {
      let indexes = [];
      let checkColumns = this.checkValue.map(v => {
        let index = this.$columns.findIndex(c => c[this.valueKey] == v);
        indexes.push(index);
        return this.columns[index];
      });
      return {
        indexes,
        checkColumns
      }
    },
    // 获取所有列选中的值
    getValues() {
      return this.getCheckValue().checkColumns;
    },
    // 设置所有列选中的值
    setValues(values) {
      let keys = [];
      values.forEach(k => {
        if (keys.indexOf(k) == -1 && this.$columns.find(c => c[this.valueKey] == k)) {
          keys.push(k);
        }
      });
      this.checkValue = keys;
      this.checkValueCopy = JSON.stringify(keys);
    },
    // 获取所有列选中值对应的索引-+
    getIndexes() {
      return this.getCheckValue().indexes;
    },
    // 设置所有列选中值对应的索引
    setIndexes(indexes) {
      let keys = [];
      indexes.forEach(i => {
        if (this.count > i) {
          let key = this.$columns[i][this.valueKey];
          if (keys.indexOf(key) == -1) {
            keys.push(key);
          }
        }
      });
      this.checkValue = keys;
      this.checkValueCopy = JSON.stringify(keys);
    },
    confirm() {
      this.checkValueCopy = JSON.stringify(this.checkValue);
      let { indexes, checkColumns } = this.getCheckValue();
      this.$emit('confirm', checkColumns, indexes);
    },
    cancel() {
      this.$emit('cancel');
    },
    /******以下这些方法从vant中copy过来改造的，别问我为什么，能用就行*******/
    removeTouchEvent(el) {
      var onTouchStart = this.onTouchStart,
        onTouchMove = this.onTouchMove,
        onTouchEnd = this.onTouchEnd;
      (0, _event.off)(el, 'touchstart', onTouchStart);
      (0, _event.off)(el, 'touchmove', onTouchMove);
      if (onTouchEnd) {
        (0, _event.off)(el, 'touchend', onTouchEnd);
        (0, _event.off)(el, 'touchcancel', onTouchEnd);
      }
    },
    onTouchStart(event) {
      if (this.readonly) {
        return;
      }
      this.touchStart(event);
      if (this.moving) {
        var translateY = getElementTranslateY(this.$refs.wrapper);
        this.offset = Math.min(0, translateY);
        this.startOffset = this.offset;
      } else {
        this.startOffset = this.offset;
      }
      this.duration = 0;
      this.transitionEndTrigger = null;
      this.touchStartTime = Date.now();
      this.momentumOffset = this.startOffset;
    },
    onTouchMove(event) {
      if (this.readonly) {
        return;
      }
      this.touchMove(event);
      if (this.direction === 'vertical') {
        this.moving = true;
        (0, _event.preventDefault)(event, true);
      }
      this.offset = (0, _number.range)(this.startOffset + this.deltaY, -(this.count * this.itemHeight), this.itemHeight);
      var now = Date.now();
      if (now - this.touchStartTime > MOMENTUM_LIMIT_TIME) {
        this.touchStartTime = now;
        this.momentumOffset = this.offset;
      }
    },
    onTouchEnd() {
      var _this = this;
      if (this.readonly) {
        return;
      }
      var distance = this.offset - this.momentumOffset;
      var duration = Date.now() - this.touchStartTime;
      var allowMomentum = duration < MOMENTUM_LIMIT_TIME && Math.abs(distance) > MOMENTUM_LIMIT_DISTANCE;
      if (allowMomentum) {
        this.momentum(distance, duration);
        return;
      }
      var index = this.getIndexByOffset(this.offset);
      this.duration = DEFAULT_DURATION;
      this.setIndex(index, true);
      setTimeout(function () {
        _this.moving = false;
      }, 0);
    },
    onTransitionEnd() {
      this.stopMomentum();
    },
    stopMomentum() {
      this.moving = false;
      this.duration = 0;
      if (this.transitionEndTrigger) {
        this.transitionEndTrigger();
        this.transitionEndTrigger = null;
      }
    },
    getIndexByOffset(offset) {
      return (0, _number.range)(Math.round(-offset / this.itemHeight), 0, this.count - 1);
    },
    momentum(distance, duration) {
      var speed = Math.abs(distance / duration);
      distance = this.offset + speed / 0.003 * (distance < 0 ? -1 : 1);
      var index = this.getIndexByOffset(distance);
      this.duration = +this.swipeDuration;
      this.setIndex(index, true);
    },
    setIndex: function setIndex(index, emitChange) {
      var _this2 = this;
      index = this.adjustIndex(index) || 0;
      var offset = -index * this.itemHeight;
      var trigger = function trigger() {
        if (index !== _this2.currentIndex) {
          _this2.currentIndex = index;
          if (emitChange) {
            _this2.$emit('change', index);
          }
        }
      };
      if (this.moving && offset !== this.offset) {
        this.transitionEndTrigger = trigger;
      } else {
        trigger();
      }
      this.offset = offset;
    },
    adjustIndex: function adjustIndex(index) {
      return (0, _number.range)(index, 0, this.count - this.visibleItemCount);
    }
  }
}
</script>