<template>
  <div class="ds-control mobile ds-subtable ds-no-padding">
    <div class="ds-subtable-conent-wrap">
      <template v-if="isDesign">
        <div class="ds-subtable-item" v-for="(row,$index) in templateData" :key="row.$hash">
          <div class="ds-subtable-item-header">
            <div class="ds-subtable-item-index">
              <slot name="index" v-bind="{row:row,$index:$index}">
                <span>{{label||'标题'}}({{($index+1)}})</span>
              </slot>
            </div>
            <div class="ds-subtable-item-delete">
              <a>{{getDeleteButtonText(null)}}</a>
            </div>
          </div>
          <div class="ds-subtable-item-content" slot-name="default">
            <slot v-bind="{row:row,$index:$index}"></slot>
          </div>
          <div class="ds-subtable-item-footer">
            <slot name="foot">
              <dsf-mobile-button none-line type="plain" icon="hao" size="small">{{getAddButtonText(row)}}</dsf-mobile-button>
            </slot>
          </div>
        </div>
      </template>
      <template v-else>
        <template v-if="list.length<=0">
          <div class="ds-subtable-item null-data">
            <div class="ds-subtable-item-footer" v-if="!readOnly">
              <slot name="foot">
                <dsf-mobile-button none-line type="plain" icon="hao" size="small" @click="addRowHandler(null,-1)">{{getAddButtonText(null)}}</dsf-mobile-button>
              </slot>
            </div>
          </div>
        </template>
        <template v-else>
          <div class="ds-subtable-item" v-for="(row,$index) in list" :key="row.$hash">
            <div class="ds-subtable-item-header">
              <div class="ds-subtable-item-index" :id="row.$hash">
                <slot name="index" v-bind="{row:row,$index:$index}">
                  <span>{{getRowName(row)}}</span>
                </slot>
              </div>
              <div class="ds-subtable-item-delete" v-if="$index==0?(isLeastOne&&list.length==1?false:true):true">
                <a @click="removeRowHandler(row)">{{getDeleteButtonText(null)}}</a>
              </div>
            </div>
            <div class="ds-subtable-item-content" slot-name="default">
              <slot v-bind="{row:row,$index:$index}"></slot>
            </div>
            <div class="ds-subtable-item-footer" v-if="!readOnly&&$index==list.length-1">
              <slot name="foot">
                <dsf-mobile-button none-line type="plain" icon="hao" size="small" @click="addRow(row,$index)">{{getAddButtonText(row)}}</dsf-mobile-button>
              </slot>
            </div>
          </div>
        </template>
      </template>
    </div>
  </div>
</template>
<script>
import oneToMany from "_platform/mixins/oneToMany";
export default dsf.component({
  name: "DsfMobileSubTable",
  ctrlCaption: "子表",
  mixins: [oneToMany],
  data() {
    return {
      templateData: [{ _id: dsf.uuid() }],
      subControls: {}
    };
  },
  props: {
    label: {
      type: String,
      default: "标题"
    },
    //数据过滤
    filterData: {
      type: Function,
      default: null
    },
    slots: {
      type: Array,
      default() {
        return [
          {
            name: "default",
            controls: [],
            scope: "scope",
            dataKey: "row",
            repeat: true
          }
        ];
      }
    },
    addButtonText: {
      type: [String, Function],
      default: ""
    },
    deleteButtonText: {
      type: [String, Function],
      default: ""
    },
    isLeastOne: {
      type: Boolean,
      default: false
    },
    rowName: {
      type: [String, Function],
      default: null
    }
  },
  computed: {
    list() {
      let value = this.value || [];
      if (this.filterData) {
        value = this.filterData(value);
      }
      return value;
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  methods: {
    updateSubControls(ctrlProps, ctrl) {
      this.addSubControls(ctrl);
      let scope = ctrl.local;
      if (!this.isDesign && scope && ctrlProps.isFormControl) {
        if (ctrlProps.metadata_fullcode) {
          let mcode = ctrlProps.metadata_fullcode;
          let defaultValue = this?.$vm?.$getDefaultValue(mcode);
          if ((scope[mcode] === undefined) & dsf.isDef(defaultValue)) {
            this.$vm.$set(scope, mcode, defaultValue);
          }
        }
        if (this.$vm && this.$vm?.$setControlPropsByWorkFlow) {
          this.$vm.$setControlPropsByWorkFlow(ctrlProps, "formControl");
        }
        if (dsf.isUnDef(ctrlProps.defaultReadOnly)) {
          ctrlProps.defaultReadOnly = ctrlProps.readOnly;
        }
        if (ctrlProps.isFormControl) {
          if (!ctrlProps.defaultReadOnly) {
            //子表只读，则强制全部只读无视一切状态
            ctrlProps.readOnly = this.readOnly;
          }
        }
      }
    },
    getSubControls(hash) {
      let sub = [];
      for (let k in this.subControls) {
        if (k.indexOf(hash) > 0) {
          sub.push(this.subControls[k]);
        }
      }
      return sub;
    },
    addSubControls(ctrl) {
      if (ctrl.designId && ctrl.local.$hash) {
        let key = ctrl.designId + "-" + ctrl.local.$hash;
        this.subControls[key] = ctrl;
      }
    },
    getRowName(row) {
      if (dsf.type(this.rowName) == "function") {
        return this.rowName(row);
      } else {
        if (this.rowName) {
          return this.rowName + (row ? "(" + (row.$index + 1) + ")" : "");
        }
        return this.label + (row ? "(" + (row.$index + 1) + ")" : "");
      }
    },
    getAddButtonText(row) {
      if (dsf.type(this.addButtonText) == "string") {
        let text = this.addButtonText;
        if (!text) {
          return "增加" + this.label;
        }
        return text;
      } else if (dsf.type(this.addButtonText) == "function") {
        return this.this.addButtonText(row);
      }
    },
    getDeleteButtonText(row) {
      if (dsf.type(this.deleteButtonText) == "string") {
        let text = this.deleteButtonText;
        if (!text) {
          return "删除";
        }
        return text;
      } else if (dsf.type(this.deleteButtonText) == "function") {
        return this.this.deleteButtonText(row);
      }
    },
    addRow(row, index) {
      let value = this.value || [];
      if (index >= value.length) {
        value.push({});
      } else {
        value.splice(index + 1, 0, {});
      }
      this.emitValueChange(value);
    },
    addRowHandler(row, index) {
      if (!row || index < 0) {
        this.addRow({}, -1);
      } else {
        this.addRow(row, index);
      }
    },
    removeRow(row) {
      let value = this.value || [];
      dsf.array.remove(value, row);
    },
    removeRowHandler(row) {
      document;
      dsf.layer
        .confirm({
          message: "确定要删除" + this.getRowName(row) + "?"
        })
        .then(() => {
          this.removeRow(row);
        })
        .catch(() => {});
    }
  },
  beforeDestroy() {},
  destroyed() {},
  watch: {
    value: {
      handler(v, o) {
        if (!this.isDesign && this.isLeastOne) {
          if (dsf.isUnDef(v) || v.length <= 0) {
            var value = [];
            value.push({});
            this.emitValueChange(value);
            return;
          }
        }
        if (v) {
          for (let i = 0; i < v.length; i++) {
            !("$editing" in v[i]) && this.$set(v[i], "$editing", false);
            !("$hash" in v[i]) && this.$set(v[i], "$hash", dsf.uuid());
            if ("$order" in v[i]) {
              this.$set(v[i], "$order", i);
            } else {
              v[i]["$order"] = i;
            }
            v[i].$index = i;
          }
          if (o && v) {
            this.emitValueChange(v);
          }
        }
      },
      immediate: true
    }
  }
});
</script>