<template>
  <div class="work_flow-usersDialog work_flow-re-choose-users">
    <div class="imgs">
      <div v-for="user in users" :key="user.ID" :class="{ noSeletc: !user.isSelect }" @click="changeUser(user)">
        <van-image round width="40px" height="40px" fit="cover" :src="'/oua/getRedisUserPhoto?userId=' + user.ID">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
          <template v-slot:error>
            <img class="header" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
          </template>
        </van-image>
        <van-icon v-if="user.isSelect" name="checked" />
        <p>{{ user.Name }}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "DsfMobileReChooseUsers",
  props: {
    users: {
      type: Array,
      default() {
        return [];
      },
    },
    Multi: {
      type: Boolean,
      default() {
        return false;
      },
    },
  },
  data() {
    return {
      selected: [],
    };
  },
  created() {},
  methods: {
    yes() {
      return this.selected;
    },
    validate() {
      this.selected = this.users.filter((user) => user.isSelect);
      if (this.selected.length == 0) {
        dsf.layer.message("请选择办理人", false);
        return false;
      }
      return true;
    },
    changeUser(user) {
      if (!this.Multi) {
        this.users.forEach((item) => {
          this.$set(item, "isSelect", false);
        });
        this.$set(user, "isSelect", !user.isSelect);
      } else {
        this.$set(user, "isSelect", !user.isSelect);
      }
    },
  },
};
</script>
