<template>
  <vue-picture-cut ref="pictureCut" :src="imgSrc">
    <vue-picture-cut-menu
      slot="menu"
      confirm-name="确定"
      cancel-name="取消"
      size-auto-name="自由"
      size-raw-name="原始"
      menu-rotate-name="旋转"
      @on-change="saveFileImg"
      @on-cancel="handleClose"
    />
  </vue-picture-cut>
</template>
<script>
import { VuePictureCut, VuePictureCutMenu } from "vue-picture-cut";

export default {
  name: "CropImage",
  components: {
    VuePictureCut,
    VuePictureCutMenu,
  },
  props: {
    imgSrc: {
      type: String,
      default: "",
    },
  },
  data() {
    return {};
  },
  methods: {
    saveFileImg(res) {
      this.$emit("onChange", res.blob, res.base64);
      this.$dialog?.close();
    },
    // 关闭裁剪
    handleClose() {
      this.$emit("onCancel");
      this.$dialog?.close();
    },
  },
};
</script>
