<template>
  <van-col class="ds-control ds-col ds-no-padding mobile" :class="getCss" :style="getStyle" slot-name="default" :span="span">
    <slot></slot>
  </van-col>
</template>
<script>
import layout from '_platform/mixins/layout'
export default dsf.component({
  name: 'DsfMobileCol',
  mixins: [layout],
  ctrlCaption: "栅格列",
  inject: {
    parentRow: {
      default: null
    },
  },
  props: {
    minHeight: {
      type: String,
      default: "0",
    },
    height: {
      type: String,
      default: "auto",
    },
    offset: {
      type: Number,
      default: null,
    },
    slots: {
      type: Array,
      default() {
        return [{ name: "default", controls: [] }];
      },
    },
    span: {
      type: Number,
      default: 24,
    }
  },
  computed: {
    getStyle() {
      let r = /(px|em|rem|vh|vw|%)$/g;
      let height = this.height;
      if (dsf.type(this.height) == "number") {
        height = this.height + "px";
      }
      if (height != "auto" && !r.test(height)) {
        height = this.height + "px";
        r.lastIndex = 0;
      }
      let style = {
        height: height
      };
      if (!this.isDesign) {
        if (this.parentRow.minHeight) {
          style["min-height"] = this.parentRow.minHeight + "px";
        }
      }
      return style;
    },
  },
  data() {
    return {}
  },
  created() {
  },
  mounted() {
  }
})
</script>