<template>
  <van-popup v-model="show" class="work_flow-usersDialog work_flow-opiDialog" position="bottom" :style="{ height: '100%' }">
    <div class="header">{{ typeText[type]["title"] }}</div>
    <div ref="content" class="content">
      <van-field
        v-if="h"
        ref="reason"
        v-model="msg"
        rows="1"
        :autosize="{ minHeight: h }"
        type="textarea"
        :placeholder="'请输入' + typeText[type]['title']"
      />
    </div>
    <div class="footer">
      <div @click="show = false">暂不{{ typeText[type]["type"] }}</div>
      <div @click="submitFun">{{ typeText[type]["type"] }}</div>
    </div>
  </van-popup>
</template>

<script>
export default {
  name: "OpinionDialog",
  data() {
    return {
      h: 0,
      msg: "",
      type: 0,
      show: false,
      typeText: [
        {
          title: "退回理由",
          type: "退回",
        },
        {
          title: "审核意见",
          type: "发送",
        },
      ],
      resolve: () => {},
    };
  },
  created() {},
  methods: {
    showFun(type, resolve) {
      this.type = type > 0 ? type : 0;
      this.msg = "";
      this.show = true;
      if (resolve) this.resolve = resolve;
      this.$nextTick(() => {
        this.h = this.$refs.content.offsetHeight - 20;
      });
    },
    submitFun() {
      if (this.msg) {
        this.resolve(this.msg);
      } else {
        dsf.layer.toast("请输入" + this.typeText[this.type]["title"], false);
      }
    },
  },
};
</script>
