<template>
  <div class="ds-control mobile ds-ouaselect">
    <div ref="header" class="header">
      <div>
        <span class="nav" @click="changeNav(0)">{{ label }}</span>
        <span
          v-for="(nav, navI) in navs"
          :key="navI"
          class="nav"
          @click="changeNav(navI + 1)"
        >
          <van-icon name="arrow" />{{ nav._name }}
        </span>
      </div>
      <van-checkbox v-model="chooseAllState" class="tool" @click="checkAll">
        全选
      </van-checkbox>
    </div>
    <div class="main" :style="{ height: mainHeight }">
      <van-checkbox-group v-model="result">
        <van-cell-group>
          <van-cell
            v-for="(item, itemI) in list"
            :key="itemI"
            :border="false"
            class="ds-multi-ellipsis-2"
            @click="toggle(itemI, item)"
          >
            <template #title>
              <van-checkbox
                ref="checkboxes"
                :name="item"
                :disabled="item.disabled"
              >
                {{ item._name }}
              </van-checkbox>
            </template>
            <template
              v-if="item.children && item.children.length > 0"
              #right-icon
            >
              <div class="to-children" @click.stop="toChildren(item)">
                <i class="iconfont icon-zuzhixiaxia" />
                下级
              </div>
            </template>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>
    </div>
    <div ref="footer" class="footer">
      <span @click="showResultPopup">
        <!-- {{
          result.length == 0 && maxTags
            ? "最多可以发送" + maxTags + "个"
            : "已选择：" + result.length + "个"
        }} -->
        已选择{{ result.length }}个
        <van-icon v-if="result.length > 0" name="arrow-up" />
      </span>
      <!-- <DsfMobileButton :disabled="result.length == 0" @click="getReturnValue">
        确定({{ result.length }}{{ maxTags ? "/" + maxTags : "" }})
      </DsfMobileButton> -->
    </div>
    <van-popup
      v-model="showResult"
      position="bottom"
      :close-on-popstate="true"
      :style="{ height: '100%' }"
    >
      <div class="top">
        <span class="btn" @click="showResult = false">取消</span>
        已选择数量:{{ resultPopup.length }}
        <span class="btn" @click="deleteResult()">确定</span>
      </div>
      <van-cell-group v-for="(r, rI) in resultPopup" :key="rI">
        <van-cell :title="r._name">
          <template #right-icon>
            <van-icon name="close" @click="deleteResultPopup(r._id, rI)" />
          </template>
        </van-cell>
      </van-cell-group>
    </van-popup>
  </div>
</template>
<script>
// import { Checkbox } from "element-ui";
import control from "_platform/mixins/control";
import dataResult from "_platform/mixins/dataResult";
export default dsf.component({
  name: "DsfMobileTreeSelector",
  ctrlCaption: "部门人员选择",
  mixins: [control, dataResult],
  design: {
    fit: true,
    isMask: true,
  },
  props: {
    label: {
      type: String,
      default: "树选择器",
      desc: "用作面包屑的起始文字",
    },
    maxTags: {
      type: [String, Number],
      default: 10,
      desc: "可选择的数量上限",
    },
    chooseIf: {
      type: String,
      default: "",
      desc: "选择条件",
    },
    dataSource: {
      type: String,
      default: "5",
      desc: "数据来源，5为选择元数据组",
    },
    chooseType: {
      type: String,
      default: "2",
      desc: "部门人员的选择类型，不同类型不同接口",
    },
    orgLevel: {
      type: [String, Number],
      default: "0",
      desc: "组织架构选择显示层级，0代表只显示子机构",
    },
    showOrgSubMechanism: {
      type: Boolean,
      default: true,
      desc: "组织架构人员类型是否仅显示子机构",
    },
    sqlUrl: {
      type: String,
      default: "",
      desc: "数据来源选择4的，接口地址",
    },
    chooseMetaData: {
      type: Object,
      default: function () {
        return {};
      },
      desc: "数据来源选择5的，选择元数据组值",
    },
  },
  data() {
    return {
      navs: [], //面包屑
      mainHeight: "", //列表高度
      chooseAllState: false, //是否全选
      list: [], //当前展示列表
      listObj: {}, //所有列表数据
      result: [], //选择的值
      showResult: false,
      resultPopup: [],
      deletedResultPopupIds: [],
      oldResult: [],
      _maxTags: undefined,
    };
  },
  created() {
    if (!dsf.isEmptyObject(this.$choiceParams)) {
      this._maxTags = this.$choiceParams.maxTags || 0;
      let oldResult = this.$choiceParams.value || [];
      _.forEach(oldResult, (r) => {
        this.oldResult.push(r.value);
      });
    }
    this.loadData();
  },
  mounted() {
    this.mainHeight = `calc(100% - ${this.$refs.header.clientHeight + "px"} - ${
      this.$refs.footer.clientHeight + "px"
    })`;
  },
  methods: {
    loadData(id) {
      if (this.isDesign) {
        this.setLists(
          [
            {
              _name: "布局",
              _id: "bj",
              children: [
                {
                  _name: "一行两列",
                  _id: "yhll",
                },
                {
                  _name: "一行三列",
                  _id: "yhsl",
                },
                {
                  _name: "栅格布局",
                  _id: "sgbj",
                },
                {
                  _name: "表格布局",
                  _id: "bgbj",
                },
                {
                  _name: "页面段落",
                  _id: "ymdl",
                },
                {
                  _name: "网页视图",
                  _id: "wyst",
                },
                {
                  _name: "内嵌视图",
                  _id: "nqst",
                },
              ],
            },
            {
              _name: "文字",
              _id: "wz",
              children: [
                {
                  _name: "文本",
                  _id: "wb",
                },
                {
                  _name: "标题",
                  _id: "bt",
                },
                {
                  _name: "标题1",
                  _id: "bt1",
                },
                {
                  _name: "标题2",
                  _id: "bt2",
                },
                {
                  _name: "标题3",
                  _id: "bt3",
                },
                {
                  _name: "标题4",
                  _id: "bt4",
                },
                {
                  _name: "标题5",
                  _id: "bt5",
                },
                {
                  _name: "标题6",
                  _id: "bt6",
                },
              ],
            },
          ],
          id
        );
      } else {
        this.getLists(id);
      }
    },
    getLists(id) {
      let loader = dsf.layer.loading(),
        sqlUrl = "",
        params = this.queryString;

      if (this.dataSource == "4") {
        sqlUrl = this.$replace(this.sqlUrl);
      } else if (this.dataSource == "5") {
        let chooseMetaData = this.chooseMetaData || {};
        if (chooseMetaData.type === "meta") {
          // 元数据
          sqlUrl = "meta/tree/byName";
          dsf.mix(true, params, {
            name: chooseMetaData.code.id,
            namespace: this.$vm ? this.$vm.nameSpace : "", //dsfa.rm
            pageName: this.$vm ? this.$vm.pageName : "", //main
            order: "[]",
          });
        } else if (chooseMetaData.type === "ds") {
          // 数据源
          sqlUrl = "meta/tree/dbSource";
          dsf.mix(true, params, {
            rmId: chooseMetaData.code,
          });
        }
      } else {
        sqlUrl = [
          ["oua/getUserByCurDept", "oua/getUserByCurDept"],
          ["oua/getDeptByCurUnit", "oua/getUserByCurUnit"],
          ["oua/getDeptByOrg", "oua/getUserByOrg"],
        ][this.dataSource * 1 - 1][this.chooseType * 1 - 1];

        if (this.dataSource == "3") {
          if (this.chooseType == "2" && this.showOrgSubMechanism) {
            // 人员类型 && 仅显示子机构 => 请求两次接口
            if (id) {
              sqlUrl = "oua/getUserByDept";
              params["deptID"] = id;
            }
            params["sExplanLevel"] = this.orgLevel;
          } else if (this.chooseType == "1") {
            params["sExplanLevel"] = this.orgLevel;
          }
        }
      }

      if (!sqlUrl) {
        dsf.layer.message("请在设计器内完成组件数据配置", false);
        return;
      }

      this.$http
        .get(sqlUrl, this.queryString)
        .done((res) => {
          if (res.success) {
            let data = res.data;
            if (id) {
              data = data.children;
            }
            this.setLists(data, id);
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error((res) => {
          dsf.layer.message(res.error.message, false);
        })
        .always(() => {
          dsf.layer.closeLoading(loader);
        });
    },
    setLists(data, id) {
      let _temp = {},
        _list = [],
        noDisabledNum = 0;

      const self = this;

      if (!Array.isArray(data)) {
        data = [data];
      }

      _.forEach(data, (node) => {
        let _id = node._id,
          _pid = node.treeinfo_pid;

        node.disabled = self.setDisabled(node);
        if (!node.disabled) {
          noDisabledNum++;
        }
        _temp[_id] = node;
        if (_temp[_pid]) {
          // 平层
          if (!_temp[_pid]["children"]) {
            _temp[_pid]["children"] = [];
          }
          _temp[_pid]["children"].push(node);
          this.listObj[_pid] = {
            data: _temp[_pid]["children"],
            noDisabledNum: -1,
            choosedNum: 0,
          };
        } else {
          _list.push(node);
          // 组织架构 && 人员类型 && 仅显示子机构 && 第一次请求
          if (
            !(
              self.dataSource == 3 &&
              self.chooseType == 2 &&
              self.showOrgSubMechanism &&
              !id
            )
          ) {
            self.listObj[_id] = {
              data: node.children || 0,
              noDisabledNum: -1,
              choosedNum: 0,
            };
          }
        }
      });

      this.list = _list;
      this.listObj[id] = {
        data: _list,
        noDisabledNum: noDisabledNum,
        choosedNum: 0,
      };
      this.setResult()
    },
    setResult() {
      // if (this.oldResult.length > 0) {
      //   let _this = this;
      //   _.each(this.list, (item) => {
      //     let i = this.oldResult.indexOf(item._id);
      //     if (i > -1) {
      //       _this.result.push(item);
      //       _this.oldResult.splice(i, 1);
      //     }
      //   });
      // }
    },
    checkAll(a) {
      _.forEach(this.$refs.checkboxes, (checkbox) => {
        if (!checkbox.disabled) {
          checkbox.toggle(this.chooseAllState);
        }
      });
      let list = this.listObj[
        this.navs[this.navs.length - 1]
          ? this.navs[this.navs.length - 1]["_id"]
          : undefined
      ];
      if (this.chooseAllState) {
        list["choosedNum"] = list.noDisabledNum;
      } else {
        list["choosedNum"] = 0;
      }
    },
    toggle(index, item) {
      if (item.disabled) {
        return;
      }
      let checked = this.$refs.checkboxes[index].checked;
      let maxTags = this.maxTags == undefined ? this.maxTags : this._maxTags;
      if (maxTags && this.result.length >= maxTags && !checked) {
        this.$refs.checkboxes[index].toggle(false);
        dsf.layer.message("最多可选择" + maxTags + "个", false);
        return;
      } else {
        this.$refs.checkboxes[index].toggle();
        let list = this.listObj[
          this.navs[this.navs.length - 1]
            ? this.navs[this.navs.length - 1]["_id"]
            : undefined
        ];
        if (checked) {
          list.choosedNum--;
          this.chooseAllState = false;
        } else {
          list.choosedNum++;
          this.chooseAllState = list.choosedNum == list.noDisabledNum;
        }
      }
    },
    toChildren(item) {
      this.navs.push(item);
      let id = item._id;
      if (this.listObj[id]) {
        this.list = this.listObj[id]["data"];
        if (
          this.list[0] &&
          this.list[0]._id &&
          this.listObj[this.list[0]._id] == undefined
        ) {
          let noDisabledNum = 0;
          _.forEach(this.list, (item, itemI) => {
            if (this.setDisabled(item)) {
              this.list[itemI].disabled = true;
            } else {
              noDisabledNum++;
            }
            this.listObj[item._id] = {
              data: item.children || 0,
              noDisabledNum: -1,
              choosedNum: 0,
            };
          });
          this.listObj[id]["noDisabledNum"] = noDisabledNum;
          this.setResult();
        }
        this.chooseAllState =
          this.listObj[id].choosedNum > 0 &&
          this.listObj[id].choosedNum == this.listObj[id].noDisabledNum;

      } else {
        this.chooseAllState = false;
        this.getLists(id);
      }
    },
    changeNav(index) {
      if (index == this.navs.length) return;
      this.navs.splice(index);
      let l = this.navs.length,
        list = {};
      if (l > 0) {
        list = this.listObj[this.navs[l - 1]._id];
      } else {
        list = this.listObj["undefined"];
      }

      this.list = list["data"];
      this.chooseAllState =
        list.choosedNum > 0 && list.choosedNum == list.noDisabledNum;
    },
    showResultPopup() {
      if (this.result.length > 0) {
        this.showResult = true;
        this.resultPopup = dsf.mix(true, [], this.result);
        this.deletedResultPopupIds = [];
      }
    },
    deleteResultPopup(id, i) {
      this.resultPopup.splice(i, 1);
      this.deletedResultPopupIds.push(id);
    },
    deleteResult() {
      this.showResult = false;
      if (this.deletedResultPopupIds.length == 0) {
        return;
      } else {
        for (let rI = 0; rI < this.result.length; rI++) {
          let r = this.result[rI],
            i = this.deletedResultPopupIds.indexOf(r._id);
          if (this.deletedResultPopupIds.length == 0) {
            break;
          } else if (i != -1) {
            this.result.splice(rI, 1);
            this.deletedResultPopupIds.splice(i, 1);
            rI--;
          }
        }
      }
    },
    getReturnValue() {
      let datas = [];
      let maxTags = this._maxTags == undefined ? this.maxTags : this._maxTags;
      if (maxTags && this.result.length > maxTags) {
        dsf.layer.message("已超出选择数量上限【" + maxTags + "】", false);
        return false;
      }
      _.forEach(this.result, (r, rI) => {
        datas.push(
          dsf.mix(
            {
              value: r._id,
              text: r._name,
            },
            r
          )
        );
      });
      this.result = [];
      return datas;
    },
    setDisabled(data) {
      if (this.chooseIf && !this.$eval(this.chooseIf, data)) {
        return true;
      } else {
        return false;
      }
    },
  },
});
</script>
