// 一天的毫秒数
export const oneDay = 86400000;

export function dateSplit(date, isMonthLastDay = false) {
  let res = {
    Y: date.getFullYear(),
    M: date.getMonth() + 1,
    D: date.getDate(),
    h: date.getHours(),
    m: date.getMinutes(),
    s: date.getSeconds()
  };
  if (isMonthLastDay) {
    let {year, month} = getDateAddMonth(res.Y, res.M, 1);
    res.MTime = new Date(year + '/' + month + '/1').getTime() - 1;
  } else {
    res.MTime = new Date(res.Y + '/' + res.M + '/1').getTime();
  }
  res.DTime = new Date(res.Y + '/' + res.M + '/' + res.D).getTime();
  res.time = new Date(`${res.Y}/${res.M}/${res.D} ${res.h}:${res.m}:${res.s}`).getTime();
  return res;
}

// 月份加n后变多少
export function getDateAddMonth(year, month, n) {
  if (!n) return {year, month};
  let newMonths = month + n;
  let newMonth = (newMonths % 12) || 12;
  newMonth = newMonth < 0 ? 12 + newMonth : newMonth;
  return {
    year: year + Math.floor((newMonths - 0.1) / 12),
    month: newMonth
  }
}

export function getValuesText(date, type) {
  if (!date) {
    if ('datetime_s' === type) {
      return ['----/--/--', '--:--:--'];
    }
    if ('datetime' === type) {
      return ['----/--/--', '--:--'];
    }
  }
  if ('datetime_s' === type) {
    return dsf.date.format(date, 'yyyy/mm/dd hh:ii:ss').split(' ');
  }
  if ('datetime' === type) {
    return dsf.date.format(date, 'yyyy/mm/dd hh:ii').split(' ');
  }
  return null;
}

// 数字补0
export const toStr = i => i < 10 ? '0' + i : i + '';

// 有单位的不补0
export const toStr2 = (i, c = '') => (c && c != '') ? i + c : toStr(i);

export const getItems = (n, c = '') => {
  return new Array(n)
    .fill('')
    .map((v, i) => {
      return {
        text: toStr2(i, c),
        disabled: false
      };
    });
}