<template functional>
  <div class="ds-grid-layout mobile" :style="$options.getStyle(_vm)">
    <template v-for="(area, index2) in props.areas">
      <div
        v-if="props.isDesign"
        :key="area.areaName"
        ref="gridAreas"
        :index="index2"
        :grid-index="area.index"
        :row-index="area.rowIndex"
        :col-index="area.colIndex"
        :colspan="area.colSpan"
        :rowspan="area.rowSpan"
        :name="area.name"
        :class="['grid-area', area.areaName]"
        :style="$options.getAreaStyle(_vm,area)"
      >
        <slot :name="area.name"></slot>
      </div>
      <div v-else :key="area.areaName" ref="gridAreas" :index="index2" :name="area.name" class="grid-area" :style="$options.getAreaStyle(_vm,area)">
        <slot :name="area.name"></slot>
      </div>
    </template>
    <template v-if="props.isDesign">
      <template v-for="(item, index) in props.gridTemplate">
        <div :key="'-' + index" :class="['grid-area','', 'e-' + index]" :style="{'grid-area':'e-'+index,'border': 'none'}"></div>
      </template>
    </template>
  </div>
</template>
<script>
export default {
  name: 'DsfMobileDataGridItemLayout',
  props: {
    isDesign: {
      type: Boolean,
      default: false
    },
    gridTemplate: {
      type: Array,
      default() {
        return []
      }
    },
    gridColumns: {
      type: Array,
      default() {
        return [];
      }
    },
    gridRows: {
      type: Array,
      default() {
        return [];
      }
    },
    areas: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  getAreaStyle(vm, area) {
    let style = {
      'grid-area': area.areaName
    }
    if (vm.props.isDesign) {
      style['min-height'] = vm.$options.getRowHeight(vm, area.rowIndex, area.colIndex, area.colspan, area.rowspan)
    }
    return style;
  },
  getTemplate(vm) {
    let template = vm.props.gridTemplate;
    let areas = [];
    let m = _.map(template, (v, index) => {
      let m2 = _.map(v, (el) => {
        return el.areaName;
      })
      if (vm.props.isDesign) {
        m2.push("e-" + index)
      }
      return "'" + m2.join(" ") + "'";
    })
    return m.join(" ");
  },
  getStyle(vm) {
    let gridColumns = vm.props.gridColumns;
    let gridRows = vm.props.gridRows;
    if (vm.props.isDesign) {
      gridColumns = JSON.parse(JSON.stringify(gridColumns));
      gridColumns.push('0');
    }
    let style = {
      'height': "100%",
      'grid-template-columns': _.map(gridColumns, (el) => dsf.pxToRem(el)).join(" "),
      'grid-template-areas': vm.$options.getTemplate(vm),
      //  'grid-template-rows':_.map(gridRows, (el) => dsf.pxToRem(el)).join(" ")
    }
    if (!vm.parent.isDesign && gridRows.length) {
      style['grid-template-rows'] = _.map(gridRows, (el) => dsf.pxToRem(el)).join(" ");
    }
    return style;
  },
  getRowHeight(vm, rowIndex, cellIndex, rowSpan, colSpan) {
    if (vm.parent.isDesign) {
      if (rowSpan > 1 && colSpan > 1) {
        return "auto";
      }
      else {
        return dsf.pxToRem(vm.props.gridRows[rowIndex])
      }
    }
    return "auto";
  }
}
</script>