<template>
  <div id="app" class="mobile-app">
    <div class="mobile-view" :class="{'demo':isDemo}">
      <template>
        <!--头部导航-->
        <div class="mobile-view-head">
        </div>
        <!--主体-->
        <div class="mobile-view-content">
          <transition :name="transitionName">
            <keep-alive v-if="isShowNavsBar" :include="cachePages" :exclude="noCachePages" :max="max">
              <router-view class="full-view has-nav-bar"></router-view>
            </keep-alive>
            <keep-alive v-else :include="cachePages" :exclude="noCachePages" :max="max">
              <router-view class="full-view no-nav-bar"></router-view>
            </keep-alive>
          </transition>
          <!--底部导航-->
          <div class="mobile-view-foot">
            <div :key="'nav-bar'" class="mobile-view-nav-bar">
              <DsfMobileButtonNav :buttons="navs"></DsfMobileButtonNav>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
export default {
  provide() {
    return {
      $isMobile: true,
      $app: this
    }
  },
  mixins: [],
  data() {
    return {
      themes: dsf.config.setting_public_default_theme,
      transitionName: '', //初始过渡动画方向
      controlName: "router-view",
      cachePages: this.$store.state.keepAlive.include,
      noCachePages: this.$store.state.keepAlive.exclude,
      navBarTitle: "",
      max:10
    }
  },
  computed: {
    isDemo() {
      //为true则表示在designer.html#/mobilesetting中使用
      return this.$root.isDemo;
    },
    navs() {
      return this.$root.navs || [];
    },
    isShowNavsBar() {
      if (this.navs.length <= 1) {
        return false;
      }
      let route = this.$route;
      let reulst = _.filter(this.navs, (nav) => {
        let href = dsf.url.anchorToRoutePath(nav.href);
        return href == route.path;
      });
      return reulst.length > 0 ? true : false;
    }
  },
  watch: {
    $route: {
      handler(to, from) {
        let route = this.$route;
        //检测to路由是否在在导航栏内
        let to_filter_index = _.findIndex(this.navs, (nav) => {
          let href = dsf.url.anchorToRoutePath(nav.href);
          return href == to.path;
        });
        //检测from路由是否在在导航栏内
        let from_filter_index = _.findIndex(this.navs, (nav) => {
          let href = dsf.url.anchorToRoutePath(nav.href);
          return href == from?.path || ""
        });
        if (to_filter_index >= 0 && from_filter_index >= 0) {
          //切换的页面都是顶级页面，无动画过渡效果
          this.transitionName = "";
        }
        else if (!from || from.path == "/") {
          //如果form路由不存在或者从根路由进入，无动画过渡效果
          this.transitionName = "";
        }
        else {
          this.transitionName = window.$$state.pageDirection
        }
        this.cachePages = this.$store.state.keepAlive.include;
        this.noCachePages = this.$store.state.keepAlive.exclude;
      },
      immediate: true
    }
  },
  beforeCreate() {

  },
  created() {
    //加载皮肤
    document.body.classList.add(this.themes);
  },
  mounted() {
  },

  methods: {
    // viewLoaded(view) {
    //   //如果页面配置了强缓存
    //   if (view.$keepAlive === true) {
    //     //如果页面初始化加载完毕才加入到keepAlive中，否则可能切换过快导致只有一半的数据缓存
    //     //同时阻止页面第一次加载时触发activated
    //     dsf.array.ensure(this.cachePages, view.$options.name);
    //   }
    // }
  }
};
</script>