<template>
  <div class="ds-control ds-page mobile" :class="getCss" :style="pageStyle">
    <!--页头-->
    <div class="ds-page-head ">
      <div v-if="hasHeader" ref="head" class="head-inner ds-page-head-color" :slot-name="slots[1].name">
        <slot :name="slots[1].name"></slot>
      </div>
    </div>
    <div class="ds-page-content" :style="pageContentStyle">
      <div v-mobile-scroll class="ds-page-center" :class="pageCenterCss" :slot-name="slots[0].name">
        <slot :name="slots[0].name">
        </slot>
      </div>
    </div>
    <!--页脚-->
    <div ref="foot" class="ds-page-foot ds-page-foot-color" :class="{'bottom-safe-area':($bottomSafeArea)}" :style="{'background-color':footerBackground}">
      <div v-if="hasFooter" ref="footInner" :style="footInnerStyle" class="foot-inner" :slot-name="slots[2].name">
        <slot :name="slots[2].name"></slot>
      </div>
      <!--iphone底部适配-->
      <!-- <div v-if="$bottomSafeArea" ref="bottomSafeArea" class="bottom-safe-area ds-page-foot-color" :style="{'background-color':footerBackground}"></div> -->
    </div>
    <div style="display:none" v-html="customCss"></div>
    <a v-if="!isDesign&&gotoIcon" ref="eleDesgin" v-drag data-flag="true" :title="`进入${title}设计模式`" class="to_desgin" @click="gotoPreview">
      <i class="iconfont icon-shezhi"></i>
    </a>
  </div>
</template>
<script>
import view from "_platform/mixins/view";
import vDrag from "_platform/mixins/vDrag";
export default dsf.component({
  name: "DsfMobilePage",
  ctrlCaption: "移动端页面",
  mixins: [view, vDrag],
  data() {
    return {
      gotoIcon: dsf.config.setting_public_gotoIcon=="1",
      headHeight: 0,
      footHeight: 0,
      footInnerHeight: 0,
      bottomSafeAreaHeight: 0,
      centerHeight: "auto",
      headerStyle: {},
      footerStyle: {},
      leftAndRIghtStyle: {},
      containerHeight: 0,
      containerWidth: 0,
      isShowNavsBar:this?.$app?.isShowNavsBar
    };
  },
  props: {
    formItemLabelWidth: {
      type: Number,
      default: 120
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: "center", controls: [] },
          { name: "head", controls: [] },
          { name: "foot", controls: [] },
          // { name: "left", controls: [] },
          // { name: "right", controls: [] }
        ];
      }
    },
    pageLevel: {
      type: Number,
      default: 1
    },
    isKeepAlive: {
      type: Boolean,
      default: false
    },
    bottomSafeArea: {
      type: Boolean,
      default: true
    }
  },
  beforeCreate() {
    // console.time("加载页面");
  },
  computed: {
    $bottomSafeArea() {
      let isDesign = this.isDesign;
      let footInnerHeight = this.footInnerHeight;
      if (!isDesign) {
        let hasNavsBar = this.isShowNavsBar;
        let bottomSafeArea = this.bottomSafeArea
        let result = hasNavsBar ? false : bottomSafeArea;
        //如果页脚有内容且页面为底部适配为true时
        if (footInnerHeight > 1 && result) {
          return true;
        }
      }
      return false;
    },
    splitWidth() {
      if (this.leftRight == 'split-small') {
        return 5;
      }
      else if (this.leftRight == 'split-medium') {
        return 10;
      }
      else if (this.leftRight == 'split-big') {
        return 15;
      }
      else {
        return 0;
      }
    },
    splitWidthHorizontal() {
      if (this.topBottom == 'split-small') {
        return "split-horizontal--8";
      }
      else if (this.topBottom == 'split-medium') {
        return "split-horizontal--10";
      }
      else if (this.topBottom == 'split-big') {
        return "split-horizontal--15";
      }
    },
    getCss() {
      return [
        ...this.$getClass(),
        (this.fit === '1' || this.fit === true) ? 'fit' : ''
      ];
    },
    pageStyle() {
      var headHeight = this.headHeight;
      var footHeight = this.footHeight;
      // var bottomSafeArea = this.$bottomSafeArea;
      // var footel = this.$refs.foot ? this.$refs.foot.parentNode : null;
      if (!this.isDesign) {
        headHeight = headHeight;
        footHeight = footHeight || 0
      }
      return {
        "padding-top": headHeight+'px',
        "padding-bottom": footHeight+'px'
      }
    },
    pageContentStyle() {
      var leftWidth = 0;
      var rightWidth = 0;
      // if (!this.isDesign) {
      //   leftWidth = leftWidth > 0 ? leftWidth + this.splitWidth : 0;
      //   rightWidth = rightWidth > 0 ? rightWidth + this.splitWidth : 0
      // }
      return {
        "padding-left": this.pxToRem(leftWidth),
        "padding-right": this.pxToRem(rightWidth),
        'width': this.isDesign ? 'auto' : (this.pageWidth == "100%" || !this.pageWidth ? 'auto' : this.pageWidth),
        'margin': "auto",
      }
    },
    pageCenterCss() {
      return [
        !this.isDesign ? this.splitWidthHorizontal : ""
      ]
    },
    hasFootInner() {
      return this.footInnerHeight > 1;
    },
    footInnerStyle() {
      return {
        'visibility': this.hasFootInner ? 'visible' : 'hidden'
      }
    }
  },
  created() {

  },
  destroyed() {
  },
  beforeDestroy() {
    if (this.$refs.eleDesgin) {
      this.$refs.eleDesgin.parentNode && this.$refs.eleDesgin.parentNode.removeChild(this.$refs.eleDesgin)
    }
  },
  beforeMount() {
    // debugger;
    // console.log(this.$scopedSlots)
  },
  mounted() {
    // console.log(this.$scopedSlots)
    // console.timeEnd("加载页面");
    if (this.isDesign) {
      this.watchCenterHeight();
      this.resize();
    }
    if (this.$refs.eleDesgin) {
      document.body.append(this.$refs.eleDesgin)
    }
  },
  updated() {
    // console.log("dfdfdf")
  },
  methods: {
    watchCenterHeight() {
      // this.resize();
      this.setCenterHeight();
      window.setTimeout(this.watchCenterHeight, 500);
    },
    setCenterHeight() {
      let head = this.$refs.head ? this.$refs.head : null;
      let foot = this.$refs.foot ? this.$refs.foot : null;
      let footInner = this.$refs.footInner ? this.$refs.footInner : null;
      // let bottomSafeArea = this.$refs.bottomSafeArea ? this.$refs.bottomSafeArea : null
      this.headHeight = head ? (head.offsetHeight > 1 ? head.offsetHeight : 0) : 0;
      this.footHeight = foot ? (foot.offsetHeight > 1 ? foot.offsetHeight : 0) : 0;
      this.footInnerHeight = footInner ? (footInner.offsetHeight > 1 ? footInner.offsetHeight : 0) : 0;
      // this.bottomSafeAreaHeight = bottomSafeArea ? bottomSafeArea.offsetHeight : 0;
    },
    //设置左右区域高度
    setleftAndRightStyle() {
      if (this.$el) {
        let style = {};
        let height = this.containerHeight - this.headHeight - this.footHeight
        style.height = this.pxToRem(height)
        style['margin-top'] = this.pxToRem(this.headHeight);
        this.leftAndRIghtStyle = style
      }
    },
    //设置容器高度
    setContainerSize() {
      if (this.$el) {
        let container = $(this.$el.parentNode);
        this.containerHeight = container.innerHeight();
        this.containerWidth = container.innerWidth();
      }
    },
    resize() {
      this.setContainerSize();
      this.setCenterHeight();
    },
    gotoPreview() {
      let isDrag = this.$refs.eleDesgin.getAttribute('data-flag')
      if (isDrag === 'true') {
        let previewUrl = `${dsf.url.getWebPath('~/designer.html#/mobile/page?B=' + this.context.B + '&M=' + this.context.M + '&pname=' + this.name + '&title=' + this.title)}`
        window.open(previewUrl)
      }
    }
  }
});
</script>