import "_platform/utils";
import "../dsf_config";
//全局样式
import "../assets/styles/mobile/dsfStyle.scss";
// 混入主题
import systemGlobalSetting from "_platform/mixins/systemGlobalSetting";

//注入平台基础组件
import platformComponents from "_platform/mobile";

//注入本地元数据
import "../metadata";
//注入入本地api
import "../api";
//引入本项目所有移动端组件
import mobile from "./../mobile/components";

//此处引入要使用的组件
import App from "./App.vue";
import store from "../store";
import router from "./router";
import NProgress from "nprogress";
import "nprogress/nprogress.css";


NProgress.configure({
  easing: "ease-in",
  minimum: 0.1,
  showSpinner: false
});

dsf.init.initPlatform(Vue);
dsf.init.globalMixin(Vue);

Vue.use(platformComponents);
Vue.use(mobile);

dsf.global.$isMobile = true;

Vue.config.productionTip = false;
const isDemo = (window.frameElement || window).name == "appDemo";

// 登录页地址处理
let loginUrl = dsf.config.setting_public_mobile_login_page;
let urlArr = loginUrl.split("#");
let path = urlArr.length > 1 ? urlArr[1] : null;
if (path) {
  let strs = path.split("?");
  if (strs.length < 2) {
    strs.push("from=${from}");
  } else if (strs.length == 2 && strs[1] == "") {
    strs[1] = "from=${from}";
  } else {
    strs[1] += "&from=${from}";
  }
  urlArr[1] = strs.join("?");
  loginUrl = urlArr.join("#");
}

router.beforeEach((to, from, next) => {
  NProgress.start();
  // 非登录页，且需要登录，跳转到登录页面
  if ((to.meta.needLogin || to.fullPath == "/") && !dsf.cookies.get("authorization_token")) {
    location.replace(loginUrl.replace("${from}", encodeURIComponent(location.href)));
    next(false);
    return;
  }
  //页面退出时（返回）时清理掉keepAlive。除非时路由的meta中keepAlive为true
  dsf.transition.clearCache(store, to, from);
  //将当前页面保存到keepAlive中
  dsf.transition.addCache(store, to, from);
  next();
});

router.afterEach((to) => {
  //通过路由的title修改页面标题
  document.title = to.meta.title || dsf.config.setting_public_logo_title || "";
  if (isDemo) {
    try {
      if (window.parent && window.parent.$mobileSetting) {
        window.parent.$mobileSetting.routeChange(to);
      }
    } catch (ex) {
      dsf.error(ex);
    }
  }
  NProgress.done();
});
//获取app信息s
let appId = "";
let navs = [];
let preloads = [];
dsf.http
  .get(`mobile/navigation/btn/${window.sessionStorage.getItem("appId") || appId}`, {})
  .then(({ data }) => {
    let configData = JSON.parse(data.data["dsfa_rm.content"] || "{}");
    navs = configData.navs;
    preloads = configData.preloads;
    return dsf.init.preloads(preloads || [], router, "mobile");
  })
  .catch((err) => {
    dsf.httpError(err);
  })
  .finally(() => {
    new Vue({
      mixins: [systemGlobalSetting],
      store,
      router,
      data() {
        return {
          isDemo,
          navs
        };
      },
      created() {
        //移动端页面增加rem适配
        dsf.flexble();
        //监听window窗体变化后，递归组件执行组件的resize函数
        dsf.resizeDetector();
        if (this.isDemo) {
          //动态加载@vant/touch-emulator模块
          import(/* webpackChunkName: "touch-emulator" */ "@vant/touch-emulator");
        }
      },
      render: (h) => h(App)
    }).$mount("#app");
  })
