
import {
  Toast,
  Dialog
} from 'vant';

export default {
  openWindow(options) {
    if (dsf.type(options) == "string") {
      let url = "";
      url = options;
      options = {};
      options.url = url;
    }
    let defaultOptions = {
      openerVm: (this._isVue ? this : null),
      url: "",
      dialogArgs: null
    };
    options = dsf.mix(defaultOptions, options);
    if (options.url.startsWith("#/")) {
      options.url = options.url.substr(1);
    }
    if (!options.url.startsWith("/")) {
      options.url = "/" + options.url;
    }
    let s = dsf.url.parse(options.url);
    if (!dsf.global.$windowArgs) {
      dsf.global.$windowArgs = {};
    }
    dsf.global.$windowArgs[s.pathname] = defaultOptions.dialogArgs || {};
    this.$router.push({
      path: options.url
    });
  },
  openDialog(options) {
    let openerVm = this._isVue ? this : null;
    let comp = null;
    if (!options.template) {
      if (options.params && options.params.path && !options.content) {
        options.content = "DsfMobileViewPart";
        options.params.showModel = "full";
      }

      if (options.content && dsf.type(options.content) !== 'string' && options.content.name) {
        comp = Object.create(options.content);
        comp.mixins = [{
          inject: {
            $dialog: {
              default: null
            }
          }
        }];
        options.content = options.content.name + "DialogCopy";
      }
    }
    let dsfDialogAttributes = [];
    if (options.type == "dialog") {
      dsfDialogAttributes = [
        `:type=type`,
        // `@confirm-cancel="confirmCancel"`,
        // `@confirm-sure="confirm"`,
        // `:message-align="'center'"`,
        `@closed="cancel"`,
        `:before-close="confirmBeforeClose"`,
        `:width=$width`,
        `:show-confirm-button="showConfirmButton"`,
        `:show-cancel-button="showCancelButton"`,
        `:confirm-button-text="confirmButtonText"`,
        `:cancel-button-text="cancelButtonText"`
      ]
    } else {
      dsfDialogAttributes = [
        `:type=type`,
        `:class="{'has-button-bar':btns.length>0}"`,
        `:has-header="hasHeader"`,
        `:width="$width"`,
        `:height="$height"`,
        `@close="close"`,
        `@closed="cancel"`,
        `:round="round"`,
        `:position="position"`,
        `:show-close="showClose"`
      ]
    }
    const elConfig = {
      router: this.$router,
      // provide() {
      //   return {
      //     openerVm: this
      //   }
      // },
      data: function () {
        return {
          type: options.type || "popup",
          dialogVisible: true,
          title: options.title || "对话框",
          content: options.content,
          width: options.width || "auto",
          height: options.height || "auto",
          destroyOnClose: true,
          params: options.params || {},
          overflow: options.overflow || "auto",
          btns: options.btns || [],
          events: options.event,
          fullscreen: options.fullscreen,
          openerVm: openerVm,
          dialogArgs: options.dialogArgs || null,
          showClose: dsf.isDef(options.showClose) ? options.showClose : true,
          position: options.position ? options.position : "bottom",
          btnPosition: options.btnPosition ? options.btnPosition : "top",
          round: options.round ? true : false,
          hasHeader: options.hasHeader === false ? false : true,
          showConfirmButton: options.showConfirmButton,
          showCancelButton: options.showCancelButton,
          confirmButtonText: options.confirmButtonText,
          cancelButtonText: options.cancelButtonText
          // dialog:options.yes||dsf.noop
          // template:options.template||""
        };
      },
      computed: {
        $width() {
          let type = this.type
          if (type == 'popup') {
            return this.position == 'bottom' || this.position == 'top' ? '100vw' : this.width;
          } else {
            return (this.width == 'auto' || !this.width) ? dsf.pxToRem(320) : dsf.pxToRem(this.width);
          }

        },
        $height() {
          let type = this.type
          if (type == 'popup') {
            return this.position == 'left' || this.position == 'right' ? '100vh' : this.height;
          } else {
            return dsf.pxToRem(this.height);
          }
        }
      },
      methods: {
        dialogSure(result) {
          if (result !== false) {
            options.yes(result);
            this.close();
          }
        },
        async clickHandler(button) {
          let result = true;
          if (button && button.handler) {
            result = await button.handler(this.$refs.content, this.$refs);
          }
          if (result !== false) {
            this.close();
          }
          return result;
        },
        close() {
          this.dialogVisible = false;
        },
        cancel() {
          let _this = this;
          _this.destroy();
        },
        async confirmBeforeClose(action, done) {
          let result = await this.clickHandler(action == 'confirm' ? this.btns[0] : this.btns[1]);
          done(result === false ? false : true);
        },
        destroy() {
          let _this = this;
          if (component) {
            component = null;
          }
          window.setTimeout(() => {
            options = null;
            openerVm = null;
            _this.$destroy();
          }, 0);
        }
      },
      template: `
        <DsfMobileDialog
          :title="title"
          ref="instance"
          :visible.sync="dialogVisible"
          ${dsfDialogAttributes.join(" ")}
          :dialog-args="dialogArgs"
          :opener-vm="openerVm"
          :dialog-sure="dialogSure"
        >
          <template v-slot:cancel>
            <button @click="close" class="van-picker__cancel">取消</button>
          </template>
          <template v-slot:title>
            {{title}}
          </template>
          <template v-if="btnPosition !== 'bottom'" v-slot:buttons>
            <button v-for="(btn,index) in btns" class="van-picker__confirm" @click="clickHandler(btn)">{{btn.text}}</button>
          </template>
          <template v-if="btnPosition === 'bottom'" v-slot:buttonBar>
            <DsfMobileButton
              v-if="!btn.class || (btn.class && btn.class.indexOf('hide') === -1)"
              v-for="(btn,$index) in btns"
              :key="$index"
              class="ds-button-bar-item"
              :title="btn.text"
              :type="btn.type"
              :btn-style="btn.btnStyle"
              @click="clickHandler(btn, $event)"
            >
              {{ btn.text }}
            </DsfMobileButton>
          </template>
          <template v-slot:dialog-buttons>
            <van-button v-if="!btn.class || (btn.class && btn.class.indexOf('hide') === -1)" v-for="(btn,index) in btns" :class="btn.class ? btn.class : 'van-dialog__confirm'" @click="clickHandler(btn)">{{btn.text}}</van-button>
          </template>
          <template v-slot:default>
            <div v-if="type=='popup'" :style="{'position':'absolute','top':'0', right: 0, left: 0, bottom: 0, 'overflow':(overflow||'auto')}">
              ${options.template ? options.template : `<component :is="content" v-bind="params" ref="content" v-on="events" @yes="dialogSure"></component>`
        }
            </div>
            <div v-else-if="type=='dialog'" :style="{'height':$height,'overflow':(overflow||'auto')}">
              ${options.template ? options.template : `<component :is="content" v-bind="params" ref="content" v-on="events" @yes="dialogSure"></component>`
        }
            </div>
          </template>
        </DsfMobileDialog>`
    }
    if (comp) {
      elConfig.components = {
        [options.content]: comp
      };
    }
    const Dialog = Vue.extend(elConfig);
    let component = new Dialog().$mount();
    document.body.appendChild(component.$el);
    return component;
  },
  message: function (options, type) {
    if (dsf.isUnDef(type)) {
      Toast(options);
    } else {
      let opts = {
        message: ""
      }
      if (dsf.type(options) == "string") {
        opts.message = options;
      }
      if (type !== false) {
        Toast.success(opts.message)
      } else {
        Toast.fail(opts.message);
      }
    }
  },
  alert: function (options) {
    if (dsf.type(options) == "string") {
      options = {
        message: options
      };
    }
    return Dialog.alert(options);
  },
  confirm: function (options) {
    if (dsf.type(options) == "string") {
      options = {
        message: options
      };
    }
    return Dialog.confirm(options);
  },
  loading: function (str, close) {
    return Toast.loading({
      message: str || '加载中...',
      forbidClick: true,
      duration: 0,
      onClose: close ? close : dsf.noop
    });
  },
  closeLoading: function (loaderIndex) {
    loaderIndex && loaderIndex.clear();
  },
}