export default {
  props: {
    /* 可选范围设置 */
    selectStartType: {
      type: Number,
      default: 0
    },
    selectEndType: {
      type: Number,
      default: 0
    },
    // 固定日期
    selectStartDate: {
      type: Number,
      default: Date.now()
    },
    selectEndDate: {
      type: Number,
      default: Date.now()
    },
    // 动态日期(根据天数)
    selectStartDateByDays: {
      type: Number,
      default: 0
    },
    selectEndDateByDays: {
      type: Number,
      default: 0
    },
    // 动态日期(根据月数)
    selectStartDateByMonths: {
      type: Number,
      default: 0
    },
    selectEndDateByMonths: {
      type: Number,
      default: 0
    },
    // 动态日期(根据年数)
    selectStartDateByYears: {
      type: Number,
      default: 0
    },
    selectEndDateByYears: {
      type: Number,
      default: 0
    },
  },
  computed: {
    // 计算时间范围--起始
    minDate() {
      if (!this.selectStartType) return undefined;
      // 当前日期
      if (this.selectStartType === 1) {
        return this.getDate(new Date());
      }
      // 固定日期
      if (this.selectStartType === 2) {
        return this.getDate(this.selectStartDate);
      }
      // 动态日期(根据天数)
      if (this.selectStartType === 3) {
        let _d = this.getDate();
        _d = this.getDate(_d.getTime() + this.selectStartDateByDays * 86400000);
        return _d;
      }
      // 动态日期(根据月数)
      if (this.selectStartType === 4) {
        let _d = new Date();
        let m = _d.getMonth() + this.selectStartDateByMonths + 1;
        let y = _d.getFullYear() + Math.floor(m / 12) + '/';
        m = m % 12;
        m = m.length === 1 ? '0' + m : m;
        return new Date(y + m + '/01 00:00:00');
      }
      // 动态日期(根据年数)
      if (this.selectStartType === 5) {
        let _d = new Date();
        let y = _d.getFullYear() + this.selectStartDateByYears;
        return new Date(y + '/01/01 00:00:00');
      }
      return undefined;
    },
    // 计算时间范围--结束
    maxDate() {
      if (!this.selectEndType) return undefined;
      // 当前日期
      if (this.selectEndType === 1) {
        return this.getDate(new Date(), 'yyyy/mm/dd 23:59:59');
      }
      // 固定日期
      if (this.selectEndType === 2) {
        return this.getDate(this.selectEndDate);
      }
      // 动态日期(根据天数)
      if (this.selectEndType === 3) {
        let _d = this.getDate();
        return this.getDate(_d.getTime() + this.selectEndDateByDays * 86400000);
      }
      // 动态日期(根据月数)
      if (this.selectEndType === 4) {
        let _d = new Date();
        let m = _d.getMonth() + this.selectEndDateByMonths + 2;
        let y = _d.getFullYear() + Math.floor(m / 12) + '/';
        m = m % 12;
        m = m.length === 1 ? '0' + m : m;
        _d = new Date(y + m + '/01 00:00:00');
        _d.setTime(_d.getTime() - 86400000);
        return _d;
      }
      // 动态日期(根据年数)
      if (this.selectEndType === 5) {
        let _d = new Date();
        let y = _d.getFullYear() + this.selectEndDateByYears;
        return new Date(y + '/12/31 00:00:00');
      }
      return undefined;
    }
  },
  methods: {
    getDate(date, format = 'yyyy/mm/dd 00:00:00') {
      if (date === undefined) {
        date = new Date();
      }
      return new Date(dsf.date.format(date, format))
    }
  }
}