<template>
  <div class="ds-control ds-form-item ds-system mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <van-field
        v-model="inputValue"
        readonly
        :label="showLabel && label ? label : ''"
        :label-width="getLabelWidth(true)"
        :required="required"
        input-align="right"
      ></van-field>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl";
export default dsf.component({
  name: "DsfMobileUpdateUserName",
  mixins: [formControl],
  ctrlCaption: "修改人",
  props: {
    caption: {
      type: String,
      default: dsf.config.kw.updateusername,
    },
    label: {
      type: String,
      default: "修改人",
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    simple: {
      type: Boolean,
      default: false,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("system-meta-data", {
          code: dsf.config.kw.updateusername,
          valueAttributes: [
            {
              name: "修改人姓名",
              code: dsf.config.kw.updateusername,
              type: dsf.metadata.getDataType("string"),
              length: "50",
              defaultValue: null,
              unit: null,
            },
          ],
        });
      },
    },
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      }
    }
  },
});
</script>
