<template>
  <van-tabbar v-model="active" class="ds-bottom-nav ds_control" :fixed="false" :safe-area-inset-bottom="true">
    <van-tabbar-item v-for="(item,index) in buttons" :key="index" :name="index" @click.stop="go(item.href)">
      <span>{{ item.text }}</span>
      <template v-if="active==index">
        <DsfIcon v-if="item.activeIcon" slot="icon" :name="item.activeIcon?item.activeIcon:'yiwen'"></DsfIcon>
        <DsfIcon v-else-if="item.icon" slot="icon" :name="item.icon?item.icon:'yiwen'"></DsfIcon>
      </template>
      <template v-else>
        <DsfIcon v-if="item.icon" slot="icon" :name="item.icon?item.icon:'yiwen'"></DsfIcon>
      </template>
    </van-tabbar-item>
  </van-tabbar>
</template>
<script>
export default {
  
  name: 'DsfMobileButtonNav',
  ctrlCaption: '自定义组件',
  props: {
    buttons: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      active: 0
    }
  },
  computed: {
  },
  watch: {
    $route(to, from) {
      this.matchUrl(to);
    }
  },
  created() {
    this.matchUrl(this.$route)
  },
  beforeCreate() {
  },
  beforeMount() {
  },
  mounted() {
  },
  beforeDestroy() {
  },
  destroyed() {
  },
  methods: {
    matchUrl(to) {
      this.setActive(to.path);
    },
    setActive(path) {
      let index = _.findIndex(this.buttons, (item) => {
        return this.urlSome(item.href, path)
      });
      if (index >= 0) {
        this.active = index;
      }
    },
    go(url) {
      if (this.urlSome(this.$route.path, url)) {
        return;
      }
      url = dsf.url.anchorToRoutePath(url);
      this.$router.push({
        path: url
      })
    },
    urlSome(url1, url2) {
      url1 = dsf.url.anchorToRoutePath(url1);
      url2 = dsf.url.anchorToRoutePath(url2);
      return url1 == url2
    }
  }
}
</script>