<template>
  <div class="ds-control" style="padding: 0">
    <div v-if="(isDesign || sPID) && flowProcess.length > 0" class="ds-form-item ds-flowprocess mobile work_flow-process">
      <!-- <div class="top-menus">
      <DsfMobileButton size="small" :type="show === 'info' ? 'plain' : ''" @click="show = 'normal'">简约</DsfMobileButton>
      <DsfMobileButton size="small" :type="show === 'normal' ? 'plain' : ''" @click="show = 'info'">详细</DsfMobileButton>
    </div> -->
      <!-- <div v-show="show === 'normal'" class="content">
      <situationSimple :flowInfo="flowProcess" />
    </div> -->
      <div v-show="show === 'info'" class="content">
        <div
          v-for="line in flowProcess"
          :key="line.linkid"
          class="item"
          :class="{
            'no-user': !line.rusername,
            unusual: line.STATUS == '-4',
            noyj: !line.opinionatts || (line.opinionatts && !line.opinionatts.Content && !line.opinionatts.Attachments),
          }"
        >
          <van-image round width="40px" height="40px" fit="cover" :src="'/oua/getRedisUserPhoto?userId=' + line.ruserid">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error>
              <img class="header" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
            </template>
          </van-image>

          <van-image class="icon" round fit="cover" :src="getStateImg(line)"></van-image>
          <div class="des">
            <p class="first-child">
              <span class="title">{{ line.nodename }}</span>
              <span class="time">{{ format(line.reachtime) }}</span>
            </p>
            <p v-if="line.rusername" class="name">{{ line.rusername }}</p>
            <p v-if="getIdea(line)" class="yj">{{ getIdea(line) }}</p>
            <p class="files">
              <van-grid :column-num="3" :border="false">
                <van-grid-item
                  v-for="(file, i) in getFiles(line)"
                  :key="i"
                  :icon="getType(file)"
                  :text="file.originalFileName"
                  @click="preview(file)"
                >
                </van-grid-item>
              </van-grid>
            </p>
          </div>
          <van-popup v-model="previewing" :style="{ height: '80%', width: '100%' }">
            <div>
              <dsf-iframe v-if="previewing" :src="filePath"></dsf-iframe>
            </div>
          </van-popup>
          <van-image-preview v-model="showImg" :images="images" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import control from "_platform/mixins/control";
// import situationSimple from "./situationSimple";
export default dsf.component({
  name: "DsfMobileFlowProcess",
  mixins: [control],
  ctrlCaption: "办理情况",
  components: {
    // situationSimple,
  },
  props: {
    caption: {
      type: String,
      default() {
        return "wfprocess";
      },
    },
  },
  data() {
    return {
      sPID: this.$route.query.sPID,
      sCurLinkID: this.$route.query.sCurLinkID,
      show: "info",
      flowProcess: [],
      previewing: false,
      filePath: "",
      images: [],
      showImg: false,
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      if (this.isDesign) {
        this.flowProcess = [
          {
            nodename: "开始",
            STATUS: 0,
            linkid: "faedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "同意",
            },
            ruserid: "",
            pid: "af60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
          {
            nodename: "审批",
            STATUS: -4,
            linkid: "saedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "不同意",
            },
            ruserid: "",
            pid: "sf60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
          {
            nodename: "拟稿",
            STATUS: 1,
            linkid: "caedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "",
            },
            ruserid: "",
            pid: "cf60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
        ];
      } else {
        this.$http
          .get("wfr/getProcessInfoByPID", {
            sPID: this.sPID,
          })
          .then((res) => {
            if (res.data.success) {
              res.data.data.forEach((item) => {
                item.opinionatts = JSON.parse(item.opinionatts);
                if (item.linkid === this.sCurLinkID && item.pid === this.sPID) {
                  if (item.STATUS != 0 && item.STATUS != 1) {
                    let params = dsf.mix({}, this.$route.query, {
                      isHide: true,
                    });
                    //更新url添加id参数
                    this.$router.replace({
                      path: this.$route.path,
                      query: params,
                    });
                  }
                }
              });
              this.flowProcess = res.data.data;
            }
          });
      }
    },
    getStateImg(data) {
      let imgI = data.STATUS;
      if (imgI == "-31") {
        imgI = "-3";
      } else if (imgI == "-1") {
        imgI = "-2";
      }
      if (!imgI && imgI !== 0) {
        return null;
      }
      return require(`_platform/assets/styles/themes/workFlow/status${imgI}.png`);
    },
    getIdea(data) {
      if (data.opinionatts) {
        return data.opinionatts.Content;
      }
      return null;
    },
    getFiles(data) {
      if (data.opinionatts) {
        return data.opinionatts.Attachments;
      }
      return null;
    },
    format(str) {
      if (str) {
        return dsf.date.format(str, "mm-dd hh:ii");
      } else {
        return "";
      }
    },
    getType(file) {
      if (file.contentType.slice(0, 5) === "image") {
        return "photo-o";
      } else {
        return "orders-o";
      }
    },
    preview(files) {
      if (files && files.contentType && files.contentType.slice(0, 5) !== "image") {
        this.filePath = dsf.config.webRoot + "preview/file?fileId=" + files.id;
        this.previewing = true;
      } else {
        this.images = [files.relativePath];
        this.showImg = true;
      }
    },
  },
});
</script>
