<template>
  <div v-if="isDesign || (sPID && sCurLinkID)" class="ds-control ds-form-item ds-textbox mobile dispose-idea" :class="getCss">
    <!-- <div class="ds-control ds-form-item ds-textbox mobile dispose-idea" :class="getCss"> -->
    <div class="txt">
      <van-field
        v-model="inputValue"
        :placeholder="placeholder"
        :type="isTextarea ? 'textarea' : 'text'"
        :rows="textareaRows"
        :disabled="disabled"
        :label-width="getLabelWidth(true)"
        :label="showLabel && label ? label : ''"
      >
      </van-field>
    </div>
    <div v-if="options.length > 0" class="template">
      <div>
        <span v-for="(tpl, i) in options" :key="i" @click="inputValue = tpl.key_word">{{ tpl.key_word }}</span>
      </div>
    </div>
    <div v-if="visibleUpBtn || visibleUpImgBtn" class="funs">
      <i v-if="visibleUpBtn" class="iconfont icon-fujian" @click="uploader(0)"></i>
      <i v-if="visibleUpImgBtn" class="iconfont icon-tupian" @click="uploader(1)"></i>
    </div>

    <van-uploader
      ref="uploader"
      v-model="uploaderData"
      :class="{ no: uploaderData.length == 0 }"
      :max-count="9"
      multiple
      :after-read="uploaderAfterRead"
      :accept="acceptsType"
      @click-preview="preview"
      @delete="deleteFile"
    />

    <van-popup v-model="previewing" :style="{ height: '80%', width: '100%' }">
      <div>
        <dsf-iframe v-if="previewing" :src="filePath"></dsf-iframe>
      </div>
    </van-popup>
  </div>
</template>
<script>
import formControl from "_platform/mixins/formControl";
import disposeIdea from "_platform/mixins/compnents/flow/disposeIdea";
export default dsf.component({
  name: "DsfMobileDisposeIdea",
  ctrlCaption: "处理意见",
  mixins: [formControl, disposeIdea],
  data() {
    return {
      showOptions: false,
      sPID: 0,
      sCurLinkID: 0,
      multipleFiles: [],
      options: [],
      isSave: true,
      inputValue: "",
      acceptTypes: [".doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document", "image/*"],
      acceptsType: "",
      uploaderData: [],
      previewing: false,
      filePath: "",
    };
  },
  methods: {
    uploader(acceptsIndex) {
      this.acceptsType = this.acceptTypes[acceptsIndex];
      this.$nextTick(() => {
        this.$refs.uploader.chooseFile();
      });
    },
    uploaderAfterRead(files) {
      if (!files.length) {
        files = [files];
      }
      _.forEach(files, (file) => {
        file.status = "uploading";
        file.message = "上传中...";

        let error = false;
        let formData = new FormData();
        formData.append("file", file.file);
        this.$http
          .post("file/upload", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .done((d) => {
            if (d.state == "20000") {
              file.status = "done";
              file.data = d.data[0];
              this.multipleFiles.push(d.data[0]);
            } else {
              error = d.message || true;
            }
          })
          .error((d) => {
            error = d.message || true;
          })
          .always(() => {
            if (error) {
              file.status = "failed";
              file.message = "上传失败";
              file._message = error;
            }
          });
      });
    },
    preview(files) {
      if (files && files.data && files.data.contentType && files.data.contentType.slice(0, 5) !== "image") {
        this.filePath = dsf.config.webRoot + "preview/file?fileId=" + files.data.id;
        this.previewing = true;
      }
    },
    deleteFile(file) {
      this.multipleFiles.splice(this.multipleFiles.indexOf(file.data), 1);
    },
  },
});
</script>
