<template>
  <van-popup v-model="show" class="work_flow-usersDialog" position="bottom" :style="{ height: '100%' }">
    <div class="header">请选择人员</div>
    <div class="content" :class="{ isFristSend: isFristSend }">
      <template v-if="isFristSend">
        <div class="txt">
          <van-field v-model="message" rows="2" autosize type="textarea" placeholder="请输入审批意见" />
        </div>
        <div v-if="tpls.length > 0" class="template">
          <div>
            <span v-for="(tpl, i) in tpls" :key="i" @click="message = tpl.key_word">{{ tpl.key_word }}</span>
          </div>
        </div>
        <div class="funs">
          <i class="iconfont icon-fujian" @click="uploader(0)"></i>
          <i class="iconfont icon-tupian" @click="uploader(1)"></i>
        </div>

        <van-uploader
          ref="uploader"
          v-model="uploaderData"
          :class="{ no: uploaderData.length == 0 }"
          :max-count="9"
          multiple
          :after-read="uploaderAfterRead"
          :accept="acceptsType"
          @click-preview="preview"
        />

        <van-popup v-model="previewing" :style="{ height: '80%', width: '100%' }">
          <div>
            <dsf-iframe v-if="previewing" :src="filePath"></dsf-iframe>
          </div>
        </van-popup>
      </template>

      <div class="users">
        <p>您即将发给：</p>
        <div
          v-for="d in data.filter((d) => {
            return d.users.length > 0;
          })"
          :key="d.id"
          class="dept"
          :class="{ noSeletc: !Object.keys(d.selects).length }"
        >
          <div class="desc">
            <div class="title">{{ getLinkName(d.name) }}</div>
            <div class="num">{{ (d.users.length == 1 ? "1" : Object.keys(d.selects).length) || 0 }}人审核</div>
          </div>
          <div class="imgs">
            <div v-for="(user, i) in d.users" :key="user.ID" :class="{ noSeletc: !user.isSelect }" @click="changeUser(user, i, d)">
              <van-image round width="40px" height="40px" fit="cover" :src="'/oua/getRedisUserPhoto?userId=' + user.ID">
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
                <template v-slot:error>
                  <img class="header" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
                </template>
              </van-image>
              <van-icon v-if="user.isSelect" name="checked" />
              <span>{{ user.Name }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <div @click="show = false">暂不发送</div>
      <div v-if="isShowBack" @click="goBack">返回</div>
      <div @click="submit">发送</div>
    </div>
  </van-popup>
</template>

<script>
export default {
  name: "ChooseUsersDialog",
  data() {
    return {
      data: [],
      show: false,
      message: "",
      loader: null,
      tpls: [],
      resolve: () => {},
      back: () => {},
      acceptTypes: [".doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document", "image/*"],
      acceptsType: "",
      uploaderData: [],
      previewing: false,
      filePath: "",
      isFristSend: false,
      isShowBack: false,
    };
  },
  created() {
    this.getTemplate();
  },
  methods: {
    showFun(data, resolve, back, isShowBack) {
      this.isShowBack = isShowBack;
      this.isFristSend = data._isFristSend ? true : false;
      this.show = true;
      this.data = [];
      if (data && data.SendParams && data.SendParams.Lines && data.SendParams.Lines.length) {
        let lines = data.SendParams.Lines;
        for (let i in lines) {
          let line = lines[i];
          this.data.push({
            name: line.Name,
            id: line.LineID,
            users: [],
            selects: {},
            sNum: 0,
            MultiplayerEnabled: line.MultiplayerEnabled,
          });
          if (line.Selected) {
            this.getUserData(line.Receivers.Dept, null, this.data.length - 1);
          }
        }
      }
      if (this.data.length > 0) {
        let userNum = 0;
        let j = 0;
        for (let i in this.data) {
          let _d = this.data[i];
          if (_d.sNum > 0) {
            j = -1;
            break;
          }
          if (_d.users.length == 1) {
            j = i;
          }
          userNum += _d.users.length;
        }
        // 一个人的时候自动选中
        if (userNum == 1 && j != -1) {
          let user = this.data[j].users[0];
          user.isSelect = true;
          this.data[j]["selects"][user.ID] = user;
          this.data[j]["sNum"]++;
        }
      }
      if (resolve) {
        this.resolve = resolve;
      }
      if (back) {
        this.back = back;
      }
    },
    getLinkName(name) {
      let ns = name.split("→");
      return ns[ns.length - 1];
    },
    getUserData(d, parent, index) {
      if (d.Objs) {
        d.ChildDepts = d.ChildDepts.concat(d.Objs);
      }
      if (d.Type != 0) {
        if (d.Selected * 1 > 0) {
          // 0 人工选择、1 默认选择、2必选
          this.data[index]["selects"][d.ID] = d;
          this.data[index]["sNum"]++;
          d.isSelect = true;
        }
        d["index"] = this.data[index]["users"].length;
        this.data[index]["users"].push(d);
      }
      for (let i in d.ChildDepts) {
        this.getUserData(d.ChildDepts[i], d, index);
      }
    },
    getTemplate() {
      let loader = dsf.layer.loading(),
        msg = "";
      this.$http
        .get("wfr/getOpinionKeyWord", {})
        .done((res) => {
          if (res.success) {
            this.tpls = res.data || [];
          } else {
            msg = `获取意见模板，请求异常`;
          }
        })
        .error((res) => {
          msg = `获取意见模板，服务器异常`;
        })
        .always(() => {
          dsf.layer.closeLoading(loader);
          if (msg) {
            dsf.layer.toast(msg, false);
          }
        });
    },
    changeUser(user, i, d) {
      let end = false;
      if (!d.MultiplayerEnabled) {
        for (let key in d["selects"]) {
          let _d = d["selects"][key];
          if (_d.Selected * 1 == 2) {
            end = true;
          } else {
            d["selects"] = {};
            d.users[_d.index]["isSelect"] = false;
          }
        }
      }
      if (end) return;
      if (user.Selected * 1 != 2) {
        user.isSelect = user.isSelect ? false : true;
        if (user.isSelect) {
          d["selects"][user.ID] = user;
        } else {
          delete d["selects"][user.ID];
        }
        this.$set(d["users"], i, user);
      }
    },
    goBack() {
      this.back();
      this.show = false;
    },
    submit() {
      let param = [],
        ltn = 0;
      for (let deptI in this.data) {
        let dept = this.data[deptI];
        let userIds = "";
        for (let id in dept.selects) {
          userIds += (!userIds ? "" : ",") + id;
        }
        if (userIds) {
          param[ltn] = dept.id + ":" + userIds;
          ltn++;
        }
      }

      if (ltn == 0) {
        dsf.layer.toast("请选择", false);
      } else {
        let files = [];
        for (let i in this.uploaderData) {
          let d = this.uploaderData[i];
          files.push(d.data);
        }
        this.resolve(param.join(";"), this.message, files);
      }
    },
    uploader(acceptsIndex) {
      this.acceptsType = this.acceptTypes[acceptsIndex];
      this.$nextTick(() => {
        this.$refs.uploader.chooseFile();
      });
    },
    uploaderAfterRead(files) {
      if (!files.length) {
        files = [files];
      }
      _.forEach(files, (file) => {
        file.status = "uploading";
        file.message = "上传中...";

        let error = false;
        let formData = new FormData();
        formData.append("file", file.file);
        this.$http
          .post("file/upload", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .done((d) => {
            if (d.state == "20000") {
              file.status = "done";
              file.data = d.data[0];
            } else {
              error = d.message || true;
            }
          })
          .error((d) => {
            error = d.message || true;
          })
          .always(() => {
            if (error) {
              file.status = "failed";
              file.message = "上传失败";
              file._message = error;
            }
          });
      });
    },
    preview(files) {
      if (files && files.data && files.data.contentType && files.data.contentType.slice(0, 5) !== "image") {
        this.filePath = dsf.config.webRoot + "preview/file?fileId=" + files.data.id;
        this.previewing = true;
      }
    },
  },
};
</script>

<style></style>
