<template>
  <div class="dsf-calendar-main" :style="{height: boxHeight + 'px'}">
    <div class="dsf-calendar-main__weekdays">
      <span class="dsf-calendar-main__weekday">一</span>
      <span class="dsf-calendar-main__weekday">二</span>
      <span class="dsf-calendar-main__weekday">三</span>
      <span class="dsf-calendar-main__weekday">四</span>
      <span class="dsf-calendar-main__weekday">五</span>
      <span class="dsf-calendar-main__weekday">六</span>
      <span class="dsf-calendar-main__weekday">日</span>
    </div>
    <div ref="box" class="dsf-calendar-main__calendar" @scroll="onScroll">
      <div
        v-for="({key, month, year, days}, index) in showCalendar"
        :key="key"
        class="dsf-calendar-main__calendar-item"
        :style="{height: rowHeight * days.length + 40 + 'px'}">
        <template v-if="index > active - 3 && index < active + 3">
          <div class="dsf-calendar-main__calendar-item-date">{{ key }}</div>
          <div class="dsf-calendar-main__calendar-item-body day">
            <div class="dsf-calendar-main__calendar-item-bg">
              <span class="month">{{ month }}</span>
            </div>
            <div v-for="(week, index2) in days" :key="index2" class="dsf-calendar-main__calendar-item-week">
              <div
                v-for="({date: day, offset, active}, index3) in week"
                :key="index3"
                class="dsf-calendar-main__calendar-item-day"
                :class="cellClass(day, month, year, offset)"
                @click="cellClick(day)">
                <span>{{ day.getDate() }}</span>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { oneDay, getDateAddMonth } from './calendarUtils';

export default {
  name: 'CalendarDayBox',
  inject: ['$calendar'],
  props: {
    boxHeight: {
      type: Number,
      required: true
    },
    // 日历行高
    rowHeight: {
      type: Number,
      required: true
    },
    // 选中方式
    type: {
      type: String,
      required: true
    },
    minTime: {
      type: Object,
      required: true
    },
    maxTime: {
      type: Object,
      required: true
    },
    value: {
      type: Array,
      required: true
    },
    // 是否多选
    multiple: {
      type: Boolean,
      required: true
    },
    // 范围选择
    range: {
      type: Boolean,
      required: false
    }
  },
  data() {
    return {
      loaded: false,
      // 当前显示月份的索引
      active: 0,
      // 显示出的日历
      showCalendar: [],
      // 缓存已计算的日历
      calendar: {}
    }
  },
  computed: {
    // 当前显示的月份
    activeMonth() {
      return this.showCalendar.length > this.active ? this.showCalendar[this.active] : null;
    }
  },
  watch: {
    '$calendar.currentView'(to) {
      if (to == 'day' && !this.loaded) {
        this.loaded = true;
        this.doLocation(this.$calendar.defaultViewDate || this.$calendar.now);
      }
    }
  },
  created() {
    this.$calendar.dayView = this.$calendar.$dayView = this;
  },
  mounted() {
    this.$box = this.$refs.box;
    // 初始化日历
    if (this.$calendar.currentView == 'day') {
      this.loaded = true;
      this.doLocation(this.$calendar.defaultViewDate || this.$calendar.now);
    }
  },
  methods: {
    // 单元格样式
    cellClass(day, month, year, offset) {
      if (offset) {
        return { offset };
      }
      let res = {
        toDay: this.$calendar.now.getTime() == day.getTime(),
        disabled: this.minTime.DTime > day.getTime() || day.getTime() > this.maxTime.DTime
      };
      if (this.range && this.multiple) {
        // todo 范围多选
      } else if (this.range && !this.multiple) {
        // 范围单选
        if (this.$calendar.tempValue.length > 0) {
          let vs = this.$calendar.tempValue[0].getTime() - day.getTime();
          if (vs >= 0 && vs < oneDay) {
            res.activeS = true;
          }
          if (this.$calendar.tempValue.length == 2) {
            let ve = this.$calendar.tempValue[1].getTime() - day.getTime();
            if (ve >= 0 && ve < oneDay) {
              res.activeE = true;
            }
            if (vs < 0 && ve >= oneDay) {
              res.activeC = true;
            }
          }
        }
      } else if (this.multiple) {
        // todo 多选
      } else if (this.value.length) {
        this.value.find(v => {
          let diff = v.getTime() - day.getTime();
          return diff >= 0 && diff < oneDay;
        }) && (res.active = true);
      }
      return res;
    },
    cellClick(date) {
      let disabled = this.minTime.DTime > date.getTime() || date.getTime() > this.maxTime.DTime;
      if (disabled) return;
      if (this.range && this.multiple) {
        // 范围多选
        this.formatValueByCheckboxRange(date);
      } else if (this.range && !this.multiple) {
        // 范围单选
        this.formatValueByRadioRange(date);
      } else if (this.multiple) {
        // 多选
        this.formatValueByCheckbox(date);
      } else {
        // 单选
        this.formatValueByRadio(date);
      }
    },
    // 单选
    formatValueByRadio(date) {
      if (['date', 'month', 'year'].indexOf(this.type) > -1) {
        this.$calendar.value = [date];
      } else if (['datetime', 'datetime_s'].indexOf(this.type) > -1) {
        let t, d = dsf.date.format(date, 'yyyy/mm/dd');
        if (this.value.length) {
          t = dsf.date.format(this.value[0], this.type == 'datetime' ? 'hh:ii:00' : 'hh:ii:ss');
        } else {
          t = dsf.date.format(new Date(), this.type == 'datetime' ? 'hh:ii:00' : 'hh:ii:ss');
        }
        d = new Date(d + ' ' + t);
        this.$calendar.value = [d];
      }
    },
    // 范围单选
    formatValueByRadioRange(date) {
      if (!this.$calendar.tempValue.length || this.$calendar.tempValue.length == 2) {
        this.$calendar.tempValue = [date];
        this.$calendar.value = [];
      } else {
        let oldTime = this.$calendar.tempValue[0];
        if (oldTime > date) {
          this.$calendar.tempValue = [date];
        } else {
          oldTime = date;
        }
        this.$calendar.tempValue.push(new Date(oldTime.getTime() + oneDay - 1));
        this.$calendar.value = [[...this.$calendar.tempValue]];
      }
    },
    // todo 多选
    formatValueByCheckbox(date) {
      if (['date', 'month', 'year', 'datetime', 'datetime_s'].indexOf(this.type) > -1) {
        let index = this.value.findIndex(v => {
          let diff = date.getTime() - v.getTime();
          return diff >= 0 && diff < oneDay;
        });
        if (index > -1) {
          this.value.splice(index, 1);
        } else {
          this.value.push(date);
        }
      }
    },
    // todo 范围多选
    formatValueByCheckboxRange() {

    },
    // 监听滚动条
    onScroll() {
      let offsetHeight = this.$box.offsetHeight;
      let scrollHeight = this.$box.scrollHeight;
      let scrollTop = this.$box.scrollTop;
      // 滚动条高度小于3屏高触发
      if (scrollTop < offsetHeight * 3) {
        let {height: addHeight, n } = this.onChange(-3);
        // 向上增加月份时，滚动条位置会变动，在此做校正
        scrollHeight += addHeight;
        scrollTop += addHeight;
        this.active += n;
      }
      // 滚动条离底部距离小于3屏高触发
      else if (scrollHeight - offsetHeight - scrollTop < offsetHeight * 3) {
        this.onChange(3)
      }
      // 这里计算当前显示月份的索引
      // 思路：从上从下分别计算大概的索引，然后找到对应的dom遍历计算坐标
      let averageHeight = scrollHeight / this.showCalendar.length;
      let index1 = Math.floor(scrollTop / averageHeight);
      let index2 = this.showCalendar.length - Math.floor((scrollHeight - offsetHeight - scrollTop) / averageHeight) - 1;
      let baseHeight = offsetHeight / 2;
      let i = Math.min(index1, index2); i = i < 0 ? 0 : i;
      let l = Math.max(index1, index2) + 1; l = l > this.showCalendar.length ? this.showCalendar.length : l;
      for (;i < l; i++) {
        let itemDom = this.$box.children[i];
        if (!itemDom) break;
        let diffTop = itemDom.offsetTop - scrollTop;
        if (diffTop < baseHeight && diffTop + itemDom.offsetHeight >= baseHeight) {
          this.active = i;
          break;
        }
      }
    },
    /**
     * 追加月份
     * @param diff 小于0: 向上追加-diff个月份。大于0: 向下追加diff个月份
     * @returns {{n: number, height: number}} n: 新增的个数。height: 新增的高度
     */
    onChange(diff) {
      let height = 0, n = 0;
      if (diff < 0) {
        let active = this.showCalendar[0];
        for (let i = -1; i >= diff; i--) {
          let {year, month} = getDateAddMonth(active.year, active.month, i);
          if (this.minTime.Y > year || (this.minTime.Y == year && this.minTime.M > month)) break;
          let item = this.setCalendar({year, month});
          this.showCalendar.unshift(item);
          height += item.height + 40;
          n++;
        }
      } else if (diff > 0) {
        let active = this.showCalendar[this.showCalendar.length - 1];
        for (let i = 1; i <= diff; i++) {
          let {year, month} = getDateAddMonth(active.year, active.month, i);
          if (this.maxTime.Y < year || (this.maxTime.Y == year && this.maxTime.M < month)) break;
          let item = this.setCalendar({year, month});
          this.showCalendar.push(item);
          height += item.height + 40;
          n++;
        }
      }
      return { height, n };
    },
    // 定位到date这个时间
    doLocation(date) {
      date = date || this.$calendar.now;
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      this.reset(year, month);
    },
    // 重置日历
    reset(year, month) {
      if (this.minTime.Y > year || (this.minTime.Y == year && this.minTime.M > month)) {
        year = this.minTime.Y;
        month = this.minTime.M;
      }
      if (this.maxTime.Y < year || (this.maxTime.Y == year && this.maxTime.M < month)) {
        year = this.maxTime.Y;
        month = this.maxTime.M;
      }
      this.showCalendar = [this.setCalendar({ year, month })];
      let {height, n } = this.onChange(-4);
      this.onChange(4);
      this.active = n;
      this.$nextTick(() => {
        this.$box.scrollTo(0, height + 40);
      });
    },
    // 根据月份获取当前天数信息
    setCalendar({year, month}) {
      if (!this.calendar[year + '-' + month]) {
        let days = dsf.date.getCalendar(year, month - 1);
        this.calendar[year + '-' + month] = {
          key: year + '年' + month + '月',
          year,
          month,
          days,
          height: this.rowHeight * days.length
        };
      }
      return this.calendar[year + '-' + month];
    }
  }
}
</script>