import wfLastNode from "./LastNodeDialog";
import wfChooseLinks from "./ChooseLinksDialog.vue";
import wfChooseUsers from "./ChooseUsersDialog.vue";
import wfProcess from "./FlowProcess.vue";
import wfOpinion from "./OpinionDialog.vue";
export default {
  components: {
    wfLastNode,
    wfChooseLinks,
    wfChooseUsers,
    wfProcess,
    wfOpinion
  },
  data() {
    return {
      sWFID: '',
      sObjectID: '',
      sObjectName: '',
      sPID: '',
      sCurLinkID: '',
      requestID: null,
      loader: null,
      processInfo: [],
      isFristSend: false
    }
  },
  methods: {
    loading(str) {
      if (!this.loader)
        this.loader = dsf.layer.loading(str || '发送中');
    },
    closeLoading(errorTxt) {
      dsf.layer.closeLoading(this.loader);
      this.loader = null;
      if (errorTxt) {
        dsf.layer.message(errorTxt, false);
      }
    },
    sendAction(noForbidAutoSend) {
      if (this.sWFID) {
        this.loading();

        let url = "",
          param = {},
          errorTxt = '';

        const that = this;

        if (!that.sPID) {
          // 开始发送
          url = "wfr/startSend";
          param = {
            sWFID: that.sWFID,
            sObjectID: that.sObjectID,
            sObjectName: that.sObjectName,
            sCurNodeID: "",
            sExInfos: "",
            sForbidAutoSend: noForbidAutoSend ? 0 : 1
          }
          this.isFristSend = true;
        } else {
          // 继续发送
          url = "wfr/send";
          param = {
            sPID: that.sPID,
            sCurLinkID: that.sCurLinkID,
            sExInfos: "",
            sForbidAutoSend: noForbidAutoSend ? 0 : 1
          }
        }

        // 发送时的意见处理
        param.sOpinion = JSON.stringify({
          'Content': '',
          'Attachments': []
        })

        that.requestID = null;
        this.$http.post(url, param)
          .done(response => {
            if (response.state == 20000 && response.data.RequestID) {
              that.wFType(response, []);
            } else {
              errorTxt = `发送异常：${response.message}`
            }
          })
          .error(response => {
            errorTxt = `${url}，服务器异常`
          })
          .always(() => {
            that.closeLoading(errorTxt);
          })
      }
    },
    wFType(res, layerBoxs, isShowBack) {
      const that = this;
      if (res.state == 20000) {
        let data = res.data,
          code = data.Code;
        if (data.SendParams && data.SendParams.Lines && data.SendParams.Lines.length > 0 && data.SendParams.Lines[0].LineID == 'ZggVllh4iHUbKq1' && !(code == 20000 || code == 20004 || code == 20005)) {
          that.sendAction(true);
          return
        }

        that.requestID = data.RequestID;
        if (code == 20000 || code == 20004 || code == 20005) {
          // 直接完成发送 || 文件已办结 || 蒋豪加的已办结状态
          for (let i in layerBoxs) {
            layerBoxs[i].show = false;
          }
          that.$refs.wfLastNode.showFun(data, () => {
            that.$router.go(-1);
          })
        } else if (code == 81000) {
          // 选择发送节点 isShowBack //选人是否显示返回按钮
          that.$refs.wfChooseLinks.showFun(data, (d, isShowBack) => {
            layerBoxs.push(that.$refs.wfChooseLinks);
            that.flowSendNextRequest("wfr/sendToLine", {
              sRequestID: that.requestID,
              sLineID: d.LineID,
              sForbidAutoSend: 1
            }, layerBoxs, isShowBack);
          }, () => {
            layerBoxs.push(that.$refs.wfChooseLinks);
            const param = {
              sRequestID: data.RequestID,
              sLineID: data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].BackLineID,
            };
            that.flowSendNextRequest("wfr/setLineToBack", param, layerBoxs);
          })
        } else if (code == 82000) {
          // 选择人
          data._isFristSend = that.isFristSend;
          that.$refs.wfChooseUsers.showFun(data, (d, txt, files) => {
            layerBoxs.push(that.$refs.wfChooseUsers);
            that.flowSendNextRequest("wfr/sendToReceivers", {
              sRequestID: that.requestID,
              sReceivers: d,
              sOpinion: JSON.stringify({
                'Content': txt,
                'Attachments': files
              })
            }, layerBoxs);
          }, () => {
            const param = {
              sRequestID: data.RequestID,
              sLineID: data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].SendToLineID,
            };
            that.flowSendNextRequest("wfr/setLineToBack", param, layerBoxs);
          }, isShowBack)
        } else {
          let errorTxt = null;
          switch (code) {
            case 91000:
              errorTxt = "无后续可发送节点"
              break;
            case 92000:
              errorTxt = "无可选的发送对象"
              break;
            case 92001:
              errorTxt = "该环节只允许发送给一人，但是必选项超过一个"
              break;
            case 92002:
              errorTxt = "未找到继承环节"
              break;
            case 93000:
              errorTxt = "该事项已被处理"
              break;
            case 93001:
              errorTxt = "保存流转情况信息失败"
              break;
            case 93002:
              errorTxt = "保存流转环节信息失败"
              break;
            case 94001:
              errorTxt = "与分支不能设置办结节点"
              break;
            case 95001:
              errorTxt = "没有可回收的环节"
              break;
            case 95002:
              errorTxt = "回收环节已接收或已处理，无法回收"
              break;
            case 99000:
              errorTxt = "获取数据锁失败"
              break;
          }
          dsf.layer.alert({
            title: "发送中断",
            message: (errorTxt || '未知流程类型发送'),
            confirmButtonText: "返回列表页"
          }, () => {
            that.$router.go(-1);
          });
        }
      }
    },
    flowSendNextRequest(url, param, layerBoxs, isShowBack) {
      this.loading();
      let errorTxt = "";
      const that = this;
      this.$http.post(url, param)
        .done(response => {
          if (response.state == 20000) {
            that.wFType(response, layerBoxs, isShowBack);
          } else {
            errorTxt = `发送异常：${response.message}`
          }
        })
        .error(response => {
          errorTxt = `${url}，服务器异常`
        })
        .always(() => {
          that.closeLoading(errorTxt);
        })
    },
    getProcessInfo(layer) {
      if (!layer) {
        layer = dsf.layer.loading();
      }
      const that = this;
      let errorTxt = null;
      this.$http.get('wfr/getProcessInfoByPID', {
        sPID: that.sPID
      })
        .done(response => {
          if (response.state == 20000) {
            that.$refs.wfProcess.setData(response.data)
          } else {
            errorTxt = `获取流转情况异常：${response.message}`
          }
        })
        .error(response => {
          errorTxt = `获取流转情况异常`
        })
        .always(() => {
          dsf.layer.closeLoading(layer);
          if (errorTxt) {
            dsf.layer.message(errorTxt, false);
          }
        })
    },
    getSendBackLink() {
      this.loading('退回中');
      const that = this;
      let errorTxt = '';
      this.$http.post('wfr/getSendBackLink', {
        sPID: that.sPID,
        sCurLinkID: that.sCurLinkID,
      })
        .done(response => {
          if (response.state == 20000 && response.data.BackLinks) {
            let BackLinks = response.data.BackLinks;
            if (BackLinks.length == 1) {
              that.sendBack(BackLinks[0]);
            } else {
              let links = {
                SendParams: {
                  Lines: []
                }
              };
              for (let i in BackLinks) {
                let BackLink = BackLinks[i];
                links['SendParams']['Lines'].push({
                  Name: BackLink.NodeName + '-' + BackLink.LinkUser.RUserName,
                  ...BackLink
                })
              }
              that.$refs.wfChooseLinks.showFun(links, (d) => {
                that.sendBack(d, that.$refs.wfChooseLinks)
              })
            }
          } else {
            errorTxt = `退回异常：${response.data.Message}`
          }
        })
        .error(response => {
          errorTxt = `退回中断，服务器异常`
        })
        .always(() => {
          that.closeLoading(errorTxt);
        })
    },
    sendBack(d, layer) {
      const that = this;
      let errorTxt = '';
      that.$refs.wfOpinion.showFun(0, (msg) => {
        that.loading('退回中');
        let param = {
          sPID: that.sPID,
          sCurLinkID: that.sCurLinkID,
          sReason: msg,
          sBackLinkID: d.ID
        }
        this.$http.post('wfr/sendBack', param)
          .done(response => {
            if (layer)
              layer.show = false;
            that.$refs.wfOpinion.show = false;

            if (response.state == 20000 && response.data && response.data.Code == '21000') {
              dsf.layer.alert({
                message: '已成功退回到 ' + d.NodeName + ' 环节'
              }, () => {
                that.$router.go(-1);
              })
            } else {
              errorTxt = `${response.data.Message || '退回异常'}`
            }
          })
          .error(response => {
            errorTxt = `退回中断，服务器异常`
          })
          .always(() => {
            that.closeLoading(errorTxt);
          })
      })
    },
    flowDestroy() {
      // 销毁
      const that = this;
      let errorTxt = '';
      if (that.sPID) {
        that.loading('销毁中');
        this.$http.post('wfr/destroy', {
          sPID: that.sPID
        }).done(response => {
          if (response.state == 20000) {
            dsf.layer.alert({
              message: '销毁成功'
            }, () => {
              that.$router.go(-1);
            })
          } else {
            errorTxt = `销毁异常`
          }
        })
          .error(response => {
            errorTxt = `销毁中断，服务器异常`
          })
          .always(() => {
            that.closeLoading(errorTxt);
          })
      } else {
        dsf.layer.message('缺少sPID参数，销毁异常', false);
      }
    },
    flowForceClose() {
      //强制办结
      const that = this;
      let errorTxt = '';
      if (that.sPID) {
        that.loading('强制办结中');
        this.$http.post('wfr/forceClose', {
          sPID: that.sPID
        }).done(response => {
          if (response.state == 20000) {
            dsf.layer.alert({
              message: '强制办结成功'
            }, () => {
              that.$router.go(-1);
            })
          } else {
            errorTxt = `强制办结异常`
          }
        })
          .error(response => {
            errorTxt = `强制办结中断，服务器异常`
          })
          .always(() => {
            that.closeLoading(errorTxt);
          })
      } else {
        dsf.layer.message('缺少sPID参数，强制办结异常', false);
      }
    }

  }
}
