import { addButton } from '../../register/buttonRegister'
import handler from './handler';

addButton({
  caption: "保存",
  text: "保存",
  title: "保存",
  actionName: "system_form_save",
  icon: "icon-baocun",
  params: [
    { value: "", desc: "是否要刷新父页面", default: "true" },
    {
      value: "",
      desc: "保存时附加的参数多用于二次开发判断区分",
      default: "",
    },
    {
      value: "",
      desc: "如果弹出窗口打开表单,保存后是否关闭窗口",
      default: "false",
    },
    {
      value: "",
      desc: "保存成功的提示信息",
      default: "保存成功",
    },
    {
      value: "",
      desc: "保存失败的提示信息",
      default: "保存失败",
    },
  ],
  moduleName: "基础平台",
  groupName: "公共",
  handler: handler.system_form_save
}, 'mobile');

addButton({
  caption: "发送",
  text: "发送",
  title: "发送",
  actionName: "system_flow_send",
  icon: "",
  params: [],
  moduleName: "基础平台",
  groupName: "流程",
  handler: handler.system_flow_send
}, 'mobile');

addButton({
  caption: "批量发送",
  text: "批量发送",
  title: "批量发送",
  actionName: "system_batch_flow_send",
  icon: "",
  params: [{
      value: "",
      desc: "tableName",
      default: "tableName 若有几张表格，则以','分割",
    },
    {
      value: "",
      desc: "ObjectID",
      default: "ObjectID 若有几张表格，则以','分割",
    },
    {
      value: "",
      desc: "ObjectName",
      default: "ObjectName 若有几张表格，则以','分割",
    },
  ],
  moduleName: "基础平台",
  groupName: "流程",
  handler: handler.system_batch_flow_send
}, 'mobile')

addButton({
  caption: "弹出流程意见框",
  text: "弹出流程意见框",
  title: "弹出流程意见框",
  actionName: "system_flow_openDialog",
  icon: "icon-hao",
  params: [
    { value: "", desc: "模态框标题", default: "对话框" },
    { value: "", desc: "打开页面的地址", default: "无" },
    {
      value: "",
      desc: "id",
      default: "id",
    },
    {
      value: "",
      desc: "sCurLinkID",
      default: "sCurLinkID",
    },
    {
      value: "",
      desc: "sPID",
      default: "sPID",
    },
    {
      value: "",
      desc: "模态框宽,高（单位%或px  用,分割）",
      default: "80%,500px",
    },
    {
      value: "",
      desc: "模态框保存按钮会抛出的事件, 方便二次开发",
      default: "system-dialog-save",
    },
  ],
  moduleName: "基础平台",
  groupName: "流程",
  handler: handler.system_flow_openDialog
}, 'mobile');


addButton({
  caption: "退回",
  text: "退回",
  title: "退回",
  actionName: "system_flow_sendBack",
  icon: "",
  if: "@[url('sPID')!=undefined]",
  params: [
    { value: "@[url('sPID')]", desc: "sPID", default: "" },
    { value: "@[url('sCurLinkID')]", desc: "sCurLinkID", default: "" },
  ],
  moduleName: "基础平台",
  groupName: "流程",
  handler: handler.system_flow_sendBack
}, 'mobile');


