<template>
  <van-dialog v-model="show" class="work_flow" :before-close="beforeClose" confirm-button-text="返回列表" :title="title || '流程已发送到以下人员'">
    <div v-if="!title && data.Links" class="userbox">
      <template
        v-for="line in data.Links.filter((line) => {
          return line.Status == 0 && line.LinkUser;
        })"
      >
        <span :key="line.id">{{ getUser(line) }}</span>
      </template>
    </div>
  </van-dialog>
</template>

<script>
export default {
  name: "LastNodeDialog",
  data() {
    return {
      data: {},
      show: false,
      title: null,
      resolve: () => {},
    };
  },
  methods: {
    showFun(data, resolve) {
      this.data = data || {};
      if (this.data.Code == 20004 || this.data.Code == 20005) {
        this.title = "流程已办结";
      } else {
        this.title = null;
      }
      this.show = true;
      if (resolve) this.resolve = resolve;
    },
    getUser(line) {
      let linkUser = line.LinkUser,
        type = linkUser.Type,
        userHtml = "";
      if (type == 1) {
        // 直接选人
        userHtml = linkUser.UserName;
      } else if (type == 2) {
        // 部门+角色
        userHtml = linkUser.DeptName + "-" + linkUser.RoleName;
      } else if (type == 3) {
        // 岗位
        userHtml = linkUser.JobName;
      } else if (type == 4) {
        // 人+关系
        userHtml = linkUser.UserName + "-" + linkUser.RelationName;
      } else if (type == 5) {
        // 机器人
        userHtml = linkUser.RobotName;
      }
      return userHtml;
    },
    beforeClose(action, done) {
      done();
      this.resolve();
    },
  },
};
</script>

<style></style>
