<template>
  <van-nav-bar :left-arrow="showBack" :border="true" :left-text="showBack?'返回':''" :title="title" @click-left="goBack"></van-nav-bar>
</template>
<script>
export default {
  name: "DsfDemoBrowserNavBar",
  props: {
    title: {
      type: String,
      default: "无标题"
    },
    goBack: {
      type: Function,
      default: dsf.noop
    },
    showBack: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    isDemo() {
      return this.$root.isDemo;
    }
  },
  methods: {
  }
}
</script>