let uid = 0;
const MOBILE_SCROLL_SESSIONKEY = 'mobile-scroll-';

export default {
  install (Vue) {
    Vue.directive('mobileScroll', {
      bind(el, binding, vnode, oldVnode) {
        if (vnode.context && !vnode.context.isDesign) {
          let sid = ++uid;
          el.setAttribute("scroll-id", sid);
        }
      },
    });
  }
}
