<template>
  <div
    class="ds-control ds-form-item ds-upload upload-img mobile"
    :class="getCss"
  >
    <template v-if="simple && readOnly && !isDesign"> 简易模式 只读 </template>
    <template v-else>
      <van-field
        :label="showLabel && label ? label : ''"
        :required="required"
        :label-width="getLabelWidth(true)"
      >
        <template #input>
          <van-uploader
            v-model="fileListCopy"
            :multiple="multiple"
            :accept="uploadFileExts"
            :after-read="afterRead"
            :before-read="beforeRead"
            :before-delete="beforeDelete"
            :max-count="uploadFileNum"
            :show-upload="!readOnly"
          />
        </template>
      </van-field>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl";

export default dsf.component({
  name: "DsfMobileUploadImg",
  mixins: [formControl],
  ctrlCaption: "图片上传",
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    label: {
      type: String,
      default: "图片上传",
    },
    multiple: {
      type: Boolean,
      default: true,
    },
    uploadFileNum: {
      type: [Number, String],
      default: 5,
    },
    uploadFileSize: {
      type: [Number, String],
      default: 1024000,
    },
    required: {
      type: Boolean,
      default: false,
    },
    requestUrl: {
      type: String,
      default: "",
    },
    isCutPic: {
      type: Boolean,
      default: true,
    },
    aspectRatio: {
      type: String,
      default: "",
    },
    uploadFileExts: {
      type: String,
      default: ".jpg,.jpeg,.png,.gif",
    },
    extsSetting: {
      type: Object,
      default() {
        return {};
      },
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("photo-meta-data");
      },
    },
  },
  data() {
    return {
      previewPopup: false, //预览文件pop
      filePath: "", // 预览文件url
      fileListCopy: [], // 文件列表
      uploadFileType: "image/*", //默认上传类型
      // uploadFileExts: ".bpm,.jpg,.jpeg,.png,.gif", //允许上传后缀名
      acceptMime: "", // 弹窗筛选条件MiMe类型
      editImg: false,
      editFileImg: {},
      cropper: "",
    };
  },
  computed: {
    setRequestUrl() {
      return dsf.url.getWebPath(this.requestUrl || "file/upload/");
    },
  },
  watch: {
    value(to) {
      this.initValue(to);
    },
  },
  created() {
    this.initValue(this.value);
    this.rebuildDialogData();
  },
  methods: {
    initValue(to) {
      if (dsf.type(to) == "string") {
        this.fileListCopy = JSON.parse(to);
      }
    },
    beforeDelete(file, detail) {
      dsf.layer
        .confirm(`确定要删除【${file.originalFileName}】文件吗?`)
        .then(() => {
          let files = JSON.parse(JSON.stringify(this.fileListCopy));
          files.splice(detail.index, 1);
          this.emitValueChange(JSON.stringify(files));
        })
        .catch((e) => e);
    },
    afterRead(files) {
      let _that = this;
      if (!files.length) {
        files = [files];
      }
      let baseIndex = this.fileListCopy.length - files.length;
      _.forEach(files, (file, index) => {
        let formData = new FormData();
        formData.append("file", file.file);
        _that.$http
          .post(this.setRequestUrl, formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .done((response) => {
            if (response.success && response.state == 20000) {
              let data = response.data[0];
              let files = JSON.parse(JSON.stringify(this.fileListCopy));
              data.url = data.absolutePath;
              files[0] = response.data[0];
              files[index + baseIndex] = response.data[0];
              this.emitValueChange(JSON.stringify(files));
            }
          })
          .catch((err) => {
            dsf.layer.message(err.message, false);
          });
      });
    },
    beforeRead(files) {
      if (!files.length) {
        files = [files];
      }
      // 判断文件格式
      _.forEach(files, (item) => {
          if (
            !this.uploadFileExts.includes(item.name.split(".")[1].toLowerCase())
          ) {
            dsf.layer.message(
              `请选择包含这些格式的文件：${this.uploadFileExts}`,
              false
            );
            return false;
          }
      });
      return true;
    },
    computeFileSize(fileSize) {
      let filesizeUnit = "";
      //1048576
      if (parseInt(fileSize) > 1050000) {
        filesizeUnit = (fileSize / Math.pow(1024, 2)).toFixed(2) + "M";
      } else {
        filesizeUnit = (fileSize / Math.pow(1024, 1)).toFixed(2) + "K";
      }
      return filesizeUnit;
    },
    rebuildDialogData() {
      if (this.extsSetting.extStr) {
        let obj = this.extsSetting;
        // this.uploadFileType = 'file';//默认上传类型
        // this.uploadFileExts=""//允许上传后缀名
        // this.acceptMime =""//弹窗筛选条件MiMe类型
        this.uploadFileType = obj.fileType;
        this.isLinkeMine = obj.isLinkeMine;
        this.uploadFileExts = obj.extStr;
        this.acceptMime = obj.mineStr;
        // console.log("this.uploadFileExts", this.uploadFileExts);
        // console.log("this.acceptMime", this.acceptMime);
      }
    },
  }
});
</script>
