<template>
  <div class="ds-control ds-form-item ds-select mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else-if="readOnly && !isDesign">
      <van-field
        :label-width="getLabelWidth(true)"
        :label="showLabel && label || ''"
        :disabled="disabled"
        :readonly="readOnly"
        input-align="left"
      >
        <template #input>
          <DsfTextProxy v-model="valueProxy" :show-label="false" v-bind="_self.$props"></DsfTextProxy>
        </template>
      </van-field>
    </template>
    <template v-else>
      <van-field v-bind="attr" v-on="events">
        <template #input>
          <DsfTextProxy v-model="valueProxy" :show-label="false" v-bind="_self.$props"></DsfTextProxy>
        </template>
      </van-field>
      <van-popup v-if="multiple" v-model="showPicker" get-container="#app" position="bottom" round>
        <van-multiple-picker
          ref="picker"
          :show="showPicker"
          :columns="$items"
          value-key="value"
          show-toolbar
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
      <van-popup v-else v-model="showPicker" get-container="#app" position="bottom" round>
        <van-picker
          ref="picker"
          :columns="$items"
          value-key="text"
          show-toolbar
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl";

export default dsf.component({
  name: "DsfMobileSelect",
  mixins: [formControl],
  ctrlCaption: "下拉框",
  props: {
    // 值
    value: {
      type: [Object, String, Number, Array],
      required: false
    },
    // 选项
    items: {
      type: Array,
      default() {
        return [];
      }
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    formatter: {
      type: String,
      default: "textValue^text"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
    multiple: {
      type: Boolean,
      default: false
    },
    // 文字对齐方式
    inputAlign: {
      type: String,
      default: 'right'
    },
    // 是否有右侧图标
    hasIcon: {
      type: Boolean,
      default: true
    },
    // 多选时，选中值是否以合并展示
    collapseTags: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      valueCopy: {value: '', text: ''},
      showPicker: false
    }
  },
  computed: {
    attr() {
      let res = {
        labelWidth: this.getLabelWidth(true),
        label: this.showLabel && this.label || '',
        value: this.text,
        placeholder: this.placeholder,
        disabled: this.disabled,
        readonly: true
      };
      if (!this.readOnly) {
        res.inputAlign = this.inputAlign;
        res.required = this.required;
        if (this.hasIcon) {
          res.rightIcon = 'arrow'
        }
      } else {
        res.inputAlign = 'left';
      }
      return res;
    },
    events() {
      let res = {};
      if (!this.readOnly && !this.disabled) {
        res.click = this.fieldClick
      }
      return res;
    },
    text() {
      return this.value && this.value.text || '';
    },
    $items () {
      return this.items || [];
    },
    valueProxy() {
      if (!this.value || !this.multiple || this.value.length < 2) {
        return this.value;
      } else {
        return {
          value: this.value[0].value + '(+' + (this.value.length - 1) + ')',
          text: this.value[0].text + '(+' + (this.value.length - 1) + ')'
        }
      }
    }
  },
  watch: {
    'value': {
      immediate: true,
      handler (to) {
        this.initValue(to);
      }
    }
  },
  created() {
  },
  methods: {
    initValue(to) {
      if (this.simple || this.readOnly || this.isDesign || dsf.isUnDef(to) || _.isEqual(to, this.valueCopy)) {
        return;
      }
      let value = null;
      let type = dsf.type(to);
      if (this.multiple) {
        let values = [];
        if (type == 'object' && to.value) {
          values = [to.value];
        } else if (type == 'array') {
          to.forEach(t => {
            if (dsf.type(t) == 'string' || dsf.type(t) == 'number') {
              values.push(t)
            } else if (dsf.type(t) == 'object' && t.value) {
              values.push(t.value)
            }
          })
        } else {
          values = [to];
        }
        if (this.$refs.picker) {
          this.$refs.picker.setValues(values);
        } else if (values.length) {
          value = [];
          values.forEach(v => {
            let item = this.$items.find(item => item.value == v);
            if (item) {
              value.push(item);
            }
          });
        }
      } else {
        if (type == 'array' && to.length) {
          to = to[0];
          type = dsf.type(to);
        }
        if (type == 'Object' && to.value) {
          to = to.value;
        }
        if (dsf.isDef(to)) {
          let index = this.$items.findIndex(item => item.value == to.value);
          if (index > -1) {
            this.$refs.picker?.setIndexes([index]);
            value = this.$items[index];
          } else {
            value = {value: '', text: ''};
          }
        } else {
          value = {value: '', text: ''};
        }
      }
      if (this.$refs.picker) {
        value = this.$refs.picker.getValues();
        if (!this.multiple) {
          value = value[0];
        }
      }
      this.valueCopy = value;
      this.emitValueChange(value);
    },
    fieldClick() {
      if (!this.readOnly) {
        let hasPicker = !!this.$refs.picker;
        this.showPicker = true;
        if (!hasPicker) {
          this.$nextTick(() => {
            if (!this.$refs.picker) return;
            if (this.multiple && this.valueCopy.length) {
              this.$refs.picker.setValues(this.valueCopy.map(v => v.value));
            } else {
              this.$refs.picker.setValues([this.valueCopy.text]);
            }
          })
        }
      }
    },
    onConfirm(value, index) {
      this.showPicker = false;
      this.valueCopy = value;
      this.emitValueChange(value);
      this.$dispatch("change", value, index);
      this.$nextTick(() => {
        this.emitFormValidate(value);
      });
    },
  }
});
</script>