<template>
  <van-empty style="background-color:#fff">
    <template>
      <van-button type="default" icon="arrow-left" @click="back">返回</van-button>
    </template>
    <template v-slot:image>
      <van-image :src="image" width="100%" height="auto"></van-image>
    </template>
  </van-empty>
</template>

<script>

const image = require("_platform/assets/styles/themes/img/404.png")
export default {
  name: 'Error',
  data() {
    return {
      image: image
    }
  },
  methods: {
    back() {
      this.$router.back();
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.show = !(to.path == "/");
    next();
  }
};
</script>
<style lang="scss" scoped>
.van-empty /deep/ {
  & > .van-empty__image {
    width: 100%;
    height: auto;
  }
}
</style>
