<template>
  <div ref="container" class="ds-control mobile ds-banner" :class="{'ds-no-padding':!padding}">
    <van-swipe
      v-if="reload"
      ref="swipe"
      :loop="loop"
      :autoplay="autoPlay"
      :duration="duration"
      :style="swipeHeight"
      :height="height"
      :show-indicators="showIndicators"
      :vertical="vertical"
      :touchable="touchable"
      :indicator-color="indicatorColor">
      <van-swipe-item v-for="(item, index) in data" :key="`ds-banner-item-${index}`">
        <slot name="default" v-bind="item">
          <div slot="default" class="ds-banner-item" :style="containerStyles" @click="handleClick(item)">
            <img v-lazy="item.image" :style="containerStyles" @dragstart.prevent.stop />
          </div>
        </slot>
      </van-swipe-item>
    </van-swipe>
    <template v-if="showButton">
      <div class="ds-banner-btn dbt-l" @click="prev">
        <i class="iconfont icon-zuo"></i>
      </div>
      <div class="ds-banner-btn dbt-r" @click="next">
        <i class="iconfont icon-weibiaoti34"></i>
      </div>
    </template>
  </div>
</template>
<script>
import control from "_platform/mixins/control";
export default dsf.component({
  name: "DsfMobileBanner",
  mixins: [control],
  ctrlCaption: "轮播图",
  props: {
    height: {
      type: String,
      default: "100px"
    },
    list: {
      type: Array,
      default() {
        return [];
      }
    },
    url: {
      type: String,
      default: ""
    },
    showButton: {
      type: Boolean,
      default: true
    },
    showIndicators: {
      type: Boolean,
      default: true
    },
    padding: {
      type: Boolean,
      default: false
    },
    vertical: {
      type: Boolean,
      default: false
    },
    touchable: {
      type: Boolean,
      default: true
    },
    indicatorColor: {
      type: String,
      default: "#1989fa"
    },
    autoPlay: {
      type: [String, Number],
      default: 3000
    },
    loop: {
      type: Boolean,
      default: true
    },
    duration: {
      type: Number,
      default: 500
    }
  },
  computed: {
    containerStyles() {
      return {
        height: dsf.isNumber(this.height) ? `${this.height}px` : this.height
      };
    },
    swipeHeight() {
      if (this.vertical) {
        return {
          height: dsf.isNumber(this.height) ? `${this.height}px` : this.height
        };
      }
      return {};
    },
    swipeProps() {
      return {
        loop: this.loop
      };
    }
  },
  data() {
    return {
      data: [],
      activeIndex: 0,
      reload: true
    };
  },
  mounted() {
    // window.addEventListener('resize', this.resize);
  },
  created() {
    if (!this.url) {
      this.data = dsf.mix(true, [], this.list);
    } else {
      this.loadData();
    }
  },
  destroyed() {
    // window.removeEventListener('resize', this.resize);
  },
  methods: {
    resize() {
      this.$refs.swipe.resize();
    },
    prev() {
      this.$refs.swipe.prev();
    },
    next() {
      this.$refs.swipe.next();
    },
    handleClick(item) {
      item.url && this.$router.push(item.url);
    },
    validateApiFiled(data) {
      if (!Array.isArray(data.data)) {
        console.error("数据源必须为数组");
      } else {
        const flag = data.data.every((it) => {
          return Object.prototype.toString.call(it) === "[object Object]" && Object.prototype.hasOwnProperty.call(it, "image");
        });
        if (!flag) {
          console.error(`
            接口数据格式请参考: \n
            [{\n
              image: '',\n
              title: '',\n
              desc: ''\n
            }]
          `);
        }
      }
    },
    loadData() {
      this.$http.get(this.url).then(({ data }) => {
        this.validateApiFiled(data.data);
        this.data = data.data;
      });
    }
  },
  watch: {
    list(v, ov) {
      if (!this.url) {
        this.data = dsf.mix(true, [], v);
      }
    },
    vertical(v, ov) {
      this.reload = false;
      this.$nextTick(() => {
        this.reload = true;
      });
    }
  }
});
</script>