<template>
  <div class="ds-control ds-button-bar mobile" :class="getCss">
    <div v-if="align != 'justify-iconleft-buttonright'" class="bar-wrap">
      <template v-for="(button, $index) in showButtonsReal">
        <DsfMobileButton
          :key="$index"
          class="ds-button-bar-item"
          :style="getButtonStyle(button)"
          :icon="getIcon(button)"
          :title="button.title"
          :disabled="button.disabled"
          :size="size"
          :type="button.type"
          :btn-style="button.btnStyle"
          @click="clickHandler(button, $event)"
        >
          {{ button.text }}
        </DsfMobileButton>
      </template>
      <DsfMobileButton
        v-if="moreButtonsReal.length > 0"
        class="ds-button-bar-item"
        :style="getButtonStyle()"
        :btn-style="moreButtonStyle"
        type="plain"
        :size="size"
        :icon="moreButtonStyle == 'icon-text' ? 'gengduo1' : ''"
        @click="open"
      >
        {{ moreButtonText }}
      </DsfMobileButton>
    </div>
    <!--图标按钮居左 普通按钮居右-->
    <div v-else-if="align == 'justify-iconleft-buttonright'" class="bar-wrap">
      <div>
        <template v-for="(button, $index) in iconButtons">
          <DsfMobileButton
            :key="$index"
            class="ds-button-bar-item"
            :style="getButtonStyle(button)"
            :icon="getIcon(button)"
            :title="button.title"
            :disabled="button.disabled"
            :size="size"
            :type="button.type"
            :btn-style="button.btnStyle"
            @click="clickHandler(button, $event)"
          >
            {{ button.text }}
          </DsfMobileButton>
        </template>
        <DsfMobileButton
          v-if="moreButtonsReal.length > 0 && moreButtonStyle == 'icon-text'"
          class="ds-button-bar-item"
          :style="getButtonStyle()"
          :btn-style="moreButtonStyle"
          :size="size"
          icon="gengduo1"
          @click="open"
        >
          {{ moreButtonText }}
        </DsfMobileButton>
      </div>
      <div>
        <template v-for="(button, $index) in generalButtons">
          <DsfMobileButton
            :key="$index"
            class="ds-button-bar-item"
            :style="getButtonStyle(button)"
            :icon="getIcon(button)"
            :title="button.title"
            :disabled="button.disabled"
            :size="size"
            :type="button.type"
            :btn-style="button.btnStyle"
            @click="clickHandler(button, $event)"
          >
            {{ button.text }}
          </DsfMobileButton>
        </template>
        <DsfMobileButton
          v-if="moreButtonsReal.length > 0 && moreButtonStyle != 'icon-text'"
          class="ds-button-bar-item"
          :style="getButtonStyle()"
          :btn-style="moreButtonStyle"
          type="plain"
          :size="size"
          icon=""
          @click="open"
        >
          {{ moreButtonText }}
        </DsfMobileButton>
      </div>
    </div>
    <van-action-sheet v-if="actionSheetShow" v-model="moreOpen" get-container="body" @closed="closed">
      <button
        v-for="(button, index) in moreButtonsReal"
        :key="index"
        type="button"
        class="van-action-sheet__item"
        :class="{ 'van-action-sheet__item--disabled': button.disabled }"
        @click="clickHandler(button, $event)"
      >
        <span class="van-action-sheet__name">{{ button.text }}</span>
      </button>
    </van-action-sheet>
  </div>
</template>
<script>
import { getButtonHander } from "../../../register/buttonRegister";
import control from "_platform/mixins/control";
export default dsf.component({
  name: "DsfMobileButtonBar",
  ctrlCaption: "按钮组",
  mixins: [control],
  inject: {
    $buttonOwner: {
      default: null,
    },
  },
  props: {
    size: {
      type: String,
      default: "",
    },
    align: {
      type: String,
      default: "center",
    },
    buttons: {
      type: Array,
      default() {
        return [];
      },
    },
    emitPrefix: {
      type: String,
      default: "",
    },
    renderButton: {
      type: Function,
      default: null,
    },
    maxNumber: {
      type: Number,
      default: 2,
    },
    moreButtonStyle: {
      type: String,
      default: "",
    },
    moreButtonText: {
      type: String,
      default: "更多",
    },
  },
  data() {
    return {
      moreOpen: false,
      actionSheetShow: false,
      flowButtons: [],
    };
  },
  computed: {
    owner() {
      return this.$buttonOwner;
    },
    actions() {
      return _.map(this.moreButtonsReal, (button) => {
        return {
          name: button.text,
          disabled: button.disabled,
        };
      });
    },
    showBar() {
      if (this.isDesign) {
        return true;
      } else {
        return this.validButtons.length > 0;
      }
    },
    validButtons() {
      return _.filter(this.buttons, (button) => {
        return this.showAndHide(button);
      });
    },
    moreButtonsReal() {
      return _.filter(this.validButtons, (button, index) => {
        return index >= this.maxNumber;
      });
    },
    showButtonsReal() {
      return _.filter(this.validButtons, (button, index) => {
        return index < this.maxNumber;
      });
    },
    getCss() {
      return {
        ...this.$getClass(),
        ["align-" + this.align]: true,
      };
    },
    iconButtons() {
      return _.filter(this.showButtonsReal, (button) => {
        return button.btnStyle == "icon-text";
      });
    },
    generalButtons() {
      return _.filter(this.showButtonsReal, (button) => {
        return button.btnStyle == "";
      });
    },
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    if (!this.isDesign) {
      this.buttons.forEach((button, index) => {
        // 按钮是流程发送按钮或者是批量发送按钮 配置了分环节发送 设置了不折叠后需要加载
        if (
          button.isLinkSend &&
          !button.isLinkSendFold &&
          (button.actionName === "system_flow_send" || button.actionName === "system_batch_flow_send")
        ) {
          this.addFlowBtns(button, this.buttons, index);
        }
      });
    }
  },
  methods: {
    getButtonStyle(button) {
      //button可能为空
      let style = {};
      if (this.align == "justify-horizontal") {
        if (this.showButtonsReal.length > 1) {
          let w = `calc(100% / ${this.showButtonsReal.length + (this.moreButtonsReal.length > 0 ? 1 : 0)} - ${this.pxToRem("5px")})`;
          style["min-width"] = w;
          style["max-width"] = w;
          style["width"] = w;
        } else {
          style["min-width"] = "100%";
        }
      }
      if (this.align == "justify-vertical") {
        style["min-width"] = "100%";
      }
      return style;
    },
    reloadData() {
      this.$forceUpdate();
    },
    showAndHide(button) {
      let result = true;
      if (!this.isDesign) {
        //非设计模式下运行表达式
        if (button.if) {
          let obj = {
            $vm: {
              value: this.$vm,
            },
          };
          let data = Object.create(this.local, obj);
          result = this.$eval(button.if, data) === false ? false : true;
        } else {
          result = true;
        }
      }
      if (button.visible === false) {
        result = false;
      }
      if (this.renderButton) {
        result = this.renderButton(button, result);
      }
      return result;
    },
    clickHandler(button, $event, response) {
      if (button.disabled || this.isDesign) {
        return;
      }
      if (button.secondConfirm) {
        dsf.layer.mobile
          .confirm(button.secondConfirmText || "确认提交？")
          .then(() => {
            this.buttonAction(button, $event, response);
          })
          .catch(() => {
            // button.execing = false;
          });
      } else {
        this.buttonAction(button, $event, response);
        $event.stopPropagation();
      }
    },
    async buttonAction(button, $event, response) {
      let owner = this.owner || this;
      //如果是系统按钮
      if (button.isSystem) {
        let handler = getButtonHander(button.actionName, "mobile");
        let params = _.map(button.params, (p) => {
          if (p.value) {
            let rfunction = /^\s*function(\s+\w*)?\(.*\)\s*{/g;
            let v = p.value.trim();
            if (rfunction.test(v)) {
              let code = [
                "var result= (" + v + ").call(this,data,button,owner,self);",
                "data=null;",
                "button=null;",
                "owner=null;",
                "self=null;",
                "return result;",
              ];
              let fn = new Function("data", "button", "owner", "self", code.join("\n"));
              let result = fn.call(this.$vm, this.local, button, owner, this);
              if (dsf.type(result) == "string") {
                result = this.$replace(result, this.local);
              }
              return result;
            } else {
              let v = this.$replace(p.value, this.local);
              if (v === "false") {
                v = false;
              } else if (v === "true") {
                v = true;
              }
              return v;
            }
          } else {
            return null;
          }
        });
        if (handler) {
          //系统按钮
          let param2 = {
            owner: owner,
            data: this.local,
            _self: this,
            targetButton: button,
            params: params,
          };
          if (response) {
            param2.response = response;
          }
          handler.call(this.$vm, param2, $event);
        }
      } else {
        //自定义按钮
        let eventName = (this.emitPrefix ? this.emitPrefix + "-" : "") + button.actionName;
        let hander = owner.$listeners[eventName];
        if (hander && dsf.isFunction(hander)) {
          hander(
            {
              owner: owner,
              data: this.local,
              targetButton: button,
              _self: this,
            },
            $event
          );
        }
      }
      this.moreOpen = false;
    },
    getIcon(button) {
      let icon = button.icon;
      if (icon && icon.startsWith("icon-")) {
        return icon.replace("icon-", "");
      }
      return icon;
    },
    //触发指定按钮的点击事件
    triggerButtonClick(button) {
      this.clickHandler(button);
    },
    addFlowBtns(button, btns, index) {
      if (!btns && this.flowButtons.length > 0) {
        button.popVisible = !button.popVisible;
        setTimeout(() => {
          document.addEventListener("click", this.hidePopVisible);
        }, 0);
        return;
      }
      let sWFID;
      dsf.recursionComponentProps(this.$page.$props, (data) => {
        if (data.isFormControl && data.caption === "wfinfo") {
          sWFID = data.chooseFlow && data.chooseFlow.id;
        }
      });
      if (sWFID) {
        let data = {};
        if (this.$vm.$dialog && this.$vm.$dialog.dialogArgs && this.$vm.$dialog.dialogArgs.data) {
          data = this.$vm.$dialog.dialogArgs.data[0];
        }
        this.$http
          .get("/wfr/getLines", {
            sWFID: sWFID,
            sPID: data.sPID || this.$route.query.sPID,
            sCurNodeID: data.sCurNodeID || this.$route.query.sCurNodeID || "",
            sCurLinkID: data.sCurLinkID || this.$route.query.sCurLinkID || "",
            sExInfos: "",
          })
          .then((res) => {
            if (res.data.success) {
              const data = res.data.data;
              const lines = data && data.SendParams && data.SendParams.Lines;
              if (lines) {
                lines.forEach((line) => {
                  if (line.Name === "审核通过") {
                    line.order = 0;
                  } else if (line.Name === "返回修改") {
                    line.order = 1;
                  } else if (line.Name === "审核不通过") {
                    line.order = 2;
                  }
                });
                lines.sort((a, b) => a.order - b.order);
                lines.forEach((line, i) => {
                  const newButton = Object.assign({}, button, {
                    caption: line.Name,
                    text: line.Name,
                    title: line.Name,
                    line: line,
                  });
                  if (!btns) {
                    this.flowButtons.push(newButton);
                    this.$set(button, "popVisible", true);
                    document.addEventListener("click", this.hidePopVisible);
                  } else {
                    btns.splice(index + i + 1, 0, newButton);
                  }
                });
                btns && btns.splice(btns.indexOf(button), 1);
              }
            }
          })
          .catch((res) => {});
      }
    },
    open() {
      this.actionSheetShow = true;
      this.$nextTick(() => {
        this.moreOpen = true;
      });
    },
    closed() {
      this.actionSheetShow = false;
      this.moreOpen = false;
    },
    onSelect() {},
  },
  beforeDestroy() {
    this.actionSheetShow = false;
    this.moreOpen = false;
  },
  destroyed() {},
});
</script>
