export default {
  name: "DsfMobileButton",
  functional: true,
  props: {
    noneLine: {
      type: Boolean,
      default: false
    },
    type: {
      //plain为次要按钮
      type: String,
      default() {
        return "";
      }
    },
    icon: {
      type: String,
      default() {
        return "";
      }
    },
    text: {
      type: String,
      default() {
        return "";
      }
    },
    title: {
      type: String,
      default() {
        return "";
      }
    },
    btnStyle: {
      type: String,
      default() {
        // 0:普通按钮 2：图标按钮 4:文字加图标 5：小图标
        return "";
      }
    },
    size: {
      type: String,
      default: ""
    }
  },
  render(h, context) {
    let c = context._c || h;
    let isIconText = false;
    let notText = false;
    let defaultSlot = context.slots().default;
    if (context.data.staticClass && context.data.staticClass.indexOf('ds-button--icon-text') >= 0) {
      isIconText = true;
    }
    if (context.props.btnStyle == 'icon-text') {
      isIconText = true;
      // if (dsf.type(context.props.btnStyle) == 'array' && context.data.class.indexOf("ds-button--icon-text") >= 0) {
      //   isIconText = true;
      // } else if (dsf.type(context.data.class) == 'object' && context.data.class['ds-button--icon-text']) {
      //   isIconText = true;
      // }
    }
    let text = ""
    if (defaultSlot && defaultSlot[0] && defaultSlot[0].text.trim()) {
      notText = false;
      text = defaultSlot[0].text
    } else if (context.props.text) {
      notText = false;
      text = context.props.text
    } else {
      notText = true;
    }
    //绘制插槽中的内容
    let scopedSlots = context._u([{
      key: "default",
      fn: (scope) => {
        return text;
      }
    }], false, null);
    if (!isIconText) {
      let cls = context.data.class;
      if (cls) {
        if (dsf.type(cls) == 'string') {
          cls = [cls]
        } else if (dsf.type(cls) == 'object') {
          cls = _.map(cls, (v, k) => { return k })
        }
      } else {
        cls = [];
      }

      return c('van-button', {
        attrs: {
          ...context.data.attrs,
        },
        props: {
          'icon': context.props.icon,
          'iconPrefix': 'iconfont icon',
          'plain': context.props.type == 'plain' ? true : false,
          'size': context.props.size ? context.props.size : 'normal',
          'tag': context.props.tag ? context.props.tag : 'button',
        },
        key: context.data.key,
        staticStyle: context.data.staticStyle,
        staticClass: context.data.staticClass,
        style: context.data.style,
        directives: context.data.directives,
        class: [...getCss(context), (notText ? 'no-text' : ''), (context.props.noneLine ? 'none-border' : ''), ...cls],
        on: context.listeners,
        scopedSlots: scopedSlots
      });
    } else {
      let iconEl = context._e();
      let spanEl = context._e();
      if (context.props.icon) {
        iconEl = c('i', {
          class: ['iconfont', 'icon-' + context.props.icon]
        })
      }
      if (text) {
        spanEl = c('span', {}, [text]);
      }
      let cls = context.data.class || [];
      return c("a", {
        attrs: {
          ...context.data.attrs,
        },
        key: context.data.key,
        staticStyle: context.data.staticStyle,
        staticClass: context.data.staticClass,
        style: context.data.style,
        on: context.listeners,
        directives: context.data.directives,
        class: [...cls, ...getCss(context), (notText ? 'no-text' : '')],
      }, [iconEl, spanEl])
    }
  }

}

function getCss(context) {
  let type = context.props.type;
  if (type == 'auxiliary') {
    type = 'plain'
  }
  return [
    'ds-button' + (context.props.btnStyle ? "--" + context.props.btnStyle : ''),
    'mobile',
    context.props.type ? type : '',
    context.props.size
  ]
}