<template>
  <div class="ds-control ds-form-item ds-system mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="setVal" v-bind="_self.$props"></DsfTextProxy> 
    </template>
    <template v-else>
      <van-field v-model="setVal" readonly :label="(showLabel&&label?label:'')" :label-width="getLabelWidth(true)" :required="required" input-align="right"></van-field>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl"
export default dsf.component({
  name: "DsfMobileDeletedStatus",
  mixins: [formControl],
  ctrlCaption: "删除状态",
  props: {
    caption: {
      type: String,
      default: dsf.config.kw.deleted,
    },
    label: {
      type: String,
      default: "删除状态",
    },
    simple: {
      type: Boolean,
      default: false,
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("system-meta-data", {
          code: dsf.config.kw.deleted,
          valueAttributes: [
            {
              name: "是否删除",
              code: dsf.config.kw.deleted,
              type: dsf.metadata.getDataType("string"),
              length: "2",
              defaultValue: null,
              unit: null,
            },
          ],
        });
      },
    },
  },
  computed: {
    setVal() {
      return Number(this.value) ? "是" : "否";
    },
  },
});
</script>
