<template>
  <div class="ds-control ds-form-item mobile ds-datachoice" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy
        v-model="value"
        v-bind="_self.$props"
        :show-label="false"
      ></DsfTextProxy>
      <slot name="error">
        <div v-if="errors.length > 0" class="ds-error-text">
          {{ errorsMsg }}
        </div>
      </slot>
    </template>
    <template v-else-if="readOnly && !isDesign">
      <van-field
        :label-width="getLabelWidth(true)"
        :label="(showLabel && label) || '数据选择'"
        :disabled="disabled"
        readonly
      >
        <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
      </van-field>
    </template>
    <template v-else>
      <van-field
        :disabled="disabled"
        :label-width="getLabelWidth(true)"
        :label="(showLabel&&label?label:'数据选择')"
        :required="!readOnly ? required : false"
        readonly
      >
        <template v-if="!readOnly && !disabled" #right-icon>
          <i class="iconfont icon-icon_tianjia" @click="popupShow = true"></i>
        </template>
      </van-field>
      <div v-if="!readOnly && !disabled" class="tags">
        <van-tag
          v-for="el in value"
          :key="el.value"
          :ref="el.value"
          closeable
          size="medium"
          @close="removeTag(el)"
        >
          {{ el.text }}
        </van-tag>
      </div>
    </template>
    <van-popup
      v-model="popupShow"
      class="van-popup_dataChoice"
      position="bottom"
      :close-on-popstate="true"
      :style="{ height: dialogHeight, width: dialogWidth }"
    >
      <div class="van-popup_top">
        <span class="btn" @click="popupShow = false">取消</span>
        {{ dialogTitle }}
        <span class="btn" @click="popupYes">确定</span>
      </div>
      <div class="main" :style="{ height: mainHeight }">
        <DsfMobileViewPart
          ref="popupVm"
          :path="$replace(dialogUrl, $vm || {})"
          show-model="full"
          v-bind="{ choiceParams: popupParams() }"
        ></DsfMobileViewPart>
      </div>
    </van-popup>
  </div>
</template>
<script>
import formControl from "_platform/mixins/formControl";
import dynamicComponent from "_platform/output/dynamicComponent";

export default dsf.component({
  name: "DsfMobileDataChoice",
  ctrlCaption: "数据选择",
  mixins: [formControl],
  provide() {
    return {
      $frame: this,
    };
  },
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "请输入",
    },
    dialogTitle: {
      type: String,
      default: "请选择",
    },
    dialogUrl: {
      type: String,
      default: "",
    },
    dialogWidth: {
      type: String,
      default: "100%",
    },
    dialogHeight: {
      type: String,
      default: "100%",
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
    // 值
    value: {
      type: [Array, Object],
      default() {
        return [];
      },
    },
    formatter: {
      type: String,
      default: "textValue^text",
    },
    maxTags: {
      type: Number,
      default: 0,
    },
    label: {
      type: String,
      default: "数据选择",
    },
  },
  data() {
    return {
      popupShow: false,
    };
  },
  computed: {
    mainHeight() {
      return `calc(${this.dialogHeight} - 0.8rem)`;
    },
    selectValue: {
      get() {
        let v = this.value;
        if (dsf.isDef(v)) {
          return v.map((s) => s.value);
        } else {
          return [];
        }
      },
      set(v) {
        let value = [];
        _.each(v, (el) => {
          if (dsf.type(el) == "string") {
            value.push({
              text: el,
              value: el,
            });
          }
          if (dsf.hasOwn(el, "text") && dsf.hasOwn(el, "value")) {
            value.push(el);
          }
        });
        this.emitValueChange(value);
      },
    },
    options() {
      if (!this.remoteInputing) {
        return this.items;
      } else {
        return this.remoteItems;
      }
    },
  },
  mounted() {},
  watch: {
    value: {
      handler(v) {
        let nv;
        if (dsf.isUnDef(v)) {
          nv = [];
        }
        if (dsf.type(v) === "string" && !v) {
          nv = [];
        }
        if (!$.isArray(v)) {
          nv = [v];
        }
        if (nv) {
          this.emitValueChange(nv);
        } else {
          this.$nextTick(() => {
            this.$dispatch("change");
            this.$nextTick(() => {
              this.emitFormValidate(this.value);
            });
          });
        }
      },
    },
    inputStr: {
      handler(v) {
        if (v) {
          this.popupHide = "";
          this.$nextTick(() => {
            this.popperWidthChange();
          });
        } else {
          this.popupHide = "hide";
        }
      },
    },
  },
  methods: {
    popupParams() {
      return {
        value: this.value,
        maxTags: this.maxTags,
      };
    },
    popupYes() {
      let _this = this;
      let vm = this.$refs.popupVm.$refs.view;
      if (vm && vm.yes && dsf.isFunction(vm.yes)) {
        let returnValue = vm.yes();
        if (!returnValue) {
          return false;
        } else {
          let result = _this.mapData(returnValue);
          let list = _this.value || [];
          _.each(result, (tag, i) => {
            if (!this.$refs[tag.value]) {
              if (
                this.maxTags &&
                this.maxTags > 0 &&
                list.length >= this.maxTags
              ) {
                dsf.layer.message(
                  "已超出数量上限【" + this.maxTags + "】",
                  false
                );
                return;
              }
              if (dsf.type(tag) == "string") {
                tag = {
                  text: tag,
                  value: tag,
                };
              }
              let index = _.findIndex(list, (el) => tag.value === el.value);
              if (index < 0) {
                list.push(tag);
              }
            }
          });
          this.emitValueChange(list);
          _this.$dispatch("selected", returnValue);
        }
        this.popupShow = false;
      }
    },
    getChoiceParams() {
      return {
        value: this.value,
      };
    },
    mapData(result) {
      if (!this.mapHandler) {
        let arr = dsf.type(result) == "array" ? result : [result];
        //默认取字段名称name、_id作为组件的text、value;
        let list = [];
        list.splice(0, list.length);
        for (let i = 0; i < arr.length; i++) {
          let r = arr[i];
          let value = this.autoMapDataText(r);
          if (value) {
            list.push(value);
          }
        }
        return list;
        // this.emitValueChange(list);
      }
    },
    autoMapDataText(data) {
      let rmetakey = /(\w+)\.(\w+)/g;
      //默认检测text 和 name作为text属性
      //默认检测_id 和 value作为value属性
      let commonTextKeys = [
        "text",
        "_text",
        "name",
        "_name",
        "label",
        "_label",
      ];
      let commonValueKeys = ["value", "_value", "id", "_id", "key", "_key"];
      let result = null;
      let text, value;
      for (let k in data) {
        let key = k;
        rmetakey.lastIndex = 0;
        let match = rmetakey.exec(k);
        if (match && match[2]) {
          key = match[2];
        }
        if (dsf.isUnDef(text) && commonTextKeys.indexOf(key) >= 0) {
          text = data[k];
        }
        if (dsf.isUnDef(value) && commonValueKeys.indexOf(key) >= 0) {
          value = data[k];
        }
      }
      if (dsf.isDef(text) || dsf.isDef(value)) {
        result = {
          text: text,
          value: value,
        };
      }
      return result;
    },
    removeTag(tag) {
      let index = _.findIndex(this.value, (el) => {
        return el.value === tag.value;
      });
      if (index >= 0) {
        this.value.splice(index, 1);
        this.emitValueChange(this.value);
      }
    },
    querySearch(queryString, cb) {
      if (queryString) {
        cb([
          { text: "1", value: "1" },
          { text: "2", value: "2" },
          { text: "3", value: "3" },
        ]);
      }
    },
    popperWidthChange() {
      if (this.$refs.input) {
        let popup = this.$refs.input.$children[1];
        let elem = this.$el.querySelector(".ds-datachoice-input");
        let rect = elem.getClientRects();
        rect = rect[0] || null;
        if (rect) {
          //强制赋予最小宽度元素不跟随文本框变小而变小
          popup.$el.style.minWidth = rect.width + "px";
        }
      }
    },
    resize() {
      this.popperWidthChange();
    },
  },
});
</script>
