<template>
  <div class="dsf-calendar">
    <div class="dsf-calendar-header">
      <span class="dsf-calendar-header-title">
        <template v-if="'date' === type">选择日期</template>
        <template v-else-if="'year' === type">选择年份</template>
        <template v-else-if="'month' === type">选择月份</template>
        <template v-else-if="'datetime_s' === type || 'datetime' === type">选择时间</template>
      </span>
      <div class="dsf-calendar-header-current" @click="chooseActive">
        <span v-if="currentView == 'day'">{{ dayView.activeMonth && dayView.activeMonth.key || '' }}</span>
        <span v-else-if="currentView == 'month'">{{ monthView && monthView.activeText || '' }}</span>
        <span v-else-if="currentView == 'year'">{{ yearView && yearView.activeText || '' }}</span>
      </div>
      <i class="dsf-calendar-header-close iconfont icon-guanbi2" @click="$emit('cancel')"></i>
    </div>
    <div class="dsf-calendar-body">
      <div v-if="'datetime_s' === type || 'datetime' === type" class="dsf-calendar-value">
        <div @click="chooseTime(0)">{{ valueText[0][0] }}<span>{{ valueText[0][1] }}</span></div>
        <template v-if="valueText.length == 2">
          <span>至</span>
          <div @click="chooseTime(1)">{{ valueText[1][0] }}<span>{{ valueText[1][1] }}</span></div>
        </template>
      </div>
      <calendar-year-box
        v-show="currentView == 'year'"
        v-model="value"
        :box-height="boxHeight"
        :type="type"
        :min-time="minTime"
        :max-time="maxTime"
        :multiple="multiple"
        :range="range"
        @change="yearChang"
      ></calendar-year-box>
      <calendar-month-box
        v-if="type != 'year'"
        v-show="currentView == 'month'"
        v-model="value"
        :box-height="boxHeight"
        :type="type"
        :min-time="minTime"
        :max-time="maxTime"
        :multiple="multiple"
        :range="range"
        @change="monthChang"
      ></calendar-month-box>
      <calendar-day-box
        v-if="type != 'year' && type != 'month'"
        v-show="currentView == 'day'"
        v-model="value"
        :box-height="boxHeight"
        :row-height="boxHeight / 5 - 8"
        :type="type"
        :min-time="minTime"
        :max-time="maxTime"
        :multiple="multiple"
        :range="range"
      ></calendar-day-box>
    </div>
    <div class="dsf-calendar-footer">
      <van-goods-action>
        <van-goods-action-icon
          v-if="type != currentView && currentView != 'day'"
          icon="revoke"
          text="返回"
          @click="backView"
        />
        <van-goods-action-icon
          v-if="currentView != 'year'"
          icon="share-o"
          text="跳转"
          @click="chooseActive"
        />
        <van-goods-action-icon
          v-show="now.getTime() >= minTime.MTime && now.getTime() <= maxTime.MTime"
          icon="location-o"
          text="今天"
          @click="doLocation()"
        />
        <van-goods-action-button v-if="clearable" type="info" text="重 置" @click="onReset" />
        <van-goods-action-button type="primary" text="确 定" @click="onConfirm" />
      </van-goods-action>
    </div>
    <dsf-calendar-time-box
      v-if="type == 'datetime_s' || type == 'datetime'"
      :type="type"
      :min-time="minTime.time"
      :max-time="maxTime.time"
    ></dsf-calendar-time-box>
  </div>
</template>

<script>
import { dateSplit, getValuesText } from './items/calendarUtils';
import CalendarDayBox from "./items/calendarDayBox";
import CalendarYearBox from "./items/calendarYearBox";
import CalendarMonthBox from "./items/calendarMonthBox";

export default {
  name: "DsfCalendar",
  components: {
    CalendarDayBox,
    CalendarYearBox,
    CalendarMonthBox
  },
  provide() {
    return {
      $calendar: this
    };
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    // 初始值
    defaultValue: {
      type: [Date, Array],
      required: false
    },
    // 选中方式
    type: {
      type: String,
      default: 'date'
    },
    // 是否多选
    multiple: {
      type: Boolean,
      default: false
    },
    // 选择区间
    range: {
      type: Boolean,
      default: false
    },
    // 可选择的最小日期
    minDate: {
      type: Date,
      required: false
    },
    // 可选择的最大日期
    maxDate: {
      type: Date,
      required: false
    },
    // 默认视图
    defaultView: {
      type: String,
      default: 'day'
    },
    // 视图默认显示时间
    defaultViewDate: {
      type: Date,
      required: false
    },
    // 是否显示清除按钮
    clearable: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      now: new Date(dsf.date.toDay()),
      currentView: 'day',
      yearView: {},
      monthView: {},
      dayView: {},
      timeView: {},
      // 容器高度
      boxHeight: 360,
      // 选中值
      value: [],
      // 范围选择时，只选了单个时间
      tempValue: [],
    }
  },
  computed: {
    minTime() {
      let minDate = this.minDate;
      // 限制可选最小时间1801年
      if (!minDate || minDate < new Date('1801/01/01')) {
        minDate = new Date('1801/01/01');
      }
      // if (!this.minDate) {
      //   return { Y: -Infinity, MTime: -Infinity, DTime: -Infinity, time: -Infinity };
      // }
      return dateSplit(minDate);
    },
    maxTime() {
      let maxDate = this.maxDate;
      // 限制可选最大时间2500年
      if (!maxDate || maxDate > new Date('2500/12/31')) {
        maxDate = new Date('2500/12/31');
      }
      // if (!this.maxDate) {
      //   return { Y: Infinity, MTime: Infinity, DTime: Infinity, time: Infinity };
      // }
      return dateSplit(maxDate, true);
    },
    valueText() {
      let res = [];
      if (this.range) {
        res.push(getValuesText(this.tempValue.length && this.tempValue[0], this.type));
        res.push(getValuesText(this.tempValue.length == 2 ? this.tempValue[1] : null, this.type));
      } else {
        res.push(getValuesText(this.value.length && this.value[0], this.type));
      }
      return res;
    }
  },
  watch: {
    show: {
      immediate: true,
      handler(to) {
        if (to) {
          if (this.type == 'year') {
            this.currentView = 'year';
          } else if (this.type == 'month') {
            this.currentView = this.defaultView == 'year' ? 'year' : 'month';
          } else {
            this.currentView = this.defaultView;
          }
        }
      }
    },
    type (to) {
      if (to == 'year') {
        this.currentView = 'year';
      } else if (to == 'month') {
        this.currentView = 'month';
      } else {
        this.currentView = 'day';
      }
    }
  },
  created() {
    this.initValue(this.defaultValue);
  },
  mounted() {
  },
  methods: {
    initValue(val) {
      if (!val) {
        this.value = [];
        return;
      }
      if (this.multiple && this.range) {
        // todo 多选范围
      } else if (!this.multiple && this.range) {
        // 单选范围
        if (Array.isArray(val) && val.length == 2) {
          this.value = [[val[0], val[1]]];
          this.tempValue = [val[0], val[1]];
        }
      } else if (this.multiple) {
        // todo 多选
        if (Array.isArray(val)) {
          this.value = val;
        } else {
          this.value = [val];
        }
      } else {
        // 单选
        if (Array.isArray(val)) {
          this.value = [val[0]];
        } else {
          this.value = [val];
        }
      }
    },
    // 点击确定按钮
    onConfirm() {
      let res = this.value;
      if (this.multiple) {
        // todo 多选
      } else if (this.value.length) {
        // 单选
        res = res.length ? res[0] : null;
      }
      this.$emit('change', res);
      this.$emit('confirm', res);
    },
    // 重置
    onReset() {
      this.value = [];
      this.tempValue = [];
    },
    // 选择时分秒
    chooseTime(index) {
      if ((this.type != 'datetime_s' && this.type != 'datetime') || !this.value.length) return;
      if (this.range) {
        this.$timeView.open(this.value[0][index]).then(str => {
          let d = new Date(dsf.date.format(this.value[0][index], 'yyyy/mm/dd') + ' ' + str);
          this.$set(this.value[0], index, d);
          this.$set(this.tempValue, index, d);
        }).catch(err => {});
      } else {
        this.$timeView.open(this.value[0]).then(str => {
          let d = new Date(dsf.date.format(this.value[0], 'yyyy/mm/dd') + ' ' + str);
          this.$set(this.value, 0, d);
        });
      }
    },
    // 定位到date这个时间
    doLocation(date) {
      this.now = new Date(dsf.date.toDay());
      date = date || this.now;
      if (this.currentView == 'day') {
        this.$dayView.doLocation(date);
      } else if (this.currentView == 'month') {
        this.$monthView.doLocation(date);
      } else if (this.currentView == 'year') {
        this.$yearView.doLocation(date);
      }
    },

    formatter(type, val) {
      if (type === 'month') {
        return `${+val}月`;
      } else if (type === 'year') {
        return `${val}年`;
      }
      return val;
    },
    // 定位年月相关
    chooseActive() {
      if (this.currentView == 'day') {
        this.currentView = 'month';
      } else if (this.currentView == 'month') {
        this.currentView = 'year';
      }
    },
    // 返回
    backView() {
      if (this.currentView == 'year') {
        this.currentView = 'month';
      } else if (this.currentView == 'month') {
        this.currentView = 'day';
      }
    },
    // 月份变化
    monthChang(year, month) {
      this.currentView = 'day';
      this.$nextTick(() => {
        this.doLocation(new Date(`${year}/${month}/1`));
      });
    },
    // 年变化
    yearChang(year) {
      this.currentView = 'month';
      let month = this.$dayView?.activeMonth.month || (this.now.getMonth() + 1);
      this.$nextTick(() => {
        this.doLocation(new Date(`${year}/${month}/1`));
      });
    }
  }
}
</script>