//注册平台级api
import "../api";
//注册pc端按钮
import "./sysButton";

import bhc from "./bhc";
//注入平台全局过滤器
import filters from "../filters";
//注入平台全局自定义指令
import directives from "../directives";

import mobileDirectives from "./directives";
//用于加载配置完成的页面,预览页面
import output from "../output";

function install(Vue) {
  Vue.use(bhc);
  Vue.use(filters);
  Vue.use(directives);
  Vue.use(output);
  Vue.use(mobileDirectives);
}

export default install;

export { install, bhc, filters, directives, output, mobileDirectives };
