//引入组件
import {
  Form,
  Field,
  Popup,
  Dialog,
  Image,
  Grid,
  GridItem,
  ImagePreview,
  Loading,
  Picker,
  Uploader,
  Icon,
  Col,
  Row,
  PullRefresh,
  List,
  Tabbar,
  TabbarItem,
  NavBar,
  Button,
  ActionSheet,
  Empty,
  Checkbox,
  CheckboxGroup,
  RadioGroup,
  Radio,
  Cell,
  CellGroup,
  Search,
  Tag,
  Swipe,
  SwipeItem,
  Lazyload,
  Calendar,
  DatetimePicker,
  Tabs,
  Tab,
  Toast,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton
} from 'vant';

import '../directives';

import { DsfMobileDialog } from './dialog';
import { VanMultiplePicker, DsfMobileLoading, DsfMobileButton, DsfCalendar, DsfCalendarTimeBox } from '../common';
import { DsfMobilePage } from "./page";
import { DsfMobileButtonBar } from './button';
import { DsfDemoBrowserNavBar, DsfMobileButtonNav, DsfMobileBanner, DsfMobileNavMenu } from './nav';
import { DsfMobileHtml } from './html';
import { DsfMobileViewPart } from './layout';
import { DsfMobileDataGrid } from "./list";
import { DsfMobileTextBox, DsfMobileTextArea, DsfMobileAutoComplete, DsfMobileUeditor } from "./input";
import { DsfMobileRadioBox, DsfMobileCheckBox, DsfMobileOuaSelect, DsfMobileSelect, DsfMobileDataChoice } from "./items";
import { DsfMobileRow, DsfMobileCol } from './layout';
import { DsfMobileTreeInfo, DsfMobileTreeSelector, DsfMobileCheckNode } from './tree';
import { DsfMobileUpdateTime, DsfMobileCreateTime, DsfMobileUpdateUserName, DsfMobileCreateUserName, DsfMobileCreateUserId, DsfMobileUpdateUserId, DsfMobileOrder, DsfMobileDeletedStatus } from './system';
import { DsfMobileUploadFile, DsfMobileUploadImg, DsfMobileUploadPhoto } from './upload';
import { DsfMobileDatePicker, DsfMobileDateIntervalPicker, DsfMobileTimePicker } from './date';
import { DsfMobileSubTable } from './sub';
import { DsfMobileFlowInfo, DsfMobileFlowProcess, DsfMobileDisposeIdea, DsfMobileChooseSendNode, DsfMobileChooseUsers, DsfMobileFormCommentBox, DsfMobileReChooseUsers } from './flow';
// 设计类
import { DsfMobileUploadDesignImg } from './design';

//引入移动端端公共弹层
import layer from "../layer/layer";
dsf.mix(dsf.layer, { 'mobile': layer });

//引入表单
import "_platform/mixins/form";



function install(Vue) {
  //为vue对象注入表达式替换
  Vue.prototype.$replace = function (express, varible) {
    return dsf.express.replace.call(this, express, varible);
  };
  //为vue对象注入表达式执行
  Vue.prototype.$eval = function (express, varible) {
    return dsf.express.eval.call(this, express, varible);
  };
  //为vue对象注入打开新窗口函数
  Vue.prototype.$openWindow = function () {
    let args = Array.prototype.slice.call(arguments, 0);
    let vm = this.$vm ? this.$vm : this;
    return dsf.layer.openWindow.apply(vm, args);
  };
  Vue.prototype.$openDialog = function () {
    let args = Array.prototype.slice.call(arguments, 0);
    let vm = this.$vm ? this.$vm : this;
    return dsf.layer.openDialog.apply(vm, args);
  };

  Vue.prototype.pxToRem = function (v) {
    return dsf.pxToRem(v);
  };

  Vue.use(Form);
  Vue.use(Field);
  Vue.use(Popup);
  Vue.use(Dialog);
  Vue.use(Image);
  Vue.use(Grid);
  Vue.use(GridItem);
  Vue.use(ImagePreview);
  Vue.use(Loading);
  Vue.use(Picker);
  Vue.use(Uploader);
  Vue.use(Icon);
  Vue.use(Col);
  Vue.use(Row);
  Vue.use(PullRefresh);
  Vue.use(List);
  Vue.use(Tabbar);
  Vue.use(TabbarItem);
  Vue.use(NavBar);
  Vue.use(Button);
  Vue.use(ActionSheet);
  Vue.use(Empty);
  Vue.use(Checkbox);
  Vue.use(CheckboxGroup);
  Vue.use(RadioGroup);
  Vue.use(Radio);
  Vue.use(Cell);
  Vue.use(CellGroup);
  Vue.use(Search);
  Vue.use(Tag);
  Vue.use(Swipe);
  Vue.use(SwipeItem);
  Vue.use(Lazyload);
  Vue.use(Calendar);
  Vue.use(DatetimePicker);
  Vue.use(Tabs);
  Vue.use(Tab);
  Vue.use(Toast);
  Vue.use(GoodsAction);
  Vue.use(GoodsActionButton);
  Vue.use(GoodsActionIcon);

  Vue.component(DsfMobileDialog.name, DsfMobileDialog);
  Vue.component(DsfMobilePage.name, DsfMobilePage);
  Vue.component(DsfMobileButtonBar.name, DsfMobileButtonBar);
  Vue.component(DsfMobileRow.name, DsfMobileRow);
  Vue.component(DsfMobileCol.name, DsfMobileCol);
  Vue.component(DsfMobileTextBox.name, DsfMobileTextBox);
  Vue.component(DsfMobileTextArea.name, DsfMobileTextArea);
  Vue.component(DsfMobileAutoComplete.name, DsfMobileAutoComplete);
  Vue.component(DsfMobileUeditor.name, DsfMobileUeditor);
  Vue.component(DsfMobileRadioBox.name, DsfMobileRadioBox);
  Vue.component(DsfMobileCheckBox.name, DsfMobileCheckBox);
  Vue.component(DsfMobileSelect.name, DsfMobileSelect);
  Vue.component(DsfMobileDataGrid.name, DsfMobileDataGrid);
  Vue.component(DsfMobileLoading.name, DsfMobileLoading);
  Vue.component(VanMultiplePicker.name, VanMultiplePicker);
  Vue.component(DsfMobileButton.name, DsfMobileButton);
  Vue.component(DsfMobileButtonNav.name, DsfMobileButtonNav);
  Vue.component(DsfCalendar.name, DsfCalendar);
  Vue.component(DsfCalendarTimeBox.name, DsfCalendarTimeBox);
  Vue.component(DsfMobileHtml.name, DsfMobileHtml);
  Vue.component(DsfMobileTreeInfo.name, DsfMobileTreeInfo);
  Vue.component(DsfDemoBrowserNavBar.name, DsfDemoBrowserNavBar);
  Vue.component(DsfMobileBanner.name, DsfMobileBanner);
  Vue.component(DsfMobileNavMenu.name, DsfMobileNavMenu);
  Vue.component(DsfMobileOuaSelect.name, DsfMobileOuaSelect);
  Vue.component(DsfMobileUpdateTime.name, DsfMobileUpdateTime);
  Vue.component(DsfMobileCreateTime.name, DsfMobileCreateTime);
  Vue.component(DsfMobileUpdateUserName.name, DsfMobileUpdateUserName);
  Vue.component(DsfMobileCreateUserName.name, DsfMobileCreateUserName);
  Vue.component(DsfMobileCreateUserId.name, DsfMobileCreateUserId);
  Vue.component(DsfMobileUpdateUserId.name, DsfMobileUpdateUserId);
  Vue.component(DsfMobileOrder.name, DsfMobileOrder);
  Vue.component(DsfMobileDeletedStatus.name, DsfMobileDeletedStatus);
  Vue.component(DsfMobileUploadFile.name, DsfMobileUploadFile);
  Vue.component(DsfMobileUploadImg.name, DsfMobileUploadImg);
  Vue.component(DsfMobileUploadPhoto.name, DsfMobileUploadPhoto);
  Vue.component(DsfMobileViewPart.name, DsfMobileViewPart);
  Vue.component(DsfMobileTreeSelector.name, DsfMobileTreeSelector);
  Vue.component(DsfMobileCheckNode.name, DsfMobileCheckNode);
  Vue.component(DsfMobileDataChoice.name, DsfMobileDataChoice);
  Vue.component(DsfMobileDatePicker.name, DsfMobileDatePicker);
  Vue.component(DsfMobileDateIntervalPicker.name, DsfMobileDateIntervalPicker);
  Vue.component(DsfMobileTimePicker.name, DsfMobileTimePicker);
  Vue.component(DsfMobileFlowInfo.name, DsfMobileFlowInfo);
  Vue.component(DsfMobileFlowProcess.name, DsfMobileFlowProcess);
  Vue.component(DsfMobileDisposeIdea.name, DsfMobileDisposeIdea);
  Vue.component(DsfMobileChooseSendNode.name, DsfMobileChooseSendNode);
  Vue.component(DsfMobileChooseUsers.name, DsfMobileChooseUsers);
  Vue.component(DsfMobileReChooseUsers.name, DsfMobileReChooseUsers);
  Vue.component(DsfMobileFormCommentBox.name, DsfMobileFormCommentBox);
  Vue.component(DsfMobileUploadDesignImg.name, DsfMobileUploadDesignImg);
  Vue.component(DsfMobileSubTable.name,DsfMobileSubTable);
}

if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
export {
  install,
  DsfMobilePage,
  DsfMobileTextBox,
  DsfDemoBrowserNavBar,
  DsfMobileBanner,
  DsfMobileButtonBar,
  DsfMobileOuaSelect,
  DsfMobileViewPart,
  DsfMobileTreeSelector,
  DsfMobileCheckNode,
  DsfMobileDataChoice,
  DsfMobileUploadPhoto
};
export default {
  install
};