<template>
  <div class="dsf-calendar-main" :style="{height: boxHeight + 'px'}">
    <div ref="box" class="dsf-calendar-main__calendar year" @scroll="onScroll">
      <div
        v-for="index in (showYearE - showYearS + 1)"
        :key="(year = showYearS + index - 1) > 0 ? year : (year = year - 1)"
        class="dsf-calendar-main__calendar-item year"
        :class="cellClass(year)"
        @click="cellClick(showYearS + index - 1)">
        <span>{{ year }}</span>年
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CalendarYearBox',
  inject: ['$calendar'],
  props: {
    boxHeight: {
      type: Number,
      required: true
    },
    // 选中方式
    type: {
      type: String,
      required: true
    },
    minTime: {
      type: Object,
      required: true
    },
    maxTime: {
      type: Object,
      required: true
    },
    value: {
      type: Array,
      required: true
    },
    // 是否多选
    multiple: {
      type: Boolean,
      required: true
    },
    // 范围选择
    range: {
      type: Boolean,
      required: false
    }
  },
  data() {
    return {
      // 显示出的月份
      showYearS: 0,
      showYearE: 0,
      activeYearS: 0,
      activeYearE: 0
    }
  },
  computed: {
    activeText() {
      if (this.activeYearS == this.activeYearE) {
        return this.activeYearS + '年';
      }
      return this.activeYearS + '年 - ' + this.activeYearE + '年';
    },
    showYearMin() {
      let y = this.minTime.Y;
      return y == -Infinity ? -Infinity : (y % 4 - 7 + y);
    },
    showYearMax() {
      let y = this.maxTime.Y;
      return y == Infinity ? Infinity : (8 - y % 4 + y);
    },
  },
  watch: {
    '$calendar.currentView'(to) {
      if (to == 'year') {
        let year = this.$calendar.now.getFullYear();
        if (this.$calendar.$monthView) {
          year = Math.floor((this.$calendar.$monthView.activeYearS + this.$calendar.$monthView.activeYearE) / 2);
        }
        this.reset(year);
      }
    }
  },
  created() {
    this.$calendar.yearView = this.$calendar.$yearView = this;
  },
  mounted() {
    this.$box = this.$refs.box;
    if (this.$calendar.currentView == 'year') {
      this.reset(this.$calendar.defaultViewDate?.getFullYear() || this.$calendar.now.getFullYear());
    }
  },
  methods: {
    cellClass(year) {
      let y = this.$calendar.now.getFullYear();
      let res = {};
      if (this.minTime.Y > year || this.maxTime.Y < year) {
        res.disabled = true;
      } else if (y == year) {
        res.toDay = true;
      }
      if (this.range && this.multiple) {
        // todo 范围多选
      } else if (this.range && !this.multiple) {
        // 范围单选
        if (this.$calendar.tempValue.length > 0) {
          let vs = this.$calendar.tempValue[0].getFullYear();
          if (vs == year) {
            res.activeS = true;
          }
          if (this.$calendar.tempValue.length == 2) {
            let ve = this.$calendar.tempValue[1].getFullYear();
            if (ve == year) {
              res.activeE = true;
            }
            if (vs < year && ve > year) {
              res.activeC = true;
            }
          }
        }
      } else if (this.multiple) {
        // todo 多选
      } else if (this.value.length) {
        // 单选
        (year == this.value[0].getFullYear()) && (res.active = true);
      }
      return res;
    },
    cellClick(year) {
      year = year < 1 ? year - 1 : year;
      if (this.type != 'year') {
        this.$emit('change', year)
      } else if (this.range && this.multiple) {
        // 范围多选
        this.formatValueByCheckboxRange(year);
      } else if (this.range && !this.multiple) {
        // 范围单选
        this.formatValueByRadioRange(year);
      } else if (this.multiple) {
        // 多选
        this.formatValueByCheckbox(year);
      } else {
        // 单选
        this.formatValueByRadio(year);
      }
    },
    // todo 多选
    formatValueByCheckbox(year) {
      let index = this.value.findIndex(v => {
        return v.getFullYear() == year;
      });
      if (index == -1) {
        this.$calendar.value.push(new Date(`${year}/1/1`));
      } else {
        this.$calendar.value.splice(index, 1);
      }
    },
    // 单选
    formatValueByRadio(year) {
      this.$calendar.value = [new Date(`${year}/1/1`)];
    },
    // todo 范围多选
    formatValueByCheckboxRange(year) {

    },
    // todo 范围单选
    formatValueByRadioRange(year) {
      if (!this.$calendar.tempValue.length || this.$calendar.tempValue.length == 2) {
        this.$calendar.tempValue = [new Date(`${year}/1/1`)];
        this.$calendar.value = [this.$calendar.tempValue[0]];
      } else {
        let oldTime = this.$calendar.tempValue[0];
        let oldYear = oldTime.getFullYear();
        if (oldYear > year) {
          this.$calendar.tempValue = [new Date(`${year}/1/1`)];
        } else {
          oldYear = year;
        }
        this.$calendar.tempValue.push(new Date(`${oldYear}/12/31 23:59:59`));
        this.$calendar.value = [[...this.$calendar.tempValue]];
      }
    },
    onScroll() {
      let offsetHeight = this.$box.offsetHeight;
      let scrollHeight = this.$box.scrollHeight;
      let scrollTop = this.$box.scrollTop;
      // 滚动条高度小于3屏高触发
      if (this.showYearS >= this.showYearMin && scrollTop < offsetHeight * 3) {
        let showYearS = this.showYearS - 32;
        this.showYearS = showYearS = showYearS < this.showYearMin ? this.showYearMin : showYearS;
      }
      // 滚动条离底部距离小于3屏高触发
      else if (this.showYearE <= this.showYearMax && scrollHeight - offsetHeight - scrollTop < offsetHeight * 3) {
        let showYearE = this.showYearE + 32;
        this.showYearE = showYearE > this.showYearMax ? this.showYearMax : showYearE;
      }
      this.updateActive(scrollHeight, scrollTop, offsetHeight);
    },
    // 定位到date这个时间
    doLocation(date) {
      date = date || this.$calendar.now;
      let year = date.getFullYear();
      this.reset(year);
    },
    reset(year) {
      let itemHeight = this.$box.children[0].offsetHeight;
      let h = itemHeight ? Math.floor(this.boxHeight * 3.5 / itemHeight) * 4 : 72;
      let n = (year - 1) % 4;
      let showYearS = year - n - h;
      let showYearE = year - n + h;
      this.showYearS = showYearS = showYearS < this.showYearMin ? this.showYearMin : showYearS;
      this.showYearE = showYearE = showYearE > this.showYearMax ? this.showYearMax : showYearE;
      this.$nextTick(() => {
        itemHeight = this.$box.children[0].offsetHeight;
        this.$box.scrollTo(0, itemHeight * (.5 + Math.floor((year - showYearS) / 4)) - this.boxHeight / 2);
        this.$nextTick(() => {
          let offsetHeight = this.$box.offsetHeight;
          let scrollHeight = this.$box.scrollHeight;
          let scrollTop = this.$box.scrollTop;
          this.updateActive(scrollHeight, scrollTop, offsetHeight);
        });
      });
    },
    updateActive(scrollHeight, scrollTop, offsetHeight) {
      let itemHeight = this.$box.children[0].offsetHeight;
      let activeYearS = Math.floor(scrollTop / itemHeight + .4) * 4 + this.showYearS;
      let activeYearE = Math.floor((scrollTop + offsetHeight) / itemHeight + .6) * 4 + this.showYearS - 1;
      this.activeYearS = activeYearS < this.showYearS ? this.showYearS : activeYearS;
      this.activeYearE = activeYearE > this.showYearE ? this.showYearE : activeYearE;
    }
  }
}
</script>