<template>
  <div class="ds-control ds-form-item ds-treeinfo mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <van-field
        v-if="!readOnly"
        v-model="inputValue.pid"
        type="text"
        :required="required"
        input-align="right"
        :disabled="disabled"
        :label-width="getLabelWidth(true)"
        :label="showLabel && label ? label : ''"
        @input="inputChangeHandler"
        @blur="inputBlurHandler"
      ></van-field>
      <van-field
        v-if="readOnly"
        v-model="inputValue.pid"
        type="text"
        :disabled="disabled"
        :required="required"
        input-align="right"
        :label-width="getLabelWidth(true)"
        :label="showLabel && label ? label : ''"
      ></van-field>
    </template>
  </div>
</template>
<script>
import formControl from "_platform/mixins/formControl";

export default dsf.component({
  name: "DsfMobileTreeInfo",
  mixins: [formControl],
  ctrlCaption: "树结构",
  props: {
    caption: {
      type: String,
      default: "treeinfo",
    },
    label: {
      type: String,
      default: "树结构",
    },
    pidExpress: {
      type: String,
      default: "",
    },
    iconExpress: {
      type: String,
      default: "",
    },
    iconMap: {
      type: Array,
      default: () => [],
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    value: {
      type: [String, Object],
      default() {
        return {
          pid: "",
          icon: "",
        };
      },
    },
    visible: {
      type: Boolean,
      default: false,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("tree-info-meta-data");
      },
    },
  },
  data() {
    return {};
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(value) {
        if (value != undefined) {
          if (typeof value == "string") {
            this.value.pid = value;
          } else {
            this.value = {
              pid: value.pid,
              icon: value.icon,
            };
          }
        }
        this.emitValueChange(this.value);
      },
    },
  },
  created() {
    // 监听表单数据提交前 console.log("this", this);
    if (this.pidExpress) {
      this.value.pid = this.pidExpress.getValueString(",");
    }
    if (this.iconExpress) {
      this.value.icon = this.iconExpress.getValueString(",");
    }
  },
  methods: {
    inputChangeHandler() {
      this.$dispatch("change");
      this.emitValueChange(this.value);
    },
    inputBlurHandler() {
      this.$dispatch("blur");
      this.emitValueChange(this.value);
    },
  },
  design: {
    layout: {
      //不做命名验证
      nameValidate: false,
    },
  },
});
</script>
