<template>
  <van-dialog v-model="show" :show-confirm-button="false" :show-cancel-button="false" class="work_flow-linksDialog" title="请选择要发送的环节">
    <van-picker ref="picker" :columns="data" value-key="Name" title="标题" />
    <div class="van-hairline--top van-dialog__footer">
      <van-button type="default" size="large" class="van-dialog__cancel" @click="cancel">取消</van-button>
      <van-button v-if="showback" type="default" size="large" class="van-dialog__confirm van-hairline--left" @click="goBack">返回</van-button>
      <van-button type="default" size="large" class="van-dialog__confirm van-hairline--left" @click="confirm">确认</van-button>
    </div>
  </van-dialog>
</template>

<script>
export default {
  name: "ChooseLinksDialog",
  data() {
    return {
      data: {},
      show: false,
      title: null,
      showback: false,
      resolve: () => {},
      back: () => {},
    };
  },
  methods: {
    showFun(data, resolve, back) {
      this.data = (data.SendParams && data.SendParams.Lines) || {};
      this.showback = data.SendParams && data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].SendToLineID ? true : false;
      this.show = true;
      if (resolve) {
        this.resolve = resolve;
      }
      if (back) {
        this.back = back;
      }
    },
    confirm() {
      this.resolve(this.$refs.picker.getValues()[0], true);
      this.show = false;
    },
    goBack() {
      this.back();
      this.show = false;
    },
    cancel() {
      this.show = false;
    },
    // beforeClose(action, done) {
    //   if (action !== "cancel") {
    //     done(false);
    //     this.resolve(this.$refs.picker.getValues()[0]);
    //   } else {
    //     done();
    //   }
    // },
  },
};
</script>

