<template>
  <div class="ds-control ds-form-item ds-textbox mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <van-field
        v-model="inputValue"
        :placeholder="!readOnly?placeholder:''"
        :type="inputType"
        :disabled="disabled"
        :label-width="getLabelWidth(true)"
        :label="(showLabel&&label?label:'')"
        :readonly="readOnly"
        :required="!readOnly?required:false"
        :input-align="!readOnly? inputAlign: 'right'"
        :error-message="errorsMsg"
        :error-message-align="!readOnly? inputAlign: 'right'"
        @blur="blurHandler"
        @change="changeHandler">
      </van-field>

    </template>
  </div>
</template>
<script>
import formControl from "_platform/mixins/formControl";
export default dsf.component({
  name: "DsfMobileTextbox",
  mixins: [formControl],
  ctrlCaption: "文本框",
  props: {
    ishideCon: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: "请输入"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("text-meta-data");
      }
    },
    inputAlign: {
      type: String,
      default: "right"
    }
  },
  computed: {
    inputType() {
      return this.ishideCon ? "password" : "text";
    },
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      }
    }
  },
  mounted() {
    // console.log(this.props)
  },
  methods: {
    changeHandler() {
      this.$dispatch("change", this.value);
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      });
    },
    blurHandler() {
      this.$dispatch("blur");
    }
  }
});
</script>