<template>
  <div class="ds-control ds-form-item ds-ueditor mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="inputValue" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <p class="van-cell">
        <label v-if="showLabel"> {{ label }}</label>
      </p>
      <quill-editor
        v-if="showEditor && !readOnly"
        v-model="inputValue"
        ref="myQuillEditor"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @ready="onEditorReady($event)"
        :style="{
          height: richTxtHeight + 'px',
          display: 'flex',
          'flex-direction': 'column',
        }"
      >
      </quill-editor>
      <div v-if="isDesign && readOnly" id="designReadOnly" :class="ueditorCss" v-text="msg"></div>
    </template>
  </div>
</template>
<script>
import { quillEditor } from "vue-quill-editor";

import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfMobileUeditor",
  mixins: [formControl],
  components: {
    quillEditor,
  },
  ctrlCaption: "富文本",
  props: {
    richTxtHeight: {
      type: Number,
      default: 400,
    },
    placeholder: {
      type: String,
      default: "",
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("big-text-meta-data");
      },
    },
    toolBar: {
      type: Array,
      default() {
        return [
          "bold",
          "italic",
          "underline",
          "strike", // toggled buttons
          "blockquote",
          "code-block",
          { header: 1 },
          { header: 2 }, // custom button values
          { list: "ordered" },
          { list: "bullet" },
          { script: "sub" },
          { script: "super" }, // superscript/subscript
          { indent: "-1" },
          { indent: "+1" }, // outdent/indent
          { direction: "rtl" }, // text direction
          { size: ["small", false, "large", "huge"] }, // custom dropdown
          { header: [1, 2, 3, 4, 5, 6, false] },
          { color: [] },
          { background: [] }, // dropdown with defaults from theme
          { font: [] },
          { align: [] },
          "clean",
          "link",
          "image",
          "video",
        ];
      },
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    ueditorCss() {
      return this.showLabel ? "have-label" : "";
    },
    msg() {
      const txt = "已经启用只读模式，";
      const msg = this.visible ? txt + "可见" : txt + "且不可见";
      return msg;
    },
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      },
    },
  },
  watch: {
    //visible 只会在设计器模式下才会发生改变
    visible(val) {
      // val ? this.ue.setEnabled() : this.ue.setDisabled();
    },
    //readOnly 只会在设计器模式下才会发生改变
    readOnly(val) {
      // val ? this.ue.setHide() : this.ue.setShow();
    },
    toolBar() {
      this.renderUEditor();
    },
    placeholder(val) {
      this.showEditor = false;
      this.editorOption.placeholder = val;
      this.$nextTick(() => {
        this.showEditor = true;
      });
    },
  },
  data() {
    return {
      ue: null, //ueditor 实例化之后的对象
      ueId: null, //ueditor 的div Id
      uparseId: null, //uParse 容器的 Id
      editorOption: {
        modules: {
          toolbar: this.toolBar,
        },
        placeholder: this.placeholder,
        readOnly: true,
        theme: "snow",
      },
      showEditor: true,
    };
  },
  mounted() {
    const self = this;
    //当它是浏览页面且是只读状态
    if (!self.isDesign && self.readOnly) {
      return;
    }
  },
  beforeDestroy() {},
  methods: {
    onEditorBlur() {
      // this.emitValueChange(this.value);
    },
    onEditorFocus() {
      // console.log("focus", this.value);
    },

    onEditorReady() {
      // console.log("ready", this.value);
    },
    renderUEditor() {
      this.showEditor = false;
      this.editorOption.modules.toolbar = this.toolBar;
      this.$nextTick(() => {
        this.showEditor = true;
      });
    },
  },
});
</script>
